﻿.#{$gridWrapper} {

    .millercolumns--container {
        margin-bottom: 5px;
        max-width: 100%;
        overflow-y: hidden;
        overflow-x: auto;

        .dragable-arrow {
            display: none !important;
        }

        > div {
            display: flex;
        }

        &.external {
            background-color: #fff;
            z-index: 9999999;
            box-shadow: 0 11px 23px rgba(0, 0, 0, 0.3), 0 6px 6px rgba(0, 0, 0, 0.22), 0 -4px 6px rgba(0, 0, 0, 0.22);
        }
    }

    .miller-item {
        margin-left: 21px;
        padding-left: 5px;
        padding-right: 5px;
        display: block;
        flex-grow: 1;
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 24px;
    }

    .millercolumns--column {
        flex: 1;
        max-width: 185px;
        flex-basis: 160px;
        flex-shrink: 0;
        height: 190px;
        min-width: 190px;
        //margin-bottom: 10px;
        overflow: hidden;
        position: relative;
        background: $white;

        &.ishidden {
            flex-basis: 0;
            width: 0;
            flex-shrink: 1;
            max-width: 0;
            min-width: 0;
        }

        .millercolumns--scroller {
            height: calc(100% - 30px);
            overflow-x: hidden;
            overflow-y: scroll;
            margin-right: -17px;
            display: flex;
            flex-direction: column;

            &:hover {
                margin-right: 0px;
                overflow-y: auto;
            }

            .actions {
                @include actions();
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 2;
                padding-top: 3px;
                align-items: center;
                width: 100%;
                padding-bottom: 3px;

                .form-control {
                    margin: 0;
                    min-height: 20px;
                }
            }
        }

        &:not(:first-of-type) {
            box-shadow: inset 5px 0 17px -6px rgba($black, 0.11);
        }

        .column-header {
            position: relative;
            padding-left: 8px;
            margin-top: 8px;

            h6 {
                font-size: 14px;
                min-height: 16px;
                font-weight: bold;
                padding: 0 0 0 25px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                margin: 0 0 10px;
                display: flex;
                justify-content: space-between;

                .button-wrapper .button {
                    margin-right: 8px;
                }
            }

            .ts-toggle {
                position: absolute;
                left: 9px;
                top: -2px;
                font-size: 18px;

                input[type=checkbox] + label {
                    margin:0;

                    &:before {
                        height: 16px;
                        width: 16px;
                    }

                    &:after {
                        width: 16px;
                        height: 16px;
                    }
                }

                input[type=checkbox]:checked + label {
                    &:before {
                        height: 18px;
                        width: 11px;
                    }
                }
            }
        }

        ul {
            margin: 0;
            padding: 0;

            li {
                position: relative;
                border-radius: 13px 0 0 13px;
                padding: 0 2px;
                margin-bottom: 1px;
                transition: .2s;
                margin-left: 5px;
                overflow: hidden;
                display: flex;
                max-width: 190px;

                &:hover {
                    background-image: linear-gradient(to right, rgba(153,153,153,.16) 0%,rgba(229,229,229,0) 30%);
                }

                &.highlighted {
                    background-color: $lightest;

                    i {
                        border-color: transparent;
                        opacity: 1;
                    }
                }
            }
        }

        &:last-child {

            ul {

                li {

                    &:hover {
                        background: none;
                        cursor: default;

                        i {
                            cursor: pointer;
                        }
                    }
                }
            }
        }

        &:before {
            content: '';
            z-index: 3;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 16px;
            pointer-events: none;
        }

        &:after {
            content: '';
            z-index: 3;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            height: 16px;
            pointer-events: none;
        }
    }

    .millercolumnscheckbox {
        cursor: pointer;
        transform: translateY(3px);

        .circle-filling {
            transition: .7s;
            stroke: $active;
            fill: none;
        }

        .circle-circumference {
            stroke: $radio_border;
            fill: none;
        }
    }
}
