﻿@keyframes flipInX {
    0% {
        -webkit-transform: perspective(400px) rotate3d(1,0,0,-90deg);
        transform: perspective(400px) rotate3d(1,0,0,-90deg);
        -webkit-transition-timing-function: ease-in;
        transition-timing-function: ease-in;
    }

    40% {
        -webkit-transform: perspective(400px) rotate3d(1,0,0,20deg);
        transform: perspective(400px) rotate3d(1,0,0,20deg);
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }

    60% {
        -webkit-transform: perspective(400px) rotate3d(1,0,0,-10deg);
        transform: perspective(400px) rotate3d(1,0,0,-10deg);
        -webkit-transition-timing-function: ease-in;
        transition-timing-function: ease-in;
        opacity: 1;
    }

    80% {
        -webkit-transform: perspective(400px) rotate3d(1,0,0,5deg);
        transform: perspective(400px) rotate3d(1,0,0,5deg);
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }

    100% {
        -webkit-transform: perspective(400px);
        transform: perspective(400px);
    }
}

@keyframes countdownHeight {
  0% {
    height:0;
  }

  100% {
    height:100%;
  }
}

@keyframes newitem {
    from { background-color: rgba($newitem_animation, 0.40); }
	to { background-color: transparent; }
}

@keyframes rot {
	from {transform: rotate(0deg);}
	to {transform: rotate(359deg);}
}

@keyframes blink {
	0% {opacity:.5;}
	50% {opacity:1;}
	100% {opacity:.5;}
}

@keyframes blinkBg {
	0% {background:rgba($secondary, 0.0);}
	50% {background:rgba($secondary, 0.30);}
	100% {background:rgba($secondary, 0.0);}
}

@keyframes blinkErrorShadow {
    0% {box-shadow: 0 0 10px 1px rgba($primary, 0.10);}
	20% {box-shadow: 0 0 16px 4px rgba($error, 1);}
	100% {box-shadow: 0 0 10px 1px rgba($error, 0.10);}
}

@keyframes blinkWarningShadow {
    0% {box-shadow: 0 0 10px 1px rgba($primary, 0.10);}
	20% {box-shadow: 0 0 16px 4px rgba($warning, 1);}
	100% {box-shadow: 0 0 10px 1px rgba($warning, 0.10);}
}