﻿.#{$gridWrapper} {
    
    .dragable-arrow {

        width: 40px;
        height: 15px;
        position: absolute;
        background: none;
        border: none;
        z-index: 1;
        outline: none;

        &:hover i {
            color: $primary;
        }

        i {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            margin: 0 auto;
            transition: all 0.3s;
            color: $tertiary;
        }

        &.dragable-arrow--top,
        &.dragable-arrow--bottom {
            left: 0;
            right: 0;
            margin: 0 auto;
        }

        &.dragable-arrow--top {
            top: 0;
        }

        &.dragable-arrow--bottom {
            bottom: 0;
        }

        &.dragable-arrow--left, 
        &.dragable-arrow--right {
            top: calc(50% - 5px);
            height: 40px;
            width: 20px;
            i {
                top: 50%;
                transform: translateY(-50%);
            }
        }

        &.dragable-arrow--left {
            left: 0;
        }

        &.dragable-arrow--right {
            right: 0;
        }
    }

    .dragable {
        cursor: url("../images/hand-paper.cur"), auto;
        
        &:active, 
        &.is-dragging {
            cursor: url("../images/hand-rock.cur"), auto;
        }
    }

    .drag-scroll-wrapper {
        position: relative;
        overflow: hidden;
    }
}
