﻿.#{$gridWrapper} {

    .ts-grid--fields-management {
        width: $metrics-width;

        .ts-grid--advanced-metrics {
            flex: 1;

            .buttons-after-hr button {
                bottom: -1px;
            }
        }

        .tabs {

            .tab {

                &:last-child {
                    box-shadow: inset -10px 0 6px -10px rgba(0, 0, 0, 0.1);
                }

                &.is-tab-selected {
                    background: $lightest;

                    &:last-child {
                        box-shadow: inset -10px 0 6px -5px rgba(0, 0, 0, 0.2);
                    }
                }
            }
        }
    }
}