﻿.#{$gridWrapper} {

    section {

        $panel-width: 370px;

        &.ts-grid--layout-validation-messages {
            width: $panel-width;

            .stop-on {

                .ts-grid--dropdown {
                    
                    input:first-child {
                        cursor:pointer;
                    }
                }
            }
        }

        &.ts-grid--layout-validation-messages-details {
            background: darken($lightest,4%);
            right: $panel-width + 40px;
            width: $panel-width + 50px;
            overflow-x: hidden;
            overflow-y: auto;
            -webkit-transform: translate3d(40px, 0, 0);
            transform: translate3d(40px, 0, 0);

            &.active {
                -webkit-transform: translate3d(0,0,0);
                transform: translate3d(0,0,0);

                + .ts-grid--layout-validation-messages {
                    width: $panel-width - 10;
                }
            }
        }
    }
}
