﻿@mixin statusbarButton() {
    width: $statusbarHeight;
    height: $statusbarHeight;
    vertical-align: top;

    i {
        vertical-align: baseline;
        line-height: $statusbarHeight;
    }
}

@keyframes marquee {
    from {
      transform: translate3d(0%, 0, 0);
    }
    to {
      transform: translate3d(-50%, 0, 0);
    }
  }

.#{$gridWrapper} {

    .ts-grid--statusbar {
        z-index: $horizontalToolbarZIndex;
        position: absolute;
        bottom: 0;
        height: $statusbarHeight;
        left: 0;
        right: 0;
        background: $statusbarBackground;
        box-shadow: inset 0 1px 2px darken($header_bg, 70%);

        >button {
            @include statusbarButton();
            border-right: 1px solid $statusbarBorderColor;
        }

        .status-wrapper {
            padding: 0 0 0 2px;
            white-space: nowrap;
            user-select: none;
            -ms-user-select: none;
            overflow-x: auto;
            line-height: $statusbarHeight;
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            @include noselect();

            &.dragable--arrows-always-visible {
                padding-left: $statusbarHeight;
                padding-right: $statusbarHeight - 1px;
            }

            .stretcher {
                display: none;
            }

            &.is-dragging {
                .stretcher {
                    @extend .stretcher-base;
                }
            }

            > div {
                border-right: 1px solid $statusbarBorderColor;
                height: 100%;
                display: inline-block;
                position: relative;
                color: $statusbarTextColor;

                >button {
                    @include statusbarButton();
                }

                i {
                    width: $statusbarHeight;
                    height: $statusbarHeight;
                    line-height: $statusbarHeight;
                    display: inline-block;
                    text-align: center;
                    vertical-align: top;

                    &.rotate-90 {
                        transform: translate(-50%, -50%) rotate(90deg);
                    }
                }

                .status-value {
                    font-size: 14px;
                    display: inline-block;
                    height: $statusbarHeight;
                    width: $statusbarHeight;
                    transition: all .2s ease .2s;
                    font-weight: normal;
                    overflow:hidden;
                    position: relative;
                    //text-overflow:ellipsis;
                    //white-space:nowrap;
                    //max-width:100%;

                    >span {
                        display: inline-block;
                        transform: translate3d(0,0,0);
                        will-change: transform;
                    }

                    &.status-value--marquee {
                        &:after {
                            @include gradient(left, rgba(0, 0, 0, 0), $statusbarBackground);
                            content: '';
                            position: absolute;
                            height: $statusbarHeight;
                            width: $statusbarHeight;
                            right: 0;
                            top: 0;
                        }
                    }
                }

                &:hover .status-value--marquee span {
                    animation: marquee linear infinite;
                }
            }
        }

        .dragable-arrow.dragable-arrow--right,
        .dragable-arrow.dragable-arrow--left {
            top: 2px;
            height: $statusbarHeight;
            width: $statusbarHeight;
            background-color: $statusbarBackground;
            border: 0 solid $statusbarBorderColor;

            i {
                color: $statusbarTextColor;
            }
        }

        .dragable-arrow.dragable-arrow--right {
            border-left-width: 1px;
        }

        .dragable-arrow.dragable-arrow--left {
            border-right-width: 1px;
            left: 0;
        }

    }
}
