﻿.#{$gridWrapper} {
    select.customized {
        width: 100%;
        padding: 10px;
        border: none;
        border-top: 1px solid $select_border;
        padding-right: 40px;
        background: url(../images/select.png) calc(100% - 11px) center no-repeat;
        cursor: pointer;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        transition: .1s;

        &:hover {
            background-color:$select_bg_hover;
        }

        &:active, &:focus {
            outline:none;
        }
    }
}
