﻿/// <reference path="../../grid-wrapper.scss" />

.#{$gridWrapper} {
    
    .in-grid-modal {

        &.is-dragging {
            opacity: 0.8;
            z-index: 10000;
        }
    }

    .in-grid-modal__title {
        display: flex;
        justify-content: space-between;
        padding: 4px 8px;
        margin: 0;
        cursor: move;
        text-overflow: ellipsis;
        overflow: hidden;
        width: 100%;
        height: $modal-title-height;
        font-size: 16px;
        border-bottom: 1px solid $lightest;
        background: $primary;
        color: $white;


        h4 {
            font-size: 14px;
            margin: 0;
            padding-top: 2px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            width: 100%;

            span {
                margin-left: 6px;
                color: $white;
            }
        }
    }
}
