﻿.#{$gridWrapper} {

    $mapper-middle-column: 200px;

    .column-mapper__cover {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.8);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 3;
    }

    .column-mapper__cover__content {
        width: 200px;
        padding: 10px;
        background-color: whitesmoke;
        box-shadow: 4px 4px 5px 0px rgba(142, 142, 142, 0.75);
        position: absolute;
        left: 0px;
        top: 0px;
        right: 0px;
        margin: 0 auto;
        top: calc(50% - 75px);
        z-index: 4;
    }

    .column-mapper__cover__content__element {
        
        margin-bottom: 10px;

        span {
            font-weight: bold;
        }
    }

    #SVGID_3_ {

        .stop1 {
            stop-color:#FBF9F6
        }

        .stop2 {
            stop-color:#E0E0DA
        }
    }

    .column-mapper {
        display:flex;

        .hidescrollbar {
            height:100%;
            overflow:hidden;
            flex:1;
            position:relative;

            &:before, &:after {
                content:'';
                position:absolute;
                top:0;
                left:0;
                right:0;
                height:80px;
                @include gradient(top, $lightest, rgba($lightest, 0.0));
                transition:.3s;
                opacity:0;
                transform: translate(0, -80px);
                pointer-events: none;
            }

            &:after {
                top:auto;
                bottom:0;
                @include gradient(bottom, $lightest, rgba($lightest, 0.0));
                transform: translate(0, 80px);
            }

            &.showbefore {
                &:before {
                    opacity:1;
                    transform: translate(0, 0);
                }
            }

            &.showafter {
                &:after {
                    opacity:1;
                    transform: translate(0, 0);
                }
            }
        }

        ul {
            list-style:none;
            margin:15px 0 0;
            padding:0;
            user-select:none;
            height:90%;
            overflow-x:hidden;
            overflow-y:scroll;

            li {
                padding: 5px 0px 5px 0px;                                    

                span {
                    display:block;
                    line-height:28px;
                    white-space:nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                }

                &.disabled {

                    span {
                        background: transparent;
                        border-bottom: 0;
                        opacity: .6;
                    }
                }
            }
        }

        .lines {
            flex:0 1 $mapper-middle-column;
            position:relative;

            svg {

                .lines-drops {

                    g {

                        rect {
                            fill: $lightest;
                            transform: translateX(-18px);
                            transition: all .2s ease;

                            & + rect {
                                transform: translateX(18px);
                            }
                        }

                        &.disabled {

                            rect, rect+rect {
                                transform: translateX(0);
                            }
                        }
                    }
                }

                .line-connectors {

                    .line-connectors-holder {
                        cursor:pointer;
                    }
                }
            }

            .line {
                pointer-events:none;
            }

            .lineGroup .handle {
                cursor: pointer;
            }
        }
    }

    // SVG specific styles
    .lines-drops-bg {
        fill-rule: evenodd;
        clip-rule: evenodd;
        fill: $lightest;
        transition: .2s;
    }

    .lines-drops-bg.bgactive {
        fill: salmon;
    }

    .lines-drops-shadow {
        clip-path: url(#SVGID_2_);
        fill-rule: evenodd;
        clip-rule: evenodd;
        fill: #E6E7E8;
        filter: url(#drop_shadow_1);
    }

    .line-connectors-st0 {
        fill-rule: evenodd;
        clip-rule: evenodd;
        fill: rgba(20, 20, 21, 0.34);
    }

    .line-connectors-st1 {
        fill-rule: evenodd;
        clip-rule: evenodd;
        fill: url(#SVGID_3_);
    }

    .line-connectors-st2 {
        fill-rule: evenodd;
        clip-rule: evenodd;
        fill: #E7E6E4;
    }

    .line-connectors-st3 {
        fill: none;
        stroke: #DEDFE0;
        stroke-miterlimit: 10;
    }

    .line-connectors-st4 {
        fill: none;
        stroke: $white;
        stroke-miterlimit: 10;
    }

    .column-mapper__headers {
        display: flex;
        justify-content: space-around;

        label {
            margin: 0px;

            &:first-child {
                margin-right: $mapper-middle-column;
            }
        }
    }
}
