﻿.#{$gridWrapper} {

	.import-mapping-element__wrapper {
		padding: 0px 5px 0px 5px;
		border-bottom:1px solid rgba($lighter, 0.60);
		background:rgba($lighter, 0.10);
		color: $columnmapper_color;
		height: 30px;
		border-radius:3px;
	}

	.button--import-element {
        @include type-abbreviation();
		top: 3px;
		margin-left: 3px;
		cursor: pointer;
	}
	
	.button--import-element--active {
		background-color: $btngroup_bg_active;
        border-color: $btngroup_bg_active;
        color:$white;
	}

	.button--wrapper--import {
		display: flex;
	}

	.import-mapping-element__main-data {
		display: flex;
		position: relative;
		top: 3px;
	}

	.import-mapping-element__additional-data:not(.import-mapping-element__additional-data--active) {
		display: none;
	}

	.import-mapping-element__additional-data {
		position: absolute;
		background: #ccc;
		padding: 5px;
		z-index: 1;
		width: 100%;
		left: 0px;

		.form-element-wrapper select {
			padding: 0px 10px;
    		margin-top: 5px;
		}
	}

	.import-mapping-element__main-data--name {
		flex-grow: 1;
		padding: 0px 3px 0px 3px;
		cursor: pointer;

		&.disabled {
			color: $disabledColor;
		}
	}

	.import-mapping-element__main-data--label {
		padding-left: 28px !important;
		margin-bottom: 0px !important;
		top: 3px;
	}

	.import-modal-content {

		.step-content > div {
			padding-top: 1px;
			margin-top: -1px;
		}

		.step-content-info {
			background: $proceduresBg;
			z-index: 10;
			box-shadow: 0px 6px 10px -8px $light;
			padding: 0 20px;
            margin-top: -7px;
            display: flex;
            justify-content: space-between;
            align-items: center;

			> div {
                opacity:.8;
                margin-right: 10px;
                overflow: hidden;
                text-overflow: ellipsis;
			}
        }

        .step-content-info__title {
            font-weight: bold;
        }

        .step-content-info__add-new-members {
            display: block;
        }

        .step-content-info__rows-summary {
            display: flex;

            div {
                margin-right: 10px;
            }
        }

		.step-content-titles {
			display:flex;
			padding: 16px 0 8px 0;
			margin-top: 30px;

			> div {
				flex:1;
				font-weight:bold;
				padding-left:5px;

				&:first-child {
					flex-basis:200px;
					flex-grow:0;
				}

				&:nth-child(2) {
					flex-basis: 140px;
					flex-grow:0;
				}

				&:last-child {
					text-align:right;
				}
			}
		}

		.step-content-line {
			margin-top:2px;
			display:flex;
			border-radius:3px;
			border:1px solid $lightest;
			position:relative;

			&:not(.is-active) {
				background:rgba($lightest, 0.40);
				box-shadow:inset 0 3px 5px -3px rgba($light, 0.20);

				.step-content-field:not(:first-child) {
					opacity:0;
					pointer-events:none;
				}
			}
		}

		.step-content-field {
			flex:1;
			padding:0 2px;
			position:relative;
			transition:.2s;
			margin-left: 15px;

			&:first-child {
				flex-basis:200px;
				flex-grow:0;
			}

			&:nth-child(2) {
				flex-basis: 140px;
				flex-grow:0;
			}

			&:last-child {
				text-align:right;
				flex-basis:70px;
				flex-grow:0;
				padding-right:0;

				label {
					padding-left:25px;
				}
			}

			label {
				margin-bottom:0;
			}
		}

		.step-content {
			width:100%;
			padding: 6px 16px;
			white-space: normal;
		}

		.drop-area {
			text-align:center;
			font-size: 16px;
			color: darken($lighter, 20%);
			height: 300px;
			box-shadow: inset 0 4px 12px -3px $darkest;
			background: lighten($lighter, 10%);

			i {
				font-size:60px;
			}
		}

		.step-content__upload {
			position: absolute;
			margin: 0 auto;
			left: 0;
			right: 0;
			top: calc(50% - 28px);
		}

		.column-mapper {
			overflow: hidden;
			height: 320px;
			width:100%;
			padding:0 2px;
		}

		.loading-screen {
			h3 {
				text-align:center;
				padding-bottom:10px;
			}
		}
	}

	.btn-tertiary {
		color: $primary;
		padding: 0;
		line-height: 40px;
		width: 300px;
		margin: auto;
		display: block;
		border: 2px solid $primary;

		&:hover,
		&:focus {
		color: lighten($primary, 20%);
		border-color: lighten($primary, 20%);
		}
	}

	/* input file style */
	.input-file {
		width: 0.1px;
		height: 0.1px;
		opacity: 0;
		overflow: hidden;
		position: absolute;
		z-index: -1;

		+ .js-labelFile {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		padding: 0 10px;
		cursor: pointer;
		width: 100%;
		border-radius: 0;

		.icon:before {
				content: "\f093";
			}
		}
	}
}
