﻿.#{$gridWrapper} {

    .floating-header--wrapper {
        position: absolute;
        
        &.x {
            z-index: $floatingHeadersZIndex;

            .floating-header--x {
                
                &:before {
                    content: "";
                    position: absolute;
                    width: $cellHeight - 2;
                    height: $cellHeight;
                    background: $white;
                    top: 0;
                    left: -$cellHeight + 2;
                    border-right: $defaultCellBorder;
                    border-left: $defaultCellBorder;
                    border-bottom: $defaultCellBorder;
                }
            }
        }

        &.y {
            z-index: 2;
            max-width: 50%;
        }
    }

    .floating-header--wrapper--hidden {
        display: none !important;
    }
        
    .floating-header {

        .width-anchor {
            position: absolute;
            top: 0;
            right: -5px;
            width: 9px;
            height: 100%;
            cursor: e-resize;
            z-index: 1;
        }

        [data-tooltip] {
            
            &:after {
                bottom: auto; // IE fix
                bottom: unset;
                top: 100%;
                transform: translate(-50%, 10px);
                left: 50%;
            }

            &:hover {
                &:after {
                    transform: translate(-50%, 7px);
                }
            }
        }

        &.floating-header--x:not(.virtual-floating-header) .floating-header__content {
            display: block;
        }
    }

    .virtual-floating-header {

        .floating-header__content {
            padding-left: 5px;
            padding-right: 5px;
            min-width: 20px;
        }

        .floating-header__cell.y {
            padding: 0;
        }
    }

    .floating-header__content {
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .floating-header--x {
        display: flex;      
    }

    .x .floating-header__full-path {
        flex-direction: column;
        width: 100%;
    }

    .floating-header__full-path {
        display: none;
        position: absolute;
        padding: 0 10px;
        top: 0;
        background: $white;
        z-index: 10;

        .x, .y {
            overflow: hidden;
            box-shadow: none;
            position: relative;
        }

        .x {
            text-align: center;
            padding-bottom: 10px;

            &:not(:first-child) {
                padding-top: 5px;
            }

            &:not(:last-child):after {
                @include icon();
                position: absolute;
                top: auto;
                bottom: -12px;
                left: 50%;
                transform: translate(-50%, -70%);
                content: '\f0d7';
                color: $light;
            }
        }

        .y {

            &:first-child {
                padding-right: 15px;
            }

            &:not(:first-child) {
                padding-left: 10px;
                padding-right: 10px;
            }

            &:not(:last-child):after {
                @include icon();
                position: absolute;
                top: 50%;
                left: 100%;
                content: '\f0da';
                transform: translate(-100%, -50%);
            }
        }
    }

    .floating-header--show-full-path .floating-header__cell:hover .floating-header__full-path {
        display: flex;
    }

    .fit-mode .ts-grid-decorated {

        &.floating-header__header .floating-header__content {
            padding-left: 5px;
            padding-right: 5px;
            min-width: 20px;
        }
        
        .floating-header__cell.y:first-child:before {
            height: $fitModeCellHeight;
            line-height: $fitModeCellHeight;
        }

        &.virtual-floating-header.floating-header--x:before {
            height: $fitModeCellHeight;
        }

        .floating-header__row-summary .floating-header__cell.y,
        .row-indicator .row-indicator__cell {
            height: $fitModeCellHeight;
            line-height: $fitModeCellHeight;
            padding: 0;
        }
        
        .floating-header__full-path {
            
            .x {
                height: $fitModeCellHeight;
                line-height: $fitModeCellHeight;
            }
            .y {
                &:first-child {
                    padding-right: 15px;
                }
    
                &:not(:first-child) {
                    padding-left: 10px;
                    padding-right: 10px;
                }
            }
        }
    }

    .floating-header__header {
        position: absolute;
        z-index: 5;
        background: $white;
        top: -33px;
        width: 100%;

        &.empty .y {
            padding: 0; 
        }
        
        .floating-header__cell {
            padding-left: 10px;

            &:before {
                content: "";
                position: absolute;
                width: 29px;
                height: $cellHeight;
                background: $white;
                top: 0;
                left: -29px;
                border-right: $defaultCellBorder;
                border-left: $defaultCellBorder;
            }
        }
    }

    .floating-header__row-summary .floating-header__cell.y {
        overflow: hidden;
        font-weight: normal;
    }

    &:not(.shadows) {

        .in-grid--section--grid-modal:not(.fit-mode) {

            .floating-header {

                &.virtual-floating-header .floating-header__item-wrapper:first-child .floating-header__cell.y {
                    height: $cellHeight;
                    line-height: $cellHeight;
                }
    
                &:not(.virtual-floating-header) .floating-header__item-wrapper:first-child .floating-header__cell.y {
                    height: $cellHeight;
                    line-height: $cellHeight;
                }
            }
        }

        .in-grid--section--grid-modal.fit-mode {
            
            .floating-header {

                &.virtual-floating-header .floating-header__item-wrapper:first-child .floating-header__cell.y {
                    height: $fitModeCellHeight + 1;
                    line-height: $fitModeCellHeight + 1;
                }
    
                &:not(.virtual-floating-header) .floating-header__item-wrapper:first-child .floating-header__cell.y {
                    height: $fitModeCellHeight;
                    line-height: $fitModeCellHeight;
                }
            }

            .floating-header--wrapper:not(.row-summary) .virtual-floating-header .floating-header__cell.y,
            .ts-grid-decorated .row-indicator__cell {
                height: $fitModeCellHeight + 1;
                line-height: $fitModeCellHeight + 1;
            }
        }

        .x:not(.floating-header__cell), 
        .y:not(.floating-header__cell):not(.row-indicator__cell), 
        .n:not(.row-indicator__cell) {
            border-bottom: $defaultCellBorder;
            border-right: $defaultCellBorder;
        }

        .floating-header--wrapper {

            .floating-header__full-path .x,
            .floating-header__full-path .y {
                border: none;
            }

            .floating-header:not(.virtual-floating-header) {
                
                .floating-header__cell {

                    &.y {
                        display: table-cell;
                        width: 130px;
    
                        .floating-header__content {
                            width: 120px;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            display: inline-block;
                        }
                    }
                }
            }
            
            &:not(.row-summary) {
                .floating-header__cell.y {
                    border-bottom: $defaultCellBorder;
                }
            }

            .floating-header__cell {

                &.y {
                    border-right: $defaultCellBorder;
                }

                &.x {
                    border-top: none;
                    border-right: $defaultCellBorder;
                    border-bottom: $defaultCellBorder;

                    &.g {
                        padding-bottom: 5px;
                        border-bottom: 3px solid darken($mg__c__base, 20%);
                    }
                }
            }
            
            &:last-child .floating-header__cell.x {
                border-right: $defaultCellBorder;
            }

            .floating-header__full-path {
                border-bottom: solid 1px $white;
            }
        }

        .row-indicator__cell.y {
            border-bottom: $defaultCellBorder;
            border-right: $defaultCellBorder;
            width: 28px;
        }

        .virtual-floating-header {
            
            .floating-header__cell.x {
                border-right: $defaultCellBorder;
                border-left: none;

                &:not(.g) {
                    border-bottom: none;
                }
            }

            .floating-header__cell.y {
                border-right: none;
            }
        }
    }
    
    &.shadows {

        .floating-header--wrapper {

            &.x {
                
                &.g {
                    padding-bottom: 5px;
                    border-bottom: 3px solid darken($mg__c__base, 20%);
                }
        
                .floating-header--x {
        
                    &:before { 
                        box-shadow: inset 1px 1px 0 $rowBorder, 0px 1px 0 $rowBorder;
                        border: none;
                    }
        
                    &:not(.docked):before {
                        @include boxshadow-md();
                    }
                }
            }

            .floating-header:not(.virtual-floating-header) .floating-header__cell {

                &.y {
                    display: table-cell;
                    width: 130px;

                    .floating-header__content {
                        width: 120px;
                    }
                }
            }

            .floating-header {
        
                &:not(.docked) {
                    @include boxshadow-md();
                    
                    .floating-header__cell.x:first-child:before {
                        @include boxshadow-md();
                    }
                }
            }
        }

        .fit-mode .floating-header--y:not(.virtual-floating-header) .floating-header__cell.y {
            height: $fitModeCellHeight;
            line-height: $fitModeCellHeight;
        }

        .floating-header:not(.virtual-floating-header) .floating-header--y .floating-header__cell.y {
            height: $cellHeight;
            line-height: $cellHeight;
            width: 200px;
            display: table-cell;
        }
        
        .virtual-floating-header {
        
            &.floating-header--y {
                box-shadow: 5px 0px 10px rgba(0, 0, 0, 0.19), 5px 0px 3px rgba(0, 0, 0, 0.23);
            }
        
            .floating-header--x {
                display: flex;      
                
                &.docked {
                    box-shadow: 0px 1px 0 $rowBorder;
                }
            }
        }
        
        .floating-header__header {
        
            &.empty .y {
        
                &:not(.on-top):before {
                    box-shadow: inset 1px 1px 0 $rowBorder, inset -1px 0 0 $rowBorder, 5px 0px 3px rgba(0, 0, 0, 0.23);
                }
            }
            
            .floating-header__cell {
                box-shadow: inset 1px 1px 0 $rowBorder, inset -1px 0 0 $rowBorder, 5px 0px 3px rgba(0, 0, 0, 0.23);
            }
        }
        
        .floating-header__row-summary .floating-header__cell.y {
            box-shadow: inset 1px 1px 0 $rowBorder, inset -1px 0px 0 $rowBorder;
            // height: $cellHeight - 2;
            // line-height: $cellHeight - 2;
        }

        .fit-mode .floating-header__row-summary .floating-header__cell.y {
            // height: $fitModeCellHeight - 2;
            // line-height: $fitModeCellHeight - 2;
        }

        .floating-header__header .floating-header__cell:before {
            border: none;
        }
    }   
}
