﻿.ts-grid-standalone {
    background-color: $gridBg;
    overflow: hidden;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: relative;
    @extend .font-custom;
    height: 100%;

    > div {
        height: 100%;

        > div {
            height: 100%;
            background-repeat: no-repeat;
            background-attachment: fixed;
            background-position: right bottom;
            background-size: cover;
        }
    }
}

.invisible {
    visibility: hidden;
}

.#{$gridWrapper} {
    font-size: 14px;
    height: 100%;

    .in-grid__container {
        height: 100%;
    }

    .component-wrapper {
        height: 100%;
    }

    .mt5 {
        margin-top: 5px;
    }

    .mt10 {
        margin-top: 10px;
    }

    .form-element-wrapper,
    div {

        [type="checkbox"] {

            &:not(:checked), &:checked {
                position: absolute;
                left: -9999px;
                opacity: 0;
            }

            + label {
                position: relative;
                padding-left: 35px;
                cursor: pointer;
                display: inline-block;
                height: 25px;
                line-height: 25px;
                font-size: 13px;
                font-weight: normal;
                -webkit-user-select: none;
                -moz-user-select: none;
                -khtml-user-select: none;
                -ms-user-select: none;

                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 18px;
                    height: 18px;
                    z-index: 0;
                    border: 2px solid $checkbox_border;
                    border-radius: 1px;
                    margin-top: 2px;
                    -webkit-transition: all .3s cubic-bezier(0.5, -0.76, 0.3, 1.95);
                    transition: all .3s cubic-bezier(0.5, -0.76, 0.3, 1.95);
                }

                &:after {
                    content: '';
                    position: absolute;
                    top: 4px;
                    left: 2px;
                    height: 15px;
                    width: 15px;
                    background: $checkbox_bg;
                    z-index: 1;
                    -webkit-transition: 0.2s;
                    transition: 0.2s;
                    -webkit-transform: scale(.1);
                    transform: scale(.1);
                    opacity: 0;
                }

                &:focus {
                    outline: none;
                }
            }

            &:not(:checked):disabled {

                + label {
                    opacity: .4;
                    cursor: not-allowed;
                }

                + label:before {
                    border: none;
                    background-color: $checkbox_bg_disabled;
                }
            }

            &:checked {

                + label:before {
                    z-index: 2;
                    top: -4px;
                    left: -3px;
                    width: 12px;
                    height: 22px;
                    border-top: 2px solid transparent;
                    border-left: 2px solid transparent;
                    border-right: 2px solid $checkbox_active;
                    border-bottom: 2px solid $checkbox_active;
                    -webkit-transform: rotate(40deg);
                    -moz-transform: rotate(40deg);
                    -ms-transform: rotate(40deg);
                    -o-transform: rotate(40deg);
                    transform: rotate(40deg);
                    -webkit-backface-visibility: hidden;
                    -webkit-transform-origin: 100% 100%;
                    -moz-transform-origin: 100% 100%;
                    -ms-transform-origin: 100% 100%;
                    -o-transform-origin: 100% 100%;
                    transform-origin: 100% 100%;
                }

                + label:after {
                    -webkit-transform: scale(1);
                    transform: scale(1);
                    opacity: 1;
                }

                &:disabled {

                    + label {
                        opacity: .4;
                    }

                    + label:before {
                        border-right: 2px solid $checkbox_active_disabled;
                        border-bottom: 2px solid $checkbox_active_disabled;
                    }
                }
            }

            &:focus + label {
                color: $primary;
            }
        }

        .checkbox-push {
            display: inline-block;
            padding-left: 20px;

            input + label {
                padding-left: 28px;

                &:before {
                    top: 1px;
                }
            }
        }
        .dropdown {
            padding-left: 20px;

            select {
                min-width: 150px;
            }
        }

        [type="radio"] {

            &:not(:checked), &:checked {
                position: absolute;
                left: -9999px;
                opacity: 0;
            }

            + label {
                position: relative;
                padding-left: 35px;
                cursor: pointer;
                display: inline-block;
                height: 25px;
                line-height: 25px;
                font-size: 13px;
                font-weight: normal;
                -webkit-user-select: none;
                -moz-user-select: none;
                -khtml-user-select: none;
                -ms-user-select: none;

                &:before {
                    content: '';
                    position: absolute;
                    top: 2px;
                    left: 0;
                    width: 18px;
                    height: 18px;
                    z-index: 0;
                    border: 2px solid $radio_border;
                    border-radius: 50%;
                    margin-top: 2px;
                    -webkit-transition: all .3s cubic-bezier(0.5, -0.76, 0.3, 1.95);
                    transition: all .3s cubic-bezier(0.5, -0.76, 0.3, 1.95);
                }

                &:after {
                    content: '';
                    position: absolute;
                    border-radius: 50%;
                    top: 4px;
                    left: 0;
                    height: 18px;
                    width: 18px;
                    background: $radio_bg;
                    z-index: 1;
                    -webkit-transition: 0.2s;
                    transition: 0.2s;
                    -webkit-transform: scale(.1);
                    transform: scale(.1);
                    opacity: 0;
                }
            }

            &:not(:checked):disabled {

                + label:before {
                    border: none;
                    background-color: $checkbox_bg_disabled;
                }
            }

            &:checked {

                + label:before {
                    z-index: 2;
                    border: none;
                    background: $checkbox_active;
                    -webkit-transform: scale(.6);
                    -moz-transform: scale(.6);
                    -ms-transform: scale(.6);
                    -o-transform: scale(.6);
                    transform: scale(.6);
                    backface-visibility: hidden;
                    -webkit-backface-visibility: hidden;
                }

                + label:after {
                    -webkit-transform: scale(1);
                    transform: scale(1);
                    opacity: 1;
                }

                &:disabled {

                    + label:before {
                        border-right: 2px solid $checkbox_bg_disabled;
                        border-bottom: 2px solid $checkbox_bg_disabled;
                    }
                }
            }

            &:focus + label {
                color: $primary;
            }
        }
    }
    .can-be-activated,
    .grid-toolbar,
    .in-grid--section--modals,
    .in-grid--section--toolbar,
    .in-grid--section--settings,
    .in-grid--section--statusbar,
    .ts-grid--rawdata,
    .ts-grid--chart,
    .ts-grid--dashboard {
        display: none;

        &.active {
            display: block;
        }
    }

    .ts-grid--canvashelper {
        width: 1000px;
        height: 400px;
        position: absolute;
        top: 0;
        right: 100%;
        background: $white;
        opacity: .01;
        z-index: -999;
    }

    .arrowz {
        //text-align: right;
        //margin-top: 20px;
        display: flex;
        justify-content: center;

        & > div {
            margin-right: auto;
        }

        .floater {
            float: left;
        }

        button {
            display: inline-block;
            min-width: 40px;
            padding: 0 10px;
            margin:0 2px;
            font-size: 12px;
            height: 30px;
            border: 1px solid rgba($dark, 0.20);
            line-height: 28px;
            text-align: center;
            cursor: pointer;
            border-radius: 2px;
            white-space: nowrap;
            transition: .2s;

            &:hover:not([disabled]) {
                color: $white;
                background: $dark;
            }

            &:disabled {
                color: $input_disabled;
            }
        }
    }

    .grid-alert {
        font-size: 12px;
        padding: 8px 0;

        i {
            margin-right: 5px;
        }
    }

    .grid-alert:not(.grid-alert--light) {
        margin-bottom: 10px;
    }

    .grid-alert--light {
        font-weight: 500;

        &:before {
            @include icon();
            margin-right: 5px;
        }
    }

    .grid-alert--light--success {
        color: $success;

        &:before {
            content: "\F05D";
        }
    }

    .grid-alert--light--error {
        color: $error;

        &:before {
            content: "\EA0D";
        }
    }

    .grid-alert--light--warning {
        color: $warning;

        &:before {
            content: "\E001";
        }
    }

    .grid-alert--error {
        background: $error;
        color: $white;
        text-shadow: 1px 1px 0px rgba(0,0,0,0.5);
    }

    .grid-alert--success {
        background: $success;
        color: darken($success, 30%);
        text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.5);
    }

    .grid-alert--warning {
        background: $warning;
        color: $white;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.5);
    }

    .grid-alert--content-center {
        padding: 8px;
        text-align: center;
    }
}
