.#{$gridWrapper} {
    .ts-grid--notifications {
        position: absolute;
        right: 50px;
        top: $toolbarHeight;
        width: 380px;
        z-index: $notificationsZIndex;
        opacity:0;
        visibility:hidden;

        &.active {
            opacity:1;
            visibility:visible;
        }

        .n-box {
            position: relative;
            width: 100%;
            min-height: 4em;
            margin: 0 auto;
            padding: 10px 36px 5px 10px;
            @include boxshadow-md();
            background: $notification_bg;
            border-bottom: 1px solid $notification_border;
            -webkit-transform-origin: 50% 0%;
            transform-origin: 50% 0%;

            &.n-hide-loader {

                .n-loader {
                    display: none;
                }
            }

            .n-close, .n-pin {
                position: absolute;
                top: 5px;
                right: 5px;
                width: 17px;
                height: 17px;
                cursor: pointer;
                text-align:center;
                padding:1px;
                opacity:.6;

                &:before {
                    @include icon();
                    content: "\f00d";
                }
            }

            .n-pin {
                top: 5px + 5 + 17;
                transition:.1s;

                &:before {
                    content: "\f08d";
                }

                &.n-pinned {
                    opacity:1;
                    transform: rotate(30deg);
                }
            }

            .n-loader {
                display:none;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 8px;
                background: $notification_loader;
                cursor:pointer;
                height: 0;
                -webkit-animation-timing-function: linear;
                animation-timing-function: linear;
                -webkit-animation-name: countdownHeight;
                animation-name: countdownHeight;

                &.short {
                    -webkit-animation-duration: 8s;
                    animation-duration: 8s;
                }

                &.medium {
                    -webkit-animation-duration: 20s;
                    animation-duration: 20s;
                }

                &.long {
                    -webkit-animation-duration: 30s;
                    animation-duration: 30s;
                }

                &.sticky {
                    width:0;
                }
            }

            pre {
                padding: 0;
                border: none;
                background: transparent;
                max-height: 400px;
                overflow: auto;
                font-family: 'Lucida Console', monospace;
                font-weight:400;
                font-size: 12px;
                color: #666;

                .expander {

                    &.closed {

                        &:before {
                            content: ' + ';
                        }

                        + .collapsible {
                            display:none;
                        }
                    }
                    
                    &:before {
                        color:#aaa;
                        content: ' - ';
                        cursor:pointer;
                    }
                }
            }

            .ObjectBrace {
                color: #27ae60;
            }

            .ArrayBrace {
                color: #3268d9;
            }

            .PropertyName {
                color: #c0392b;
            }

            .String {
                color: #34495e;
            }

            .Number {
                color: #9b59b6;
            }

            .Boolean {
                color: #1224ee;
            }

            .Function {
                color: #d35400;
            }

            .Null {
                color: #0f2656;
            }

            .Comma {
                color: #bbb;
            }

            &.n-show {
                -webkit-animation-name: flipInX;
                animation-name: flipInX;
                -webkit-animation-duration: 0.8s;
                animation-duration: 0.8s;
            }

            &.n-hide {
                transition: all .2s ease;
                min-height: 0;
                height: 0 !important;
                overflow: hidden;
                padding: 0;
                border: none;
            }
        }

        .n-picture {
            height: 36px;
            width: 36px;
            display: block;
            border-radius: 50%;
            float: left;
            background: #A0D468;
            border: 1px solid darken(#A0D468,10%);
            box-shadow: inset 0 0 0 3px $white;
            position: relative;
            color: $white;
            display: none;

            i {
                position: absolute;
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
            }
        }

        .n-message {
            font-size: .9em;
            display: block;
            //width: 78%;
            width: 100%;
            //float: right;
            color: $notification_color;
            word-wrap: break-word;
        }
    }
}
