﻿.#{$gridWrapper} {

    .igrid-flex {
        display:flex;

        &.igrid-flex-vertical {
            flex-direction: column;
        }

        &.full-size {
            height: 100%;
            width: 100%;
        }

        &.igrid-flex-vertical {
            flex-direction: column;
        }

        .igrid-flexitem-noshrink{
            flex-shrink: 0;
        }

        .igrid-flexitem-nogrow{
            flex-grow: 0;
        }

        .igrid-flexitem-maxgrow{
            flex-grow: 100;
        }
    }
}