﻿.#{$gridWrapper} {
    
    .form-element-wrapper {
        position:relative;
        border-radius:2px;

        input[type=text], input[type=number], select, textarea, .fake-input {
            border-radius:2px;
            background-color:$input_bg;
            border: 1px solid $input_border;
            color:$input_color;
            font-size:13px;
            display: block;
            width: 100%;
            min-height: 34px;
            padding: 6px 12px;
            line-height: 1.42857143;
            color: $form_element__c;
            transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;

            &::-webkit-input-placeholder {
                color: $input_placeholder;
            }
            &::-moz-placeholder {
                color: $input_placeholder;
                opacity:  1;
            }
            &:-ms-input-placeholder {
                color: $input_placeholder;
            }

            &[disabled] {
                background-color: darken($input_bg,5%);
                color: lighten($input_color, 30%);
            }

            &[readonly] {
                background-color: $input_bg;
                color: lighten($input_color, 30%);
            }

            &:focus {
                outline:none;
                border-color:$active;
                box-shadow: inset 0 1px 1px rgba(0,0,0,0.075), 0px 2px 3px rgba(0, 0, 0, 0.175);
            }

            &.group-align {
                height:39px;
            }

            &.control-small {
                min-height: 26px;
                padding-top: 3px;
                padding-bottom: 3px;
            }
        }

        select {
            cursor:pointer;
            outline: 0 none !important;

            &::-ms-expand {
                display: none;
            }

            &:focus::-ms-value {
                background: transparent;
                color: $form_element__c;
            }

            optgroup {
                background:$lightest;
                color:$tertiary;
            }

            option {
                color:$form_element__c;

                &:disabled {
                    color: $input_disabled;
                }
            }
        }

        textarea {
            width:100%;
        }

        label {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-size:13px;
            padding: 0 10px 0 0; 
            font-weight:normal;
            margin-bottom:0;

            strong {
                color: lighten($secondary,10%);
            }
        }

        .formula-input {
            position: relative;

            input[type='text'] {
                padding-left: 22px;
            }

            .icon {
                position: absolute;
                top: 25%;
                left: 6px;
            }
        }
    }
}