﻿.#{$gridWrapper} {
    .buttons-after-hr {
        position:relative;

        button {
            position:absolute;
            right:21px;
            bottom:4px;
            width:32px;
            height:32px;
            border-radius:50%;
            border:none;
            background:$nav-color;
            @include boxshadow-sm();
            color:$white;
            opacity:.75;
            transform:scale(1);
            transition:all .2s cubic-bezier(1,0.89,0.2,1.45);
            z-index:2;
            outline: none;

            + span {
                visibility:hidden;
                opacity:0;
                position:absolute;
                height:22px;
                bottom:0px;
                border-radius:10px;
                right:44px;
                transition:.2s;
                background:rgba($lighter, 0.95);
                line-height:20px;
                padding: 0 10px;
                font-size:13px;
            }

            &:disabled {
                opacity: 0.4;
            }

            &:not([disabled]):hover {
                opacity:1;
                transform:scale(1.21);
                @include boxshadow-md();
                background:$primary;

                + span {
                    opacity:1;
                    visibility:visible;
                    transform:translate(-20px, 0);
                }
            }

            &:not([disabled]):active {
                transition:all .1s cubic-bezier(1,0.89,0,1.25);
                transform:scale(1.1);
                @include boxshadow-sm();
            }
        }
    }



    .button-wrapper--settings-list {
        text-align: center;
        min-height: 22px;
        max-height: 58px;
        flex: 1;
        transition: background .2s ease;

        button[data-tooltip]:after {
            left: auto;
            right: 50px;
            top: 0;
            height: 100%;
            line-height: 58px;
            background: $lighter;
            color: $nav_color;
            bottom: auto;
            z-index: 2;
            position: absolute;
            padding: 0 10px;
        }
    }

    .button-wrapper--block {
        position: relative;
        display: block;
    }

    .button-wrapper--align-right {
        text-align: right;
        margin-top: 10px;
    }

    .button-wrapper--fancy {
        margin: 15px -30px;
        padding:0 30px;

        &.has-error {

            .error,
            .error-list {
                left: 5px;
            }
        }
    }

    .button-wrapper--fancy--list {
        border-bottom:1px solid lighten($lightest,10%);
        box-shadow: inset 0 -20px 20px -10px rgba(darken($lightest, 0.20),30%);
        transition:all .2s cubic-bezier(0.51, -0.07, 0.29, 1.76);
        display: block;
    }

    .button-wrapper--fancy--settings {
        box-shadow: inset 0 -20px 20px -10px $rowBorder;
        border-bottom:1px solid $white;
    }

    .button-wrapper--submit {
        padding: 10px 0;
        text-align: center;
        width: 100%;
    }

    .button-wrapper--procedures-footer {
        margin: 10px 0 0;
        padding: 5px 20px 10px;
        text-align: right;
        width: 100%;
    }

    .button-wrapper--filters-footer {
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 0;
    }

    .button-wrapper--quick-buttons {
        white-space: nowrap;
        position: absolute;
        top: 10px;
        right: 10px;
        opacity: 0;
        visibility: hidden;
        transform: translate(0, -5px);
        transition: .2s;
    }

    .expanded .button-wrapper--quick-buttons {
        opacity: 1;
        visibility: visible;
        transform: translate(0, 0);
    }

    .button-wrapper--type-selection {
        border: solid 1px $light;
        display: flex;
        padding: 3px;
    }

    .button-wrapper--type-selection--paging {
        width: 100px;
    }

    .button-wrapper--content-center {
        width: 100%;
        text-align: center;
        margin-bottom: 10px;
    }

    .button-wrapper--type-selection--paging__size {
        padding: 2px 6px;
    }

    .button-wrapper--flex {
        display: flex;
    }

    .button-wrapper--spread {
        justify-content: space-between;
    }

    .button-wrapper--preview-actions {
        margin: 10px 0;

        button {
            margin-right: 10px;
        }
    }

    .button-wrapper--condition-confirmation {
        margin-top: 4px;
        margin-left: 5px;
    }


    .button-wrapper--modal-title {
        white-space: nowrap;
    }

    //buttons
    .button {
        color: $button-color;
        background: transparent;
        display: inline-block;
        position: relative;
        outline: none;
        border: 0;
        padding: 0;

        &:disabled {
            cursor: not-allowed;
            color: $disabledColor;
            //pointer-events: none;

            i {
                color: $disabledColor;
            }
        }
    }

    .button__toolbar {
        height: $toolbarHeight;
        line-height: $toolbarHeight;
        padding: 0 12px;
        @include toolbar-tooltip();

        &.info {
            background: $primary;
            color: $white;

            &:not(:disabled):hover {
                background:rgba($primary, 0.90);
            }
        }

        &:not(:disabled):hover {
            background:rgba($lighter, 0.40);
        }

        &:focus, &.active:focus {
            background:rgba($lighter, 0.40);
        }
    }

    .button__toolbar__text {
        margin-left:3px;
    }

    .button__toolbar--active:before {
        content: "";
        position: absolute;
        background: $primary;
        width: 100%;
        height: 3px;
        left: 0;
        bottom: 0;
    }

    .button__toolbar__number-indicator {
        position: absolute;
        border-radius: 8px;
        color: $lightest;
        transform: scale(.1);
        opacity: 0;
        transition: all .2s cubic-bezier(0.53, -0.45, 0.31, 1.79);
        z-index: 1;
    }

    .button__toolbar__number-indicator--pop {
        transform: scale(1);
        opacity: 1;
    }

    .button__toolbar__number-indicator--active {
        background: lighten($secondary,10%);
        font-size: 12px;
        left: 50%;
        bottom: 50%;
        min-width: 16px;
        line-height: 16px;
    }

    .button__toolbar__number-indicator--active--bottom {
        background: lighten($primary, 10%);
        bottom: 2px;
        font-size: 11px;
    }

    .button__toolbar__number-indicator--on {
        background: lighten($on, 10%);
        width: 6px;
        height: 6px;
        font-size: 8px;
        right: 2px;
        top: 4px;
    }

    .button__settings-list {
        width: 100%;
        height: 100%;
        font-size: 15px;

        i {
            position:absolute;
            top:50%;
            left:50%;
            transform: translate(-50%, -50%);
            transition: all .2s ease;
        }

        &:not(:disabled):hover {
            background:rgba($lighter, 0.40);

            &:after {
                transform: rotateY(0);
            }

            i {
                transform: translate(-50%, -50%) scale(1.3);
            }
        }
    }

    .button__settings-list--active,
    .button__settings-list--active:not(:disabled):hover {
        background:$lighter;

        &:after {
            display:none;
        }
    }

    .button__settings-list--modified:before {
        content: '';
        display: block;
        position: absolute;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(5px,-12px);
        transform: translate(5px,-12px);
        background: lighten($secondary,10%);
        z-index: 1;
    }

    .button--fancy {
        color:$dark;

        &:not(:disabled):hover {
            color: $tertiary;
        }
    }

    .button--fancy--list {
        padding: 16px 0;
    }

    .button--fancy--list--separator {
        padding-left: 48px;
    }

    .button--submit {
        transition: color 0.3s;
        transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
        padding: 6px 16px;
        color: $white;
        position: relative;
        z-index: 1;

        &:not(.button--no-min-width) {
            min-width: 80px;
        }

        &:not([disabled]):hover,
        &:not([disabled]):focus {
            color: $primary;

            &:before {
                opacity: 1;
                transform: scale3d(1, 1, 1);
            }

            &:after {
                opacity: 0;
                transform: scale3d(0.8, 0.8, 1);
            }
        }

        &:before, &:after {
            content: '';
            z-index: -1;
            pointer-events: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            backface-visibility: hidden;
            transition: transform 0.3s, opacity 0.3s;
            transition-timing-function: cubic-bezier(0.65, -0.92, 0.4, 1.82);
        }

        &:before {
            border: 2px solid $button2_border;
            opacity: 0;
            -webkit-transform: scale3d(1.2, 1.2, 1);
            transform: scale3d(1.2, 1.2, 1);
        }

        &:after {
            background: $primary;
        }

        &:not(.button--submit--alternative):disabled {
            color: $white;

            i {
                color: $white;
            }

            &:after {
                background: rgba($primary, 0.50);
            }
        }

    }

    .button--no-min-width {
        min-width: auto;
    }

    .button--tiny {
        font-size: 12px;
        padding: 4px 8px;
    }

    .button--submit--alternative {
        color: $button2_color;

        &:not([disabled]):hover {
            color:$button2_color_alt;
        }

        &:after {
            background: $white;
        }

        &:before {
            border: 2px solid $button2_color_alt;
        }
    }

    .button--submit--sadness {

        &:not([disabled]):hover {
            color:$button2_color_alt_2;
        }

        &:before {
            border: 2px solid $button2_color_alt_2;
        }
    }

    .button--list {
        padding: 10px 20px;
        text-align: left;
        width: 100%;

        &:not(:disabled):focus,
        &:not(:disabled):hover {
            color: $primary;
            background: lighten($lighter, 17%)
        }

        &:focus {
            font-weight: bold;
        }
    }

    .button--bold {
        font-weight: bold;
    }

    .button--link {
        color: darken($primary, 10%);

        &:not(:disabled):hover {
            color: $primary;
        }
    }

    .button--link--custom {
        padding-left: 10px;
    }

    .button--list-action {

        .button__button-text-content--no-hover {
            color: $button2_color;
        }

        &:not(:disabled):hover i {
            background: lighten($lighter, 15%);
        }

        i {
            float:left;
            display:block;
            height:24px;
            width:24px;
            line-height:24px;
            text-align:center;
            margin-right:0;
            transition: .2s;
        }
    }

    .button--list-action--active {
        color: $primary;
    }

    .toremove .remove {
        color: lighten($secondary,5%);
    }

    .isvisible .show {
        color: $primary;
    }

    .button--list-title {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        height: 42px;
        line-height: 42px;
        text-align: left;
        width: 100%;
        position: static;

        strong {
            cursor:e-resize;
            color:$lighter;
        }
    }

    .button--type-selector {
        border: solid 1px transparent;
        border-right-color: $btngroup_bg;
        padding: 4px 12px;
        transition:.2s;
        font-size: 12px;

        &:last-child {
            border-right-color: transparent;
        }

        &:disabled {
            color:rgba($btngroup_color, 0.45);
        }

        &:not(:disabled):hover {
            border-color: $btngroup_bg;
        }

        &:not([disabled]):focus {
            outline:none;
            border-color: $btngroup_bg_active;
        }

        &:after {
            color:$btngroup_color;
            background:$btngroup_bg_after;
            @include boxshadow-md();
        }
    }

    .button--type-selector--active {
        background-color: $btngroup_bg_active;
        border-color: $btngroup_bg_active;
        color:$white;
        box-shadow: none;

        &:disabled {
            background: rgba($btngroup_bg_active, 0.45);
            border-color: rgba($btngroup_bg_active, 0.45);
            color:$white;

            i {
                color: $white;
            }
        }
    }

    .button--type-selector--tiny {
        padding: 4px 6px;
        font-size: 10px;
        flex: 1 1 auto;

        &:not(:last-child) {
            margin-right: 3px;
        }

        i {
            margin-right: 0;
        }
    }

    .button--type-selector--with-separator {
        border-right: solid 1px $light;
    }

    .button--range-selector {
        height: 100%;
        width: 100%;
        border: 1px solid transparent;
    }

    .button--range-selector--active {
        height: 125%;
        margin-top: -3px;
        border-color: $primary;
    }

    .button--list-selection {
        background: $white;
        padding: 8px 50px 8px 20px;
        text-align: left;
        width: 100%;

        &:not(:disabled):hover, &:not(:disabled):focus, &.button--list-selection--active {
            background: $lightest;
            color: $black;
            padding-left: 25px;

            .button--list-selection__details {
                line-height: 14px;
                height: 14px;
                font-size: 14px;
            }
        }
    }

    .button--list-selection__name {
        display: block;
        padding: 4px 0;
    }

    .button--list-selection__name-icon {
        display: inline-block;
        position: relative;
        padding-right: 12px;
        text-align: center;
    }

    .button--list-selection__name-icon-bubble {
        position: absolute;
        border-radius: 8px;
        background: #12cbff;
        color: #f5f5f5;
        bottom: -6px;
        z-index: 1;
        font-size: 11px;
        min-width: 16px;
        line-height: 16px;
    }

    .button--list-selection__details {
        display: block;
        height: 0;
        overflow: hidden;
        transition: .1s;
    }

    .button--status-menu {
        text-align: left;
        padding: 4px 8px;
        width: 100%;

        &:not(:disabled):hover {
            background:rgba($lighter, 0.40);
        }
    }

    .button--status-add {
        vertical-align: top;
        width: $statusbarHeight;
        height: $statusbarHeight;
    }

    .button--paging {
        padding: 0 4px;
    }

    .button--miller-action {
        line-height: 24px;;
        width: 24px;
        flex-grow: 0;
        padding: 0;
        border: 1px dotted transparent;
        z-index: 2;
        transition: .2s;

        &:hover {
            border-style: dotted;
            background: rgba(darken($lightest, 0.8),50%);
        }

        &:focus {
            border-color: $primary;
        }
    }

    .button--miller-item {
        padding-left: 5px;
        text-align: left;
        padding-right: 5px;
        height: 24px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        flex-grow: 1;
        font-size: 12px;
    }

    .button--miller-item--flex {
        display: flex;
        line-height: 24px;
        margin-bottom: 2px;

        span {
            margin-left: 5px;
        }
    }

    .button--miller-item--has-next-col {
        max-width: 120px;
    }

    .button--miller-item--active .circle-filling {
        stroke-dasharray: 15.707963267948966, 15.707963267948966;
    }

    .button--miller-item--applied:before {
        @include icon();
        display: inline-block;
        color: lighten($secondary,10%);
        content: "\f00c";
        position: absolute;
        right: 5px;
        top: 6px;
    }

    .button--confirmation-action {
        height: 28px;
        width: 28px;
        text-align: center;
        margin: 2px 4px;
        padding-top: 6px;
        line-height: 18px;
        padding-bottom: 6px;
        color: $primary;

        &:not(:disabled):hover {
            background: lighten($lighter, 15%);
        }
    }

    .button--dimension-switch-toggle {
        font-size: 12px;
        font-weight: bold;
        margin-bottom: 5px;

        &.up {

            .icon-chevron-up {
                display: none;
            }
            .iconfa-chevron-down {
                display: inline;
            }
        }
        &.down {

            .icon-chevron-up {
                display: inline;
            }
            .icon-chevron-down {
                display: none;
            }
        }
    }

    .button--modal {
        color: $white;
        transition: background ease-in .2s;
        padding: 2px;

        span {
            display: flex;
        }

        &:hover {
            background: darken($primary, 10%);
        }
    }

    .button--toggle {
        font-weight: 600;
        padding: 4px 8px 4px 28px;
        position: relative;
        width: 100%;
        text-align: left;

        &:hover {
            color: $primary;
        }

        &:before {
            @include icon();
            display: inline-block;
            content: "\F063";
            position: absolute;
            top: 7px;
            left: 10px;
        }

        &.button--toggle--on:before {
            content: "\F062";
        }
    }

    .button--miller-toggle {
        height: 35px;
        border: 1px solid $input_border;
        width: 35px;
        border-radius: 2px;
        background: $white;
        margin-left: 5px;

        &:hover {
            background: lighten($input_border, 10%);
        }
    }

    .button--add-extra-row {
        margin-left: -28px;
        background: $white;
        top: -19px;
        padding: 5px 5px 5px 28px;
        width: calc(100% + 8px);
        text-align: left;
        
        i {
            position: absolute;
            left: 8px;
        }
    }
}
