﻿.#{$gridWrapper} {
    .tabs {
        display: flex;
        margin: 20px 0;
        align-items: flex-end;
        height: 40px;
        @include gradient(bottom, $lighter, rgba($lighter, 0.0));

        .tab {
            background: transparent;
            cursor: pointer;
            padding: 5px 30px;
            color: $nav-color;
            font-size: 12px;
            border: 1px solid $lightest;
            border-left: 0;
            flex-grow: 1;
            flex-wrap: nowrap;
            border-radius: 3px 3px 0 0;
            transition: .2s;
            outline: none;

            &:not(.is-tab-selected) {
                //background:$lighter;
                border-top-color: transparent;

                &[disabled] {
                    opacity: .4;
                    cursor: not-allowed;
                }
            }

            &.is-tab-selected {
                background: darken($lightest,4%);
                border-bottom-color: transparent;
            }

            &:focus {
                color: $primary;
            }
        }
    }
}
