﻿.#{$gridWrapper} {

    .grid-tooltip {
        position: absolute;
        z-index: 10000;
    }

    [data-tooltip-fix] {
        position: relative;
        display: inline-block;
    }

    [data-tooltip]:after {
        position: absolute;
        opacity: 0;
        z-index: 1000;
        content: attr(data-tooltip);
        bottom:100%;
        left:0;
        visibility:hidden;
        transition: all 0.3s ease;
    }

    [data-tooltip]:after,
    .grid-tooltip {
        pointer-events: none;
        background: $tooltip_bg;
        color: $tooltip_color;
        font-weight: normal;
        padding: 8px 10px;
        font-size: 12px;
        display: block;  
        white-space: nowrap;
    }

    [data-tooltip] {

        &.panel-warning:after {
            white-space: normal;
        }

        &:disabled {
        
            &.active {
                background-color: rgba(249, 86, 79, 0.65);
                border-color: transparent;
            }
        }
    }
            
    [data-tooltip]:hover:after, [data-tooltip].show-tooltip:after {
        opacity: 1;
        visibility:visible;
    }

    [data-tooltip] {

        &[data-tooltip-position='left']:after {
            bottom:auto;
            left:auto;
            right:100%;
            top:0;
        }
    }
}