﻿.#{$gridWrapper} {
    
    .ts-grid--loader {
        display:none;

        position:absolute;
        z-index:99999;
        top:0;
        left:0;
        right:0;
        bottom:0;
        background:rgba($black, 0.85);
        transition:.15s;
        -webkit-transform: scale(1);
        transform: scale(1);

        &.ishidden {
            opacity:0;
            visibility:hidden;
            -webkit-transform: scale(1.1);
            transform: scale(1.1);
        }

        &.isreallyhidden {
            display:none;
        }

        > div {
            position: absolute;
            left: calc(50% - 20px);
            top: calc(50% - 20px);
            border: 4px solid rgba(255, 255, 255, .1);
            border-top: 4px solid rgba(255, 255, 255, 1);
            border-radius: 50%;
            height: 40px;
            width: 40px;
            -webkit-animation: rot .6s infinite linear;
            animation: rot .6s infinite linear;
        }
    }
}