﻿@mixin hint() {
    position: absolute;
    top: 50%;
    right: 5px;
    transform: translate(0,-50%);
    background: $nav-color;
    color: $white;
    @extend .font-custom;
    font-size: 11px;
    border-radius: 3px;
    padding: 1px 5px;
    opacity: .86;
    transition: .2s;
}

.#{$gridWrapper} {
    .procedures {
        background: $lightest;

        div {
            outline: none;
        }

        .millercolumns--column {
            background: transparent;
        }
    }

    .procedures--body {
        min-height: $step-height;
        padding:7px 0;
        box-shadow: inset 0 10px 10px -12px rgba(0, 0, 0, 0.42);

        .split-title{
            display:flex;
        }

        h5 {
            font-size: 16px;
            font-weight: bold;
            position: relative;
            padding: 10px 20px;
            margin: 0px;

            &:before {
                content: '';
                position: absolute;
                top: -1px;
                bottom: 0;
                left: 0px;
                width: 5px;

                background: $primary;
            }
        }
    }

    .procedures--body__step {
        padding: 0 0 5px 0;
    }

    .single-condition__element {

        &:not(.button-wrapper) {
            flex-basis: 100%;
            margin-right: 5px;
        }

        &.button-wrapper {
            display: flex;
        }

        input,
        select {
            height: 20px;
        }

        textarea {
            height: 34px;
        }
    }

    .procedure {
        margin-top: 5px;
    }

    .single-condition {
        padding: 5px 20px;

        > .single-condition--error {
            padding: 0;
        }
        
        .single-condition--error-form-element {
            margin-left: 15px;
        }

        .single-condition--error {
            padding: 0;
        }

        &:not(.single-condition--error) {
            display: flex;
            justify-content: space-between;
        }

        &.has-checkbox .single-condition__element--value-editor {
            padding-top: 4px;
        }

        label {
            display: inline;
        }

        .hint {
            @include hint();
        }
    }

    .has-error .single-condition__element:first-child {
        margin-left: 15px;
    }

    .single-condition__element--hierarchical-wrapper {
        padding: 5px 20px; 
        display: flex;
        justify-content: space-between;        
    }

    .single-condition__element--hierarchical-components {
        flex: 0.49;
        display: flex;

        > div:first-child {
            flex: 1;
        }
    }   

    .single-condition__element--value-editor {
        padding-right: 40px;
    }

    .single-condition__element--value-editor--datetime .datepicker-advanced {
        padding: 0;
    }

    .single-condition__element--required:before {
        content: "*";
        position: absolute;
        left: 5px;
        top: 15px;
        color: $error;
    }

    .single-condition__wrapper--with-border {
        border-bottom: 1px solid $lighter;
    }

    .single-condition__wrapper--with-border:first-child {
        border-top: 1px solid $lighter;
    }

    .procdure__conditions__message__required-mark {
        color: $error;
    }

    .single-condition--error {

        .error-icon {
            left: 5px;
            z-index: 10;

            &.error-icon--top {
                top: 10px;
            }
        }

        .error-list {
            top: 0;
            z-index: 9;
            min-height: 44px;
            display: flex;
            align-items: center;

            li {
                padding-left: 20px;

                &:before {
                    display: none;
                }
            }
        }
    }

    .procedures__error-list {
        margin-bottom: 0;
    }

    .procedures--body--scroller {
        position: relative;
        max-height: 400px;
        overflow-y: auto;
    }

    .procedures-list {

        li {
            border-bottom: 1px solid $lighter;
            position: relative;
            line-height: 1;

            &:first-child {
                border-top: 1px solid $lighter;
            }

            &:last-child {
                border-bottom: 1px solid $lighter;
            }
        }
    }

    .procedures-footer {
        border-top: 1px solid $lightest;
        box-shadow: inset 0 10px 10px -12px $light;
    }

    .single-condition__simple-view,
    .single-condition__element__label {
        line-height: 34px;
    }

    .single-condition__simple-view--hierarchical-data {
        line-height: 34px;        
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .procdure__conditions__message {
        padding: 5px 20px;
        font-size: 11px;
        margin: 0;

        span {
            white-space: normal;
        }
    }

    .procdure__conditions__message--info {
        color: darken($infoBg, 60%);
    }

    .procdure__conditions__message--warning {
        background: $warningBg;
        color: darken($warningBg, 60%);
    }

    .single-condition__element--date {
        display: flex;
    }

    .checkbox-wrapper {
        text-align: center;

        label {
            margin-top: 4px;
        }
    }

    .procedure__preview {
        padding: 0 20px 10px 20px;

        h5 {
            font-weight: bold;
            font-size: 13px;
        }

        ul {
            padding: 0;
            margin: 0 0 0 10px;
            list-style-type: none;

        }

        p {
            margin-left: 10px;
        }

        table {
            margin-top: 10px;

            td, th {
                width: 100px;
                min-width: 100px;
                overflow: hidden;
                text-overflow: ellipsis;
                font-size: 12px;
                text-align: left;
            }

            tr td:first-child > div,
            tr th:first-child > div {
                height: 22px;
                width: 30px;
                min-width: 30px;
            }

            td {
                border: 1px dashed $lighter;
                border-left: none;
                border-right: none;
            }
        }
    }

    .single-condition__elements-wrapper {
        flex-basis: 100%;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    .single-condition__elements-wrapper--with-margin {
        margin-top: 10px;
    }

    .single-condition--advanced {
        flex-wrap: wrap;
    }

    .single-condition__element--datepicker-full-width .datepicker-advanced {
        width: 100%;
    }
}
