.#{$gridWrapper} {
    $panel-width: 370px;

    .ts-grid--configuration {
        width: $panel-width;
    }

    .ts-grid--configuration--other,
    .ts-grid--configuration--row-summary,
    .ts-grid--configuration--statusbar,
    .ts-grid--configuration--hide-tabs {
        right: $panel-width + $settingsSidebarWidth;
        display: flex;
    }

    .ts-grid--configuration--statusbar {
        .list-standard.list-extended li {
            padding-right: 74px;
        }

        .buttons-after-hr {
            button {
                right: -4px;
                bottom: -4px;

                &+span {
                    right: 21px;
                }
            }
        }
    }
}
