﻿.#{$gridWrapper} {

    .datepicker-advanced {
        position: relative;
        //width: 253px;
        padding: 0px;
        background: $datepicker_bg;
        //border: 1px solid darken($datepicker_bg,5%);
        text-align: center;
        background: rgba($datepicker_bg, 0.95);

        &.datepicker-advanced--no-header {

            .input-group {
                display: none;
            }
        }

        &:not(.datepicker-advanced--no-header){
            .calendar {
                position: absolute;
                left: 5px;
                top: 100%;
                z-index: 3;
            }
        }
    }

    .input-group-header {
        display: flex;
        margin-right: 3px;
    }

    .input-group-header__title {
        line-height: 34px;
        padding: 0 10px;
        font-weight: bold;
    }

    .calendar {
        outline: none;
        position: absolute;
        z-index: 10000;
        background: $lightest;

        box-shadow: 0 11px 23px rgba(0, 0, 0, 0.3), 0 6px 6px rgba(0, 0, 0, 0.22), 0 -4px 6px rgba(0, 0, 0, 0.22);

        .tiny-select {
            line-height: 1;
            position: relative;
            width: 70px;
            background: $white;
            border: solid 1px darken($lightest, 10%);
            text-align: left;
            padding-left: 5px;

            &:before {
                @include icon();
                position: absolute;
                content: "\F107";
                right: 3px;
                top: 2px;
            }

            label {
                line-height: 18px;
                font-weight: bold;
                cursor: pointer;
                margin-bottom: 0;
                padding-right: 15px;
                position: relative;
                z-index: 1;
                display: block;
            }
        }

        .tiny-select__options {
            position: absolute;
            width: 70px;
            background: $white;
            top: 18px;
            left: -1px;
            border-left: solid 1px darken($lightest, 10%);
            border-right: solid 1px darken($lightest, 10%);
            border-bottom: solid 1px darken($lightest, 10%);
            border-top: solid 1px $white;

            div {
                line-height: 18px;
                padding: 2px 0 2px 5px;
                cursor: pointer;

                &:hover {
                    background: darken($lightest, 10%);
                }
            }
        }

        .btn.btn-default,
        input[type="text"] {
            border-radius: 0;
        }

        input {
            width: 241px;
            padding-left: 9px;
        }

        .footer {

            .special-dates-wrapper {
                transition: all 0.2s ease-in;
                overflow: hidden;
                height: 0;
                position: absolute;
                bottom: 30px;
                background: rgba($datepicker_bg, 0.80);
            }

            &:hover .special-dates-wrapper {
                height: 212px;
            }
        }

        .header {
            display: flex;
            width: 241px;
            line-height: 30px;
            border-bottom: 1px solid darken($datepicker_bg,5%);

            span {
                flex-grow: 1;
                text-align: left;
                padding-left: 10px;
                font-size: 14px;
            }

            button {
                width: 30px;
                padding: 0 7px;
                background: $datepicker_bg;
                color: $datepicker_color;
                text-decoration: none;
                border: none;

                &:hover {
                    color: $datepicker_bg;
                    background: darken($datepicker_bg,25%);
                }
            }
        }

        .body {
            font-size: 12px;
            border-top: 1px solid $datepicker_border;
            width: 241px;
        }

        .cRow {
            display: flex;
            text-align: center;

            &:nth-child(2) {

                .cRow__day--week-number {
                    box-shadow: inset -6px 6px 5px -4px $datepicker_insetshadow;
                }
            }
        }

        .cRow__day {
            width: 30px;
            height: 30px;
            line-height: 30px;
            cursor: pointer;
            transition: .2s;
            
            &.disabled {
                opacity: 0.4;
                pointer-events: none;
            }

            &.cRow__day--day {

                &.notcurrent {
                    opacity: .4;
                }

                &:not(.active):hover {
                    background: darken($datepicker_bg,10%);
                }
            }

            &.active {
                background: $datepicker_color;
                color: $datepicker_bg;
            }

            &.cRow__empty,
            &.cRow__week-day {
                color: darken(darken($datepicker_bg,5%),10%);
                text-transform: uppercase;
                cursor: default;
            }

            &.cRow__empty {
                background: transparent;
            }

            &.cRow__day--week-number {
                color: $datepicker_color;
                background: darken($datepicker_bg,5%);
                cursor: default;
                box-shadow: inset -6px 0 5px -4px $datepicker_insetshadow;
            }
        }



        .shift-buttons {
            flex: 0 0 auto;
            padding: 5px 0 5px 5px;
            display: flex;
            position: relative;
            z-index: 2;
            text-align: center;

            > div {
                flex: 1 1 auto;
                display: flex;

                &:first-of-type {
                    margin-right: 5px;
                }

                button {
                    flex: 1 1 auto;
                    height: 18px;
                    width: 18px;
                    line-height: 18px;
                    padding: 0;
                    border-radius: 2px;
                    margin: 0 3px;
                    border: 1px solid rgba($white, 0.80);
                    transition: .2s;
                    color:$form_element__c;
                    background: $white;

                    &:hover,
                    &.active {
                        @include boxshadow-md();
                        background: $white;
                        color:$form_element__c;
                    }
                }
            }
        }
    }
}
