.#{$gridWrapper} {

    $hlistelement: 42px;

    .list-component__wrapper {
        height: 100%;

        &.list-component__wrapper--auto-height {
            height: auto;
        }
    }

    .list-component {
        display:flex;
        height: 100%;
        flex-direction: column;
    }

    .list-component__header-button {
        position:relative;
        height: 50px;
        padding: 10px 0px 5px 0px;

        button:hover + span {
            transform:translate(-30px, 0);
        }

        .error-list {
            z-index: 2;
        }

        .error {
            position:absolute;
            right:32px + 32px;
            bottom:3px;
            left:auto;
            z-index:3;
        }
    }

    .list-component__header-button-visibility {
            position:absolute;
            top: 5px;
            right:21px;
            width:32px;
            height:32px;
            border-radius:50%;
            border:none;
            background:$nav-color;
            @include boxshadow-sm();
            color:$white;
            opacity:.75;
            transform:scale(1);
            transition:all .2s cubic-bezier(1,0.89,0.2,1.45);
            z-index:2;
            outline: none;

            + span {
                visibility:hidden;
                opacity:0;
                position:absolute;
                height:22px;
                bottom:15px;
                border-radius:10px;
                right:44px;
                transition:.2s;
                background:rgba($lighter, 0.95);
                line-height:20px;
                padding: 0 10px;
                font-size:13px;
            }

            &:disabled {
                opacity: 0.4;
            }

            &:not([disabled]):hover {
                opacity:1;
                transform:scale(1.21);
                @include boxshadow-md();
                background:$primary;

                + span {
                    opacity:1;
                    visibility:visible;
                    transform:translate(-20px, 0);
                }
            }

            &:not([disabled]):active {
                transition:all .1s cubic-bezier(1,0.89,0,1.25);
                transform:scale(1.1);
                @include boxshadow-sm();
            }
        }

    .list-component__list-wrapper {
        overflow-y: auto;
        height: 100%;
        margin: 0 -25px 0 -30px;
        padding: 0 25px 0 30px;
    }

    .list-component__item {
        position: relative;
        display: flex;

        &:before {
            position:absolute;
            content:'';
            top:-1px;
            bottom:-1px;
            right:100%;
            width:30px;
            background: darken($lightest, 4%);
            transition:.1s;
            visibility:hidden;
            opacity:0;
        }

        &:after {
            @include icon();
            position:absolute;
            content:'\f104';
            top:15px;
            color:$primary;
            left:-20px;
            visibility:hidden;
            opacity:0;
            transition: all .2s ease .1s;
            -webkit-transform: translate3d(10px,0,0);
            transform: translate3d(10px,0,0);
        }

        &.item--selected {
            background: darken($lightest, 4%);

            &:before {
                visibility:visible;
                opacity:1;
            }

            &:after {
                visibility:visible;
                opacity:1;
                -webkit-transform: translate3d(0,0,0);
                transform: translate3d(0,0,0);
            }
        }
    }

    .list-component__item--error {
        color: lighten($error,10%);
        font-weight: bold;
    }

    .list-component__item--new {
        color: lighten($active,10%);
        font-weight: bold;
    }

    .list-component__item--removed {
        text-decoration:line-through;
    }

    .list-component__item--no-button {
        padding: 10px 0px;
    }

    .list-component--underline {

        .item--separator {
            border-bottom:1px dashed rgba($nav-color, 0.40);
        }
    }

    .list-component__item-buttons {
        display: flex;
    }

    .list-component__item-graphics {
        line-height: $hlistelement;

        span {
            margin-right: 5px;

            &:before {
                right: 77px;
                width:15px;
                text-align:center;
                text-shadow:0 1px 0 $white;
            }

            &.info:before {
                @include info-icon();
            }

            &.warning:before {
                @include warning-icon();
            }

            &.error:before {
                    @include error-icon();
                }
        }
    }

    .list-component__list-name-wrapper {
        flex-grow: 1;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
