﻿@import "../icomoon/icomoon.scss";



@font-face {
    font-family: "icomoon";
    src: url("../icomoon/fonts/icomoon.eot");
    src: url("../icomoon/fonts/icomoon.eot?#iefix") format("embedded-opentype"),
    	 url("../icomoon/fonts/icomoon.woff") format("woff"),
    	 url("../icomoon/fonts/icomoon.ttf") format("truetype"),
    	 url("../icomoon/fonts/icomoon.svg#icons") format("svg");
    font-weight: normal;
    font-style: normal;
}



@mixin icon() {
    display: inline-block;
    position: relative;
    font-family: "icomoon";
    font-style: normal;
    font-weight: normal;
    speak: none;
    text-decoration: inherit;
    line-height: 99%;
    text-align: center;
    vertical-align: baseline;

    // better font rendering
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.#{$gridWrapper} {

    .icon {

        &.rotate-45:before {
            transform: rotate(45deg);
        }

        &.rotate-90:before {
            transform: rotate(90deg);
        }

        &.rotate-180:before {
            transform: rotate(180deg);
        }

        &.rotate-270:before {
            transform: rotate(270deg);
        }

        &.flip-h:before {
            transform: scaleX(-1);
        }

        &.flip-v:before {
            transform: scaleY(-1);
        }

        &:before {
            @include icon();
        }
    }
    @include icon-classes();
}
