﻿
.#{$gridWrapper} {

    .grid-chart-gallery {
        height: 100%;
        padding-top: 30px;
        padding-bottom: 50px;
    }

    section.chart-gallery {
        background: rgba(245, 245, 245, 1);
        position: relative;
        padding: 10px;
        right: auto;
        width: 500px;
        overflow:visible;
        height: 100%;

        @media (max-width: 1000px) {
            bottom: 0;
        }

        .chart-widget {

            .svg {
                width: 100%;
                height: 210px;
            }

            @include boxedbutton();

            button {
                margin-top: 3px;
                min-width: 0;
            }

            div h4 {
                cursor: pointer;
                display: inline-block;
                @include noselect();
            }
        }

        .width-anchor {
            bottom: 0;
            cursor: ew-resize;
            position: absolute;
            right: 0;
            top: 0;
            width: 9px;

            .stretcher {
                display: none;
            }

            &.active {
                border-right: 4px solid rgba(125, 125, 125, 0.8);

                .stretcher {
                    @extend .stretcher-base;
                }
            }
        }
    }

    .chart-gallery-visible {

        .chart-gallery {
            display: block;
        }

        .chart-gallery--widgets {
            width:100%;
            height:100%;
            overflow:auto;

            .chart-widget {
                margin: 10px 0;
            }
        }

        .chart-widget--header {
            padding:10px 10px 0 0;
        }

        .in-grid--section--grid-modal {
            left: 500px;
        }
    }

    .chart-modal-content {
        height:100%;
    }
}
