﻿.#{$gridWrapper} {
    
    section {
        background: $lightest;
        position:absolute;
        top: 0;
        right:$settingsSidebarWidth;
        bottom: 0;
        width:320px;
        overflow-x:hidden;
        overflow-y:auto;
        box-shadow:inset -10px 0 10px -8px rgba(0,0,0,.3);
        display:none;
        z-index: $sectionZIndex;
        padding:30px 30px 15px;
        color:$nav-color;
        user-select: text;


        &.no-scroll {
            flex-direction:column;

            .block-fluid {
                flex:1; //flex:0 1 auto; will take only needed space //flex:1; will take all available space and push lower blocks to bottom
                overflow-y: auto;
                overflow-x: hidden;
                margin: 0 -25px 0 -30px;
                padding: 0 25px 0 30px;

                &.no-overflow {
                    overflow-y: hidden;
                }
            }

            .block-fixed {
                flex:0 1 auto;
            }
        }

        &:not(.active) .active, &:not(.active) .has-error.errors-expanded .error-list, &:not(.active) .list-standard.list-extended li.more:before, &:not(.active) .list-standard.list-extended li.more:after {
            visibility:inherit; //well...
        }

        &.may-animate {

            &.active {
                transition: all .15s ease;

                + section {
                    transition: all .15s ease;
                }
            }
        }

        hr {
            border-top:1px solid darken($lightest,14%);
            border-bottom:1px solid lighten($lightest,10%);
            margin-left:-30px;
            margin-right:-30px;
            margin-top:30px;
            margin: 10px -30px 10px -30px;
        }

        &.active {
            display:block;
            -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);

            &.no-scroll {
                display:flex;

                &[class*="--details"] .block-fluid {
                    overflow-x: hidden;
                }
            }
        }

        .section-pin {
            position:absolute;
            top:0;
            right:0;
            cursor:pointer;
            font-size:14px;

            &:before {
                position:absolute;
                z-index:1;
                top:0;
                right:0;
                content:'';
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 40px 60px 0;
                border-color: transparent rgba($lighter, 0.30) transparent transparent;
                transition:.2s;
            }

            i {
                position:absolute;
                z-index:10;
                top:14px;
                right:10px;
                transition: all .3s cubic-bezier(0.56, -0.55, 0.4, 1.99);
                display:inline-block;
            }

            &:hover {
                color:$white;
            }

            &.active, &.active:hover {
                color:$dark;

                &:before {
                    border-width: 0 46px 69px 0;
                    border-color: transparent rgba($lighter, 0.75) transparent transparent;
                }

                i {
                    -webkit-transform: scale(1.3) rotate(60deg) translate(1px, 3px);
                    transform: scale(1.3) rotate(60deg) translate(1px, 3px);
                }
            }
        }

        .search {
            position:relative;
            height:60px;
            vertical-align:middle;
            width:100%;

            i {
                position:absolute;
                top:23px;
                left:0;
                color:rgba($dark, 0.20);
                transition: .2s;
                text-shadow:none;
            }

            input.form-control {
                display:inline-block;
                width:300px;
                height: 40px;
                background-color:transparent;
                box-shadow:none;
                border:none;
                border-radius:0;
                color:rgba($lighter, 0.60);
                font-size:13px;
                border-bottom:2px solid rgba($dark, 0.40);
                padding-left:24px;
                margin-top:10px;
                outline:none!important;

                &:focus {
                    box-shadow:none;
                    border-color:$dark;
                    outline:none;

                    + i {
                        color:rgba($dark, 0.80);
                    }
                }
            }
        }

        h4 {
            position:relative;
            font-size: 16px;

            .panel-warning i {
                margin-left: 10px;
                color: darken($warningBg, 30%);
            }

            &:before {
                content:'';
                position:absolute;
                top:-5px;
                bottom:-5px;
                left:-30px;
                width:5px;
                background:$primary;
            }

            &:nth-of-type(2) {

                &:before {
                    background: lighten($secondary,10%);
                }
            }

            &:nth-of-type(3) {

                &:before {
                    background:$dark;
                }
            }

            & ~ h4 {
                margin-top:10px;
            }

            i {
                text-align:center;
                width:20px;
                margin-right:10px;
            }
        }

        .alert.alert-info {
            border-radius:1px;
            text-shadow: none;
            border-radius: 1px;
            margin-bottom: 0;
            background-color: transparent;
            @include gradient(top, rgba($lighter, 0.50), fade($lighter,0));
            color:lighten($dark, 30%);
            box-shadow: inset 0 12px 10px -10px rgba($black, 0.55);
            border:none;
            border-top:1px solid lighten($lightest,5%);
            margin: 0 -30px 20px -20px;
            padding: 25px 44px;
            position:relative;

            &.alt {
                margin: 20px -30px 20px -30px;

                &:after {
                    display:none;
                    @include gradient(left, $lightest, fade($lightest,0));
                }
            }

            &:before {
                @include icon();
                content:'\f05a';
                color:$dark;
                margin-right:10px;
            }

            &.warning {
                border-top-color: rgba(lighten($warningBg, 0.20),30%);
                @include gradient(top, rgba($warningBg, 0.30), fade($warningBg,0));
                color: darken($warningBg, 30%);

                &:before {
                    content:'\f071';
                }
            }

            &:after {
                content:'';
                position:absolute;
                top:-1px;
                left:0;
                bottom:0;
                width:100px;
                @include gradient(left, darken($lightest,4%), rgba(darken($lightest, 0.4),0));
            }
        }

        .tabs {
            margin: 0 -30px 0;

            &:first-child {
                margin-top:0;
            }

            .tab {

                &:last-child {
                    border-right:0;
                    box-shadow:inset -10px 0 6px -4px rgba(0,0,0,.1);
                }
            }
        }

        .formula-input {
            position:relative;

            i.icon {
                position:absolute;
                top:11px;
                left:6px;
            }

            textarea {
                padding-left:28px;
            }

            button {
                position:absolute;
                border:none;
                top:6px;
                right:6px;
                bottom:6px;
                padding-left:14px;
                padding-right:14px;
                z-index:2;
                background:$lightest;
                transition:.2s;
                font-size:12px;

                i {
                    font-size:14px;
                    margin-left:6px;
                }

                &:hover {
                    color:$white;
                    background:$primary;
                }
            }

            &:after {
                z-index:1;
                position:absolute;
                content:'';
                top:0;
                right:100px;
                bottom:1px;
                width:100px;
                @include gradient(left, rgba(darken($lighter, 0.2), 0%), rgba(darken($lighter, 0.2), 100%));
            }

            &.edit-hidden {

                &:after {
                    display:none;
                }
            }
        }

        .nest-arrow {
            margin: 0 13px 0 10px;
            opacity: 0.5;
        }

        table.table {
            margin-left: -30px;
            margin-right: -30px;
            width: auto;
            max-width: none;
            border-collapse: separate;
            border: none;
            min-width: calc(100% + 60px);

            > thead {

                > tr {

                    > th {
                        border-right:1px solid $lighter;
                        border-bottom: none;
                        vertical-align: top;
                        @include gradient(top, rgba($black, 0.0), rgba($black, 0.10));
                        width: 190px;
                        position: relative;

                        &.has-value {
                            width: 150px;
                        }

                        &.has-buttons {
                            width: 40px;
                            border-right:none;
                        }

                        &:first-child {
                            padding-left:25px;
                        }

                        .levels__description-dictionary {
                                float: left;
                                width: calc(100% - 28px);
                                word-break: break-all;
                        }

                        button {

                            background: none;
                            border: 1px dotted transparent;
                            float: right;
                            color: lighten($secondary,5%);
                            margin-right: 2px;
                            min-height: 26px;
                            min-width: 26px;

                            &:disabled {
                                color: rgba($nav-color, 0.50);
                                cursor: default;
                            }
                            i {
                                transition: .1s;
                            }
                            &:not([disabled]):hover i {
                                color: $secondary;
                            }

                            &.filter-reset {
                                position: absolute;
                                bottom: 20px;
                                right: 8px;
                            }

                            &:focus {
                                border-color: $primary;
                            }
                        }

                        .select {
                            margin-top: 10px;
                            margin-bottom: 10px;
                            font-weight: normal;

                            input:first-child[readonly] {
                                box-shadow: none;
                                font-size: 12px;
                                width: 100%;
                                font-weight: normal;
                                padding-left: 10px;
                                opacity: .75;
                                transition: .2s;
                                min-height: 20px;
                                padding-top: 4px;
                                padding-bottom: 4px;

                                &:hover {
                                    opacity: 1;
                                }
                            }

                            ul {

                                li {
                                    white-space: nowrap;
                                    overflow: hidden;
                                    text-overflow: ellipsis;
                                }
                            }
                        }
                    }
                }
            }

            > tbody {

                > tr {

                    > td {
                        border-right:1px solid $lighter;
                        border-bottom: 1px solid rgba($black, 0.16);
                        border-top: 1px solid $lightest;
                        transition: .2s;
                        vertical-align: middle;
                        cursor: default;
                        padding: 6px 14px;
                        position: relative;
                        font-size: 12px;

                        &.form-element-wrapper {

                            input {
                                padding: 3px 8px;
                                min-height: 26px;
                            }
                        }

                        .ts-grid--dropdown {

                            input {
                                padding: 3px 35px 3px 8px;
                                min-height: 26px;
                                text-overflow: ellipsis;
                                overflow: hidden;
                            }
                        }

                        &.add-entry {
                            background: rgba($black, 0.16);

                            div {
                                transition: .2s;
                                button {
                                    border: none;
                                    background: none;
                                    width: 100%;
                                    text-align: left;

                                    &:disabled {
                                        opacity: .5;
                                        cursor: default;
                                    }

                                    &:not([disabled]):hover {
                                        color: $dark;
                                    }
                                }
                            }
                        }

                        &.has-value {
                            width: 150px;

                            input.form-control {
                                text-align: right;
                            }

                            button {
                                margin-top: 4px;
                                padding: 2px;
                                background: transparent;
                                border: none;
                                color: lighten($secondary,10%);
                                font-size: 11px;

                                &:last-child {
                                    color: lighten($secondary,5%);
                                }
                            }
                        }

                        &.has-buttons {
                            text-align: center;
                            width: 40px;
                            padding-left: 0;
                            padding-right: 10px;
                            border-right:none;

                            &.ishighlighted {
                                background: $dark;
                                color: $lightest;
                                cursor: pointer;

                                &:hover {
                                    background: $dark;
                                }
                            }

                            button {
                                background: transparent;
                                border: 1px dotted transparent;
                                color: lighten($secondary,10%);
                                min-height: 26px;
                                min-width: 26px;

                                &[disabled] {
                                    color: rgba($nav-color, 0.50);
                                }

                                &:after {
                                    left: auto;
                                    right: 0;
                                }

                                &:focus {
                                    border-color: $primary;
                                }
                            }

                            + .has-buttons {

                                button {
                                    color: lighten($secondary,5%);

                                    &[disabled] {
                                        color: rgba($nav-color, 0.50);
                                    }
                                }
                            }
                        }

                        &:first-child {
                            padding-left:25px;
                        }
                    }

                    &:hover {

                        > td {
                            background: rgba($white, 0.50);
                        }
                    }

                    &.new-row {

                        > td {
                            background:$white;
                        }
                    }

                    &:first-child {

                        > td {
                            border-top: none;
                        }
                    }
                }
            }
        }    
    
        .table-pager {
            display:flex;
            margin: 0 -30px;
            border-bottom:1px solid $lighter;
            @include noselect();

            button {
                flex:1;
                border:none;
                background:transparent;
                padding:8px 10px;
                cursor:pointer;
                transition:.2s;

                i {
                    margin: 0 6px;
                }

                &:hover {
                    background:$dark;
                    color:$lightest;
                }

                &[disabled] {
                    opacity:0;
                    cursor:default;
                }
            }
        }
    }

    $haction: 24px;
    $waction: 24px;
    $hlistelement: 42px;
    $typeAbbreviationTotalWidth: 30px;

    .type-wrapper {
        line-height: $hlistelement;
        width: 24px;
        margin-right: 5px;
    }

    .type-abbreviation {
        @include type-abbreviation();
    }

    .list-standard {
        margin-top:10px;

        li {
            cursor:pointer;
            display:block;
            padding: 8px 0;
            border-bottom:1px dashed rgba($nav-color, 0.40);
            color:darken($nav-color,4%);
            transition: all .1s ease;

            &:last-child {
                border-bottom:none;
            }

            &.active {
                color:lighten($nav-color,16%);

                i {
                    color: $primary;
                }

                &.negative {

                    i {
                        color: lighten($secondary,5%);
                    }
                }
            }

            &:hover {
                color:lighten($nav-color,10%);
            }

            button:not(.button) i {
                margin-right: 5px;
            }
        }

        &.list-extended {

            @include noselect();

            li {
                position:relative;
                padding: 0 (($waction + 2px)*4) + $typeAbbreviationTotalWidth + 10px 0 0;
                cursor:auto;

                i.error {
                    color:$secondary;
                }

                > i {
                    cursor:n-resize;
                    width:$waction;
                    height:$haction;
                    position:absolute;
                    top:0;
                    left:0;
                    text-align:center;
                    line-height:$haction;
                    font-size:16px;
                    opacity:.5;
                }

                .actions {
                    position:absolute;
                    top:0;
                    right:0;
                    height: $hlistelement;
                    overflow:hidden;
                    display: flex;
                }

                &:before {
                    position:absolute;
                    content:'';
                    top:-1px;
                    bottom:-1px;
                    right:100%;
                    width:30px;
                    background: darken($lightest, 4%);
                    border-bottom:1px dashed rgba($nav-color, 0.40);
                    border-top:1px dashed rgba($nav-color, 0.40);
                    transition:.1s;
                    visibility:hidden;
                    opacity:0;
                }

                &:after {
                    @include icon();
                    position:absolute;
                    content:'\f104';
                    top:15px;
                    color:$primary;
                    left:-20px;
                    visibility:hidden;
                    opacity:0;
                    transition: all .2s ease .1s;
                    -webkit-transform: translate3d(10px,0,0);
                    transform: translate3d(10px,0,0);
                }

                &:first-child {

                    &:before {
                        border-top: none;
                        top:0;
                    }
                }

                &:last-child {

                    &:before {
                        border-bottom: none;
                    }
                }

                .button--list-title .icon.new,
                .button--list-title .icon.modified,
                .button--list-title .icon.error {
                    display: none;
                }

                &:not(.error):not(.toremove).new .button--list-title .icon.new,
                &:not(.error):not(.toremove).modified .button--list-title .icon.modified {
                    display: inline;
                }

                &.error .icon.error {
                    display: inline;
                }

                &:not(.error).modified button,
                &:not(.error).new button {
                    color: lighten($active,10%);
                    font-weight: bold;
                }

                &.error button {
                    color: lighten($error,10%);
                    font-weight: bold;
                }

                &.more {
                    background: darken($lightest, 4%);

                    &:before {
                        visibility:visible;
                        opacity:1;
                    }

                    &:after {
                        visibility:visible;
                        opacity:1;
                        -webkit-transform: translate3d(0,0,0);
                        transform: translate3d(0,0,0);
                    }
                }

                &.toremove {

                    .button--list-title {
                        text-decoration:line-through;
                    }
                }
            }
        }
    }

    .list-nested {
        $sortableListLeftPadding: 50px;

        li {
            position:relative;

            span {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                max-width: 100%;
                display: inline-block;
                vertical-align: middle;
            }


            .options {
                position:absolute;
                top:0;
                right:0;
                cursor:pointer;
                z-index:2;

                button {
                    float:left;
                    width:38px;
                    overflow:hidden;
                    transition:.15s;
                    opacity:.2;
                    height:38px;
                    border:none;
                    background:transparent;

                    &.active {
                        color:$primary;
                        background:rgba($lighter, 0.20);
                        opacity:1;
                    }

                    &[disabled] {
                        opacity:0!important;
                    }
                }
            }

            &:hover {

                .options {

                    button {
                        opacity:1;

                        &:hover {
                            background:rgba($lighter, 0.50);
                        }
                    }
                }
            }

            &:before {
                position:absolute;
                content:'';
                top:-1px;
                bottom:-1px;
                right:100%;
                width:30px;
                background:darken($lightest,4%);
                border-bottom:1px dashed rgba($nav-color, 0.40);
                border-top:1px dashed rgba($nav-color, 0.40);
                transition:.1s;
                visibility:hidden;
                opacity:0;
            }

            &:after {
                @include icon();
                position:absolute;
                content:'\f104';
                top:15px;
                color:$primary;
                left:-20px;
                visibility:hidden;
                opacity:0;
                transition: all .2s ease .1s;
                -webkit-transform: translate3d(10px,0,0);
                transform: translate3d(10px,0,0);
            }

            &:first-child {
                &:before {
                    border-top: none;
                    top:0;
                }
            }

            &:last-child {
                &:before {
                    border-bottom: none;
                    top:0;
                }
            }

            &.more {
                background:darken($lightest,4%);

                &:before {
                    visibility:visible;
                    opacity:1;
                }

                &:after {
                    visibility:visible;
                    opacity:1;
                    -webkit-transform: translate3d(0,0,0);
                    transform: translate3d(0,0,0);
                }
            }

        }

        > li {
            padding-left:0;

            &:first-child {

                > i {
                    display:none;
                }
            }

            &.add {
                color:$dark;

                span {
                    font-style:italic;
                }

                &:after {
                    display:none;
                }
            }
        }
        &.list-sortable > li {
            padding-left:$sortableListLeftPadding;
        }


        > li:nth-child(9 + n){padding-left:16px + 24px * 8;}

        ul {
            padding-left:20px;
            padding-top:10px;

            li {
                padding-left:0;
            }
        }
    }

    .list-sortable {
        $sortableListLeftPadding: 50px;
        li {
            padding-left: $sortableListLeftPadding;
            position:relative;
            cursor:default;
            .arrows {
                position:absolute;
                left:0;
                top:0;
                width:40px;
                bottom:0;
                cursor:pointer;
                //opacity:.2;
                //transition:.2s;

                i {
                    width:18px;
                    overflow:hidden;
                    height:38px;
                    float:left;
                    transition:.15s;
                    margin-right:0;
                    text-align:center;
                    line-height:38px;
                    opacity:.3;

                    &:hover {
                        background:rgba($lighter, 0.50);
                    }
                }
            }

            &:hover {

                .arrows {
                    //opacity:1;

                    i {
                        opacity:1;
                        width:20px;
                    }
                }
            }
        }
    }

    .simplified-info {
        margin-left:18px;
        margin-top:5px;
        font-size:12px;
        opacity:.8;

        i {
            position:relative;
            top:2px;
            font-size:14px;
            color:$primary;
            margin-right:10px;
        }
    }

    .select {
        position:relative;

        &.filterable:not(.static) {

            .filter {
                top:0;
                position: absolute;
                left:0;
                right:0;
                background:$select_bg;
                height:100%;

                &:focus {
                    box-shadow:none;
                }
            }

            ul {
                top:100%;
                list-style:none;
                padding:0;
                border-top:1px solid $lightest;

                li {
                    padding: 10px 8px;
                }
            }
        }

        input.selected-value,
        .fake-input {
            padding-right:40px;
            background: url(../images/select.png) calc(100% - 11px) center no-repeat;
            background-color:$input_bg;

            &:not([disabled]),
            &:not(.disabled) {
                cursor: pointer;
            }

            &[readonly] {
                background-color:$select_bg;
            }
            &[disabled],
            &.disabled {
                background-color: rgba(255, 255, 255, 0.32);
                pointer-events: none;
                color: lighten($input_color, 30%);
            }
        }

        .filter {
            position:absolute;
            z-index:100;
            left:0;
            right:0;
            top:100%;
        }

    }

    input:focus + .select-list {
        display:block;
    }

    .select-list {

        .select-indent {
            padding-left: 10px; 
            display: inline-block;
        }
        
        &:not(.error-list) {
            max-height: 300px;
            overflow-y: auto;
            background-color: $select_bg;
            color: $select_color;
            @include boxshadow-xl;
            padding: 0;
            transition: .1s;
            display: none;

            &:not(.static) {
                position: absolute;
                z-index: 100;
                left: 0;
                right: 0;
                top :100%;
            }

            &.static {
                position: relative;
            }

            li {
                transition:.2s;
                font-size: 12px;
                padding: 8px;
                text-shadow:none;

                &:not(.no-hover):hover, &.hovered {
                    background: $select_bg_hover;
                    color: $select_color_hover;
                    padding-left:10px;
                }

                &.active {
                    padding-left: 10px;
                    background: $select_bg_active;
                    color: $select_color_active;
                    text-shadow:none;
                }
            }
        }
    }

    &.select-expand-top {

        .select-list{
            top:auto;
            bottom:100%;
        }
    }

    .icon-load-icon {
        -webkit-animation: fa-spin 1s infinite ease-in-out;
        animation: fa-spin 1s infinite ease-in-out;
        width: 1.28571429em;
        text-align: center;

        &:before {
            content:"\f1ce" !important;
        }
    }

    .value-selector {
        display: inline-block;
    }
}
