﻿.#{$gridWrapper} {
    
    .ts-grid--dropdown {
        display: block;
        position : absolute;
        min-width : 200px;

        input.form-control {
            padding: 10px 3px;
            height:40px;
            border:none;
            background:transparent;
            outline:none;
            background:$dropdown_bg;
            border-bottom:1px solid darken($dropdown_bg,10%);

            &:focus {
                outline:none!important;
                box-shadow:none!important;
            }
        }

        input.form-control:first-child {
            cursor:default;
        }

        ul {
            position:relative;
            list-style: none;
            padding:0;
            margin: 0;
            max-height: 189px;
            overflow: auto;
            font-size:12px;
            background-color: $select_bg;
            color: $select_color;

            li {
                padding: 5px;
                margin: 0;
                &:not(.no-hover) {
                    cursor: pointer;
                }
                
                strong {
                    color: $select_color;
                }

                &.hovered {
                    background: $select_bg_hover;
                    color: $select_color_hover;

                    strong {
                        color: $select_color_hover;
                    }
                }
            }
        }
    }

    .multiselect {
        position:relative;

        .fake-input--wrapper {
            max-height: 150px;
            overflow-y: auto;
        
            .fake-input {
                border-radius:2px;
                text-align:left;
                background: $white;
                background-color:$input_bg;
                border: 1px solid $input_border;
                color:$input_color;
                font-size:13px;
                display: block;
                width: 100%;
                min-height: 34px;
                padding: 0 2px;
                line-height: 1.42857143;
                color: $form_element__c;
                transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;

                ul.selected {
                    display:inline-block;
                    margin-top:0;
                    white-space:normal;

                    li {
                        //float:left;
                        display:inline-block;
                        margin: 2px 0 2px 1px;
                        border: 1px solid $lightest;
                        border-radius:3px;
                        background: rgba($lightest, 0.50);
                        padding: 3px 5px;

                        &.input-element {
                            padding:0;
                            border:0;
                        }
                        
                        &.placeholder-element {
                            display:none;
                            border:none;
                            padding-left: 0;
                            background:transparent;
                            color:lighten($tertiary, 4%);
                            
                            &:first-child{
                                display:inline-block;
                            }
                        }

                        .remove{
                            padding:0;
                            margin-left:3px;
                            font-size:12px;
                            color:$light;
                            border-radius:4px;
                            cursor: pointer;

                            i {
                                font-size:13px;
                            }
                        }
                
                        input {
                            display:inline-block;
                            padding: 0;
                            height: 29px;
                            border: none;
                            transition: initial;
                            outline: none;
                            background: $white;
                            line-height: 29px;
                            min-height: 29px;
                        }
                    }
                }
            }
        }

        .new-element {
            color: lighten($secondary,5%);
        }

        .options {
            opacity:0;
            visibility:hidden;
            max-height: 140px;
            overflow: auto;
            display: none;
            z-index:5;
            position:absolute;
            left:0;
            top:100%;
            width:100%;
            padding:0;
            border:0;
            box-shadow:none;
            background-color: $select_bg;
            color: $select_color;

            li {
                display:list-item;
                position: relative;

                &:not(.no-hover) {
                    cursor: pointer;
                }
            
                &:not(.no-hover):hover {
                    color: $select_color_hover;
                    background: $select_bg_hover;
                }  

                &.active,
                &.selected {
                    color: $select_color_active;
                    background: $select_bg_active;
                }

                .remove-element {
                    position: absolute;
                    right: 10px;
                    padding: 0px !important;
                    height: 20px !important;
                    width: 20px !important;
                    color: black !important;
                    top: 0px !important;
                }
            }
        }

        ul {
            position: relative;
            list-style: none;
            padding: 0;
            margin: 0;
            font-size: 12px;
            
            li {
                padding: 5px;
                margin: 0;
            
                strong {
                    color: $active;
                }
            
                &.hovered {
                    background: $active;
                    color: $white;
                }

                &.hovered strong {
                    color: $white;
                }
            }
        }
    }

    .focused{ 
        .multiselect {
            position:relative;
            text-align:left;
            background: $white;

            .options {
                display: block;
                opacity:1;
                visibility:visible;
                padding:1px;
                border:1px solid $light;
                box-shadow: 1px 1px 3px 1px fade($dark, 30);
            }

            .fake-input {

                ul.selected {
            
                    li{
                        &.placeholder-element:first-child,
                        &.placeholder-element {
                            display:none;
                        }
                    }
                }
            }
        }
    }
}