﻿/// <reference path="../../grid-wrapper.scss" />
.#{$gridWrapper} {

    section {

        $panel-width: 370px;

        &.ts-grid--layout-dictionaries {
            width: $panel-width;
        }

        .parent-select {
            margin-bottom: 20px;
        }

        &.ts-grid--layout-dictionaries--details {
            background: darken($lightest,4%);
            right: $panel-width + 20px;
            width: $panel-width + 50px;
            overflow-x:hidden;
            overflow-y:auto;
            -webkit-transform: translate3d(40px, 0, 0);
            transform: translate3d(40px, 0, 0);

            &.active {
                -webkit-transform: translate3d(0,0,0);
                transform: translate3d(0,0,0);

                + .ts-grid--layout-dictionaries {
                    width: $panel-width - 30;
                }
            }
        }
    }
}