﻿.#{$gridWrapper} {

    .grid-tabs__tabs-wrapper {
        display: flex;
        flex: 0.5;
        margin: 0;
        height: 100px;
        padding: 0px 25px $ulPadding 25px;
        overflow-x: auto;
        overflow-y: hidden;
        position: relative;
    }

    .grid-tabs {
        display:none;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 0px $settingsSidebarWidth 0px 0px;
        @extend .font-custom;
        background: $header_bg;
        box-shadow: inset 0px 2px 3px darken($header_bg, 50%);
        z-index: $horizontalToolbarZIndex;

        &.active {
            display: inherit;
        }

        &.with-statusbar {
            bottom: $statusbarHeight;
        }

        >nav {
            height: $toolbarHeight;
            display:flex;
            justify-content: space-between;

            .dragable-arrow {
                top: -3px;

                &.dragable-arrow--left {
                    left: -3px;
                }

                &.dragable-arrow--right {
                    right: 3px;
                }
            }

            ul {
                display:flex;
                overflow-x: auto;
                overflow-y: hidden;

                li {
                    cursor: pointer;
                    color: $header_color;
                    text-decoration: none;
                    padding: 2px 10px 0px 10px;
                    font-size: 13px;
                    white-space: nowrap;
                    line-height: 2;
                    position: relative;

                .grid-tab-text {
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

                &:after {
                    content: "";
                    border-left: $itemBorder;
                    height: 20px;
                    display: block;
                    position: absolute;
                    margin: auto;
                    left: 0px;
                    top: 5px;
                    }

                    i {
                        margin-right: 5px;
                        font-size: 14px;
                    }
                    &.disabled {
                        color: rgba($header_color, 0.20);
                        cursor: default;
                        &:hover {
                            color: rgba($header_color, 0.20);
                            background: $header_li_bg;
                        }
                    }
                    &:hover {
                        color: $header_li_color_hover;
                        background: $header_li_bg_hover;
                    }
                    &.active {
                        background: $header_li_bg_active;
                        border-bottom: $itemBorder;
                        border-left: $itemBorder;
                        border-right: $itemBorder;
                        border-top: 1px solid $header_li_bg_active;
                        text-shadow: none;
                        height: 27px;
                        padding-top: 0px;

                        + li {
                            &:after {
                                display: none;
                             }
                        }

                        &:after {
                           display: none;
                        }
                    }
                    &.new,
                    &.more {
                        border-bottom: none;
                        padding-left: 15px;
                        padding-right: 10px;
                        position: relative;
                        span {
                            position: absolute;
                            top: 8px;
                            left: 120%;
                            width: 120px;
                            opacity: 0;
                            visibility: hidden;
                            transition: .2s;
                        }
                        &:hover {
                            background: $dark;
                            color: $lightest;
                            span {
                                opacity: 1;
                                visibility: visible;
                            }
                        }
                    }
                    &.more {
                        &:hover {
                            background: inherit;
                        }
                    }
                    .closeme {
                        display: none;
                    }
                    &.is-drilled-down {
                        color: $primary
                    }
                    &.can-be-closed {
                        //padding-right: 45px;
                        position: relative;
                        .closeme {
                            display: block;
                            position: absolute;
                            top: 0;
                            right: 0;
                            margin-right: 0;
                            width: 22px;
                            height: 22px;
                            line-height: 22px;
                            text-align: center;
                            transition: .2s;
                            opacity: .4;
                            font-size: 12px;
                            text-shadow: none;
                            &:before {
                                position: relative;
                                z-index: 2;
                            }
                            &:after {
                                content: '';
                                position: absolute;
                                width: 100%;
                                height: 100%;
                                top: 0;
                                left: 0;
                                background: $nav-color;
                                opacity: 0;
                                -webkit-transform-origin: 0 0;
                                transform-origin: 0 0;
                                -webkit-transform: scale(.1);
                                transform: scale(.1);
                            }
                            &:hover {
                                opacity: 1;
                                color: $white;
                                &:after {
                                    -webkit-transform: scale(1);
                                    transform: scale(1);
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
