﻿$filters-height: 295px;

.#{$gridWrapper} {

    .filters {
        background: $lightest;

        .circle-circumference {
            stroke: rgba(136, 136, 136, 0.7);
            fill: none;
        }

        .circle-filling {
            transition: .7s;
            stroke: $active;
            fill: none;
        }

        .toggle--on {

            .circle-filling {
                stroke-dasharray: 15.707963267948966, 15.707963267948966;
            }
        }
    }

    .filters__body__content-wrapper {
        position: relative;
        overflow-y: auto;
    }

    .filters__body__content {

        &:not(.filter-content--formula) {
            padding: 0 10px;
            display: flex;
            flex-wrap: wrap;
        }
    }

    .filters__footer {
        overflow: hidden;

        .tabs {
            box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.42);
            margin: 0 0 10px;
            height: 30px;
        }

        .tab {
            height: 30px;
            border-bottom: none;

            &:after,
            &:before {
                display: none;
            }
        }

        .axes {
            display: inline-block;
            margin-right: 10px;
        }

        .is-tab-selected {
            background: $lightest;
        }
    }

    .filters__menu {
        padding: 10px 15px 5px 15px;
        display: flex;
        justify-content: space-between;
        position: relative;

        .breadcrumbs {
            width: 100%;
            overflow: hidden;

            ul {
                margin: 0;
                height: 24px;

                li {
                    display: inline-block;
                    border: 1px solid $light;
                    background: lighten($lighter, 10%);
                    padding-left: 4px;
                    padding-right: 4px;
                }
            }
        }

        .dragable-arrow {
            position: absolute;
            top: 10px;
            width: 40px;
            height: 25px;
            outline: none;
        }

        .dragable-arrow--right {
            right: 0;
            @include gradient(right, $lightest, rgba($lightest, 0.0));

            i {
                right: -20px;
            }
        }

        .dragable-arrow--left {
            left: 0;
            @include gradient(left, $lightest, rgba($lightest, 0.0));

            i {
                left: -20px;
            }
        }
    }

    .filters__filter-box {
        margin: 5px;
        background: $white;
        min-width: 165px;
        height: 280px;

        .millercolumns--column {
            height: 280px;
            width: 165px;

            .actions {
                padding: 0 8px;
            }
        }

        .millercolumns--column .millercolumns--scroller .actions input {
            margin-left: 0;
        }
    }

    .filter-content--metrics,
    .filter-content--characteristic--without-relations {

        .column-header h6 {
            padding: 0;
            display: flex;
            justify-content: space-between;

            .button-wrapper .button {
                margin-right: 8px;
            }
        }
    }

    .filter-content--formula {
        border-top: solid 1px $lighter;

        .single-condition--error {

            .error {
                left: 15px;
                z-index: 20;
            }

            .error-list {
                padding-left: 24px;
            }
        }
    }
}