﻿.#{$gridWrapper} {

    .slider-wrapper {
        left: 10px;
        right: 10px;
        top: 0;
        height: 100%;
        position: absolute;

        &.slider-wrapper--disabled {
            pointer-events: none;
            opacity: 0.5;
        }

        .slider-helper-area {
            position:absolute;
            top:-80px;
            right:-80px;
            bottom:-80px;
            left:-80px;
            z-index:9999;
        }

        .slider-bar {
            position: absolute;
            top: 15px;
            left: 0;
            right: 0;
            height: 2px;
            background: $light;
            border-bottom: 1px solid $white;

            > div {
                display:none;

                position: absolute;
                top: 50%;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background: $light;
                transform: translate(-50%,-50%);
                box-shadow: 0 2px 0 white;
                transition: .2s;

                &.droppablepoint {
                    box-shadow: 0 0 0 6px rgba($light, 0.50);
                }
            }
        }

        .slider-handlers {
            position: absolute;
            top: 8px;
            height: 16px;
            @include gradient(right, $primary, darken($primary,10%));

            &.smoothsliding {
                transition:all .1s ease;
            }

            &.faded {
                @include gradient(right, $lighter, $light);
            }

            .slider-tooltips {
                position: absolute;
                font-size: 11px;
                text-transform: uppercase;
                bottom: 16px + 8px;
                right: -8px;
                min-width: calc(100% + 16px);
                white-space: nowrap;
                height: 20px;
                line-height: 20px;
                display: flex;
                opacity: 0;
                visibility: hidden;
                transform: translate(0, -4px);
                transition: .1s;
                cursor: default;

                &.visible {
                    opacity: 1;
                    visibility: visible;
                }

                span {
                    display: inline-block;
                    padding: 0 4px;
                    flex: 1;
                    text-align: left;

                    &:last-child {
                        text-align: right;
                    }

                    &:first-child, &:last-child {
                        background: rgba($lightest, 0.95);
                        color: $lightest;
                    }

                    &:nth-child(2) {
                        background: transparent;
                        flex-grow: 100;
                    }
                }
            }

            .slider-handle {
                position: absolute;
                top: 0;
                left: 0;
                width: 16px;
                height: 16px;
                border-radius: 50%;
                background: $lightest;
                border: 2px solid darken($primary,10%);
                box-shadow: 0 2px 4px rgba($black, 0.25);
                transform: translate(-50%, 0);

                &.ease {
                    transition: all .25s ease-out;
                }

                ~ .slider-handle {
                    left: auto;
                    right: 0;
                    transform: translate(50%, 0);
                    border: 2px solid $primary;
                }
            }
        }

        .slider-min, .slider-max {
            position: absolute;
            top: 16px;
            font-size: 11px;
            line-height: 12px;
            color: $tertiary;
            left: 0;
            transition: .1s;
        }

        .slider-max {
            left: auto;
            right: 0;
        }
    }
}
