﻿.#{$gridWrapper} {
    
    .form-element-wrapper {

        .ts-grid--datepicker-simple {
            display:inline-block;
            white-space:nowrap;
        
            &.popup{
                position:absolute;
                z-index: 1000;
                box-shadow: 0 11px 23px rgba(0, 0, 0, 0.3), 0 6px 6px rgba(0, 0, 0, 0.22), 0 0 5px 1px $dark;
            }

            input[type=text] {
                width:28px;
                text-align:center;
                padding: 0px 3px;
                margin: 0 2px;
                display:inline-block;

                &:first-of-type {
                    width:40px;
                }
            }

            label {
                font-weight: bold;
                padding:0 3px 0 7px;
                line-height:1;
            }

            &.settings {
                background: transparent;
                border:none;
            }

            &.inline{
                background: transparent;
                border:none;
                input {
                    background: rgba(0,0,0,0.1);
                    border:none;
                }
            }

            &.live-update{

                button {
                    display:none;
                }
            }
        }
    }
}
