﻿.#{$gridWrapper} {

    .ts-grid--edit-cell {
        height:32px;
        position:absolute;
        z-index: 5000;
        border-radius:2px;
        background:$input_bg;
        white-space:nowrap;
        box-shadow: 0 11px 23px rgba(0, 0, 0, 0.3), 0 6px 6px rgba(0, 0, 0, 0.22), 0 0 5px 1px $dark;

        & > div {
            height:100%;
        }

        .input-component {
            position: relative;
            height:100%;

            > div[data-bind] {
                height: 100%;
            }


        }

        .datepicker-advanced {
            padding: 0;
            border: none;
            width:100%;
            min-width: 70px;
            height: 100%;

            .input-group-header {
                height:100%;
            }

            .input-group {
                width: 100%;
                height:100%;
                display:block;

                input {
                    width: 100%;
                    padding: 10px 3px 10px 11px;
                    height: 100%;
                    min-height: 0;
                    border: none;
                    outline: none;
                    background: $datepicker_bg;
                    border-bottom: 1px solid darken($datepicker_bg,5%);
                    color: $black;
                }

                button {
                    display: none;
                }
            }

            .calendar {
                position: static;
            }
        }

        .form-element-wrapper {
            height: 100%;

            input[type=text], input[type=number] {
                height:100%;
                min-height: auto;
            }
        }

        .ts-grid-typeahead {
            height: 100%;

            input[type=text] {
                height: 100%;
                min-height: 100%;
            }
        }

        .ts-grid--dropdown {
            position:static;
            background: $input_bg;
            box-shadow: 0 11px 23px rgba(0, 0, 0, 0.3), 0 6px 6px rgba(0, 0, 0, 0.22), 0 0 5px 1px $dark;
            min-width:20px;

            .selected-value{
                display:none;
            }

            &.filterable  {
                .filter {
                    position: static;
                }

                ul {
                    top: 100%;
                }
            }

            input {
                padding: 6px 3px;
                height:32px;
                border:none;
                background:$input_bg;
                transition: .2s;
                outline:none;

                &:focus {
                    outline:none!important;
                    //box-shadow:none!important;
                }
            }

            button {
                height:32px;
                padding-left:38px;
                color:$white;
                text-shadow:0 1px 1px rgba($black, 0.30);
                border:none;
                padding-right:16px;
                background:$primary;
                position:relative;
                transition:.2s;

                display:none;

                &:before {
                    content:'';
                    position:absolute;
                    bottom:0;
                    left:0;
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 44px 0 0 32px;
                    border-color: transparent transparent transparent $white;
                }

                &:after {
                    content:'';
                    position:absolute;
                    top:0;
                    left:0;
                    bottom:0;
                    width:32px;
                    background: url(../images/small-separator.png) right bottom no-repeat;
                }

                &:hover {
                    background:darken($primary,5%);
                }
            }
        }

        .value-editor-number{
            z-index: 105;
            min-width: 50px;
            border: 1px solid $tertiary;
            box-shadow: 0 0 3px 1px rgba(0,0,0,.3);
            padding: 5px 10px;
            color: $black;
        }
    }

    .in-grid--section--grid-modal {

        &.fit-mode {

            .form-element-wrapper {
                .select-list li {
                    padding: 2px 8px 3px 8px;
                }
            }

            input.form-control.filter {
                height: 22px;
                min-height: 22px;
                padding: 2px 8px 3px 8px;
            }

            .value-editor-number {
                padding: 2px 8px;
                height: 22px;
                min-height: 22px;
            }
        }

    }
}
