﻿/// <reference path="../../grid-wrapper.scss" />

@function get-brightness($n) {
    @return round((red($n) * 299) + (green($n) * 587) + (blue($n) * 114) / 1000);
}

@function text-contrast($n) {
    $color-brightness: get-brightness($n);
    $threshold: get-brightness(#ffffff) / 2;

    @if ($color-brightness < $threshold) {
        @return white;
    }

    @else {
        @return black;
    }
  }

