﻿@mixin cell() {
    display: block;
    min-height: 30px;
    font-size:13px;
    padding: 0 6px;
    line-height:30px;
    position:relative;
    transition:.2s;
}

@mixin edit() {
    .pseudocell:not(.has-button) {
    cursor:pointer;
    box-shadow: inset 1px 1px 0px 0px $rowBorder;

        &:before {
            @include icon();
            content:'\f040';
            position:absolute;
            top:50%;
            left:5px;
            transform:translate(0, -50%);
            color:$light;
            opacity:0;
            transition:.2s;
        }

        &:hover:not(.disabled) {

            &:before {
                transform:translate(5px, -50%);
                opacity:1;
            }
        }
    }
}

.#{$gridWrapper} {

    .scales-modal-content {

        max-width:700px;
        width:auto;
        overflow: hidden;
        display:flex;
        flex-direction:column;
        padding: 10px 0 0;
        max-height: calc(100vh - 140px);

        hr {
            margin: 0 0 5px;
        }

        .ts-grid--scales {
            height: 100%;
            max-height: 100%;
            position: relative;
            display: flex;
            flex-direction: column;

            .ts-toggle-wrapper div {
                display: inline-block;
                margin-left: 10px;
                line-height: 32px;
            }
            .ts-toggle-wrapper {
                display: inline-block;
                margin-right: 10px;
            }

        }

        .ts-grid--scales-scrollr {
            flex-grow:1;
            overflow:auto;
            border-top: 1px solid $white;
            border-bottom: 1px solid $lighter;
            padding: 0 30px 10px;
            box-shadow: inset 0 -12px 16px -8px rgba(0, 0, 0, 0.12);
            /*box-shadow: inset 0 12px 16px -8px rgba(0, 0, 0, 0.08), inset 0 -12px 16px -8px rgba(0, 0, 0, 0.12);*/

            .scale-metadata {
                white-space:normal;
                margin-bottom: 5px;

                li {
                    white-space:normal;
                    //margin-bottom:5px;
                    font-size:12px;
                    display:inline-block;
                    width:auto;
                    margin-right:28px;

                    span {
                        opacity:.7;
                    }
                }
            }

            .matrix-view {
                @include group();

                button {
                    background: transparent;
                }

                .scale-dimension {
                    display: flex;

                    & > div:not(:first-child) {
                        margin-left: 12px;
                    }

                    label {
                        display: block;
                        font-size: 12px;
                        font-weight: normal;
                    }
                }

                .scale-dimension-name {
                    line-height: 29px;
                    margin-right: 5px;
                    font-weight: bold;
                    font-size: 10px;
                }

                .matrix-view__table {
                    margin-top: 10px;
                    overflow: auto;
                    min-height: 100px;
                    max-width: 620px;

                    .tabs {
                        background: none;
                        margin: 0;
                        height: auto;
                        padding-bottom: 2px;

                        .tab {
                            background:rgba($rowBorder, 0.60);
                            padding: 5px;

                            &:hover,
                            &.is-tab-selected:hover {
                                background:darken($rowBorder,10%);
                            }

                            &.is-tab-selected {
                                background: $rowBorder;
                                font-weight: bold;

                                &:focus {
                                    color: $black;
                                }
                            }

                            &:not(:last-child) {
                                margin-right: 5px;
                            }
                        }
                    }

                    input[type=number]::-webkit-inner-spin-button,
                    input[type=number]::-webkit-outer-spin-button {
                        -webkit-appearance: none;
                        -moz-appearance: none;
                        appearance: none;
                        margin: 0;
                    }

                    input[type=number] {
                        -moz-appearance:textfield;
                    }

                    .matrix-view__data {
                        display: flex;
                        position: relative;
                        margin-bottom: 10px;

                        .values {
                            box-shadow: inset 0px 0px 0px 1px $rowBorder;
                        }

                        .row {

                            .pseudocell {
                                width: 70px;
                                padding-left: 25px;

                                .pseudorange {
                                    overflow: hidden;
                                    text-overflow: ellipsis;
                                }
                            }
                        }
                    }

                    .column {
                        display: flex;
                        flex-direction: column;
                        width: 70px;

                        &.header {

                            label {
                                width: 70px;
                                margin-bottom: 0;
                            }
                        }
                    }

                    .legend {
                        font-size: 10px;
                        font-weight: bold;

                        &.y {
                            margin-left: 70px;
                        }
                        &.x {
                            position: absolute;
                            top: -15px;
                            text-overflow: ellipsis;
                            max-width: 70px;
                            overflow: hidden;
                        }
                    }


                    .row {
                        display: flex;
                        flex-direction: row;
                        margin: 0;

                        &.header {
                            margin-left: 70px;
                            position: relative;

                            & > div {
                                display: flex;
                            }
                        }

                        label {
                            margin-bottom: 0;
                        }
                    }

                    .header {

                        label {

                            input {
                                width: 100%;
                                border: none;
                                text-align: right;
                                font-weight: bold;
                                background: $rowBorder;
                                @include cell();
                            }
                        }
                    }
                }
            }
            @include edit();
            .pseudocell {
                @include cell();
                font-weight:normal;
                margin-bottom: -1px;

                strong {
                    font-weight:bold;
                }

                .pseudorange {
                    text-align:right;
                    display: block;
                }

                input {
                    position:absolute;
                    top:0;
                    left:0;
                    right:0;
                    bottom:100%;
                    width: 100%;
                    height: 100%;
                    z-index:3;
                    text-align: right;
                    padding-right: 4px;
                    transition:.1s;
                    opacity:0;
                }
                input:focus, input.always-on {
                    opacity:1;
                    bottom:0;
                    min-height: 100%;
                    height: 30px;
                }
            }
            .ts-grid--pseudotable {
                display:flex;

                .pseudocolumn {
                    flex:0 1 90px;
                    padding: 0;
                    box-shadow: inset 0px 0px 0px 1px $rowBorder;
                    margin-left: -1px;

                    .has-error {
                        position: absolute;
                        top: 15px;
                        z-index: 4;

                        ul li {
                            &:before {
                                display: none;
                            }
                            padding: 0 5px 0 10px;
                        }
                    }

                    .pseudotitle {
                        font-weight:bold;
                        white-space:nowrap;
                        overflow:hidden;
                        text-overflow:ellipsis;
                        text-align: center;

                        div:last-child {
                            line-height: 1;
                        }

                        small {
                            opacity:.6;
                            font-size: 65%;
                        }
                    }

                    > div:not(.pseudocell) {
                        padding:4px;
                        height:30px;

                        &.pseudotitle {
                            height:44px;
                            box-shadow: inset 1px 1px 0px 0px $rowBorder;
                            background: $rowBorder;
                        }
                    }



                    &:last-child {
                        flex-basis:28px;
                        flex-grow:0;
                        border-right:none;

                        .pseudocell {
                            background:transparent;
                            box-shadow: inset 1px 1px 0px 0px $rowBorder;

                            i {
                                position:absolute;
                                top:50%;
                                left:50%;
                                padding:4px;
                                transform:translate(-50%, -50%);
                                color:$light;
                                opacity:.8;
                                transition:.2s;
                                font-size:13px;
                                cursor:pointer;

                                &:hover {
                                    opacity:1;
                                    color: lighten($secondary,5%);
                                }

                                &.add-new-symbol {
                                    color:$dark;
                                    font-size:20px;
                                }
                            }
                        }
                    }

                    &:nth-last-child(2) {
                        flex-grow:0;
                        border:none;
                        text-align:right;

                        .pseudotitle {
                            cursor:default;
                        }

                        .pseudocell {
                            background:none;
                            font-weight:bold;

                            .pseudorange {
                                text-align:right;
                            }
                        }
                    }

                    &:not(:last-child) {
                        @include edit();
                    }

                    &:first-child {

                        .pseudotitle {
                            padding-left:0;
                        }
                    }
                }
            }
        }

        .ts-grid--scales-footer {
            flex-grow: 0;
            padding:10px 30px;
            border-top: 1px solid $white;
            //flex-basis:30px;

            .errors-inflow {
                margin-top: 0;
            }
        }

        .section-blurry {
            filter: blur(5px);
            opacity: .75;
            pointer-events: none;
        }
    }
}
