.#{$gridWrapper} {

    .vertical-scroll {
        position:absolute;
        top: $toolbarHeight;
        bottom:$toolbarHeight;
        right: 0px;
        width:15px;
        z-index:$verticalScrollbarZIndex;

        &.pinable-container {
            visibility: hidden;

            &.is-pinned {
                visibility: visible;
            }
        }

        &.with-statusbar {
            bottom: $toolbarHeight + $statusbarHeight;
        }

        &.with-gridSettings {
            right: $settingsSidebarWidth;
        }

        .scroll-bar-wrapper {
            display:flex;
            flex-direction: column;
            background-color: $scroll_bar_color;
            border: 1px solid $scroll_bar_border;
            height:100%;

            .arrow {
                max-height:15px;
                outline: none;
                border: 0;
                background-color: $scroll_bar_color;

                i {
                    position: relative;
                    top: -3px;
                    right: 3px;
                }
            }

            .scroll-bar {
                position: relative;
                flex: 1;

                .scroll-handle {
                    background-color: $scroll_handle_color;
                    position: relative;
                    height: 100%;

                    &:hover {
                        background-color: darken($scroll_handle_color, 8%);
                    }

                    .stretcher {
                        display: none;
                    }

                    &.is-dragging {
                        background-color: darken($scroll_handle_color, 20%);

                        .stretcher {
                            @extend .stretcher-base;
                        }
                    }
                }
            }
        }


    }
}
