﻿.#{$gridWrapper} {

    .in-grid-settings {
        background: $t_bg;
        position:absolute;
        top: $toolbarHeight;
        right:0;
        bottom:$toolbarHeight;
        width:$settingsSidebarWidth;
        z-index:$sideBarZIndex;
        opacity:0;
        visibility:hidden;

        &.with-statusbar {
            bottom:$statusbarHeight + $toolbarHeight;
        }

        &.active {
            opacity:1;
            visibility:visible;

            & ~ .in-grid--section--grid-modal {
                right:60px;
            }
        }

        &.disabled {
            .in-grid-settings--nav {
                li {
                    pointer-events: none;
                    opacity: 0.5;
                    cursor: default;
                }
            }
        }

        .in-grid-settings--nav {
            width: 100%;
            height: 100%;

            ul {
                height: 100%;
                padding-top:20px;
                display:flex;
                flex-direction:column;
            }
        }
    }
}
