﻿/// <reference path="../../grid-wrapper.scss" />
$cell-left-space: 10px;

* { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

@mixin selected-cell($opacity) {
    position: relative;
    transition: none;

    span:last-child {
        background-color: $primary;
        width: 100%;
        height: 150%; // not 100% because IE11 calculates height of this span differently from other browsers
        position: absolute;
        left: 0;
        top: 0;
        opacity: $opacity;
    }
}

.#{$gridWrapper} {

    .fit-mode {

        .row-indicator:not(.docked):not(.has-freeze-panes) .row-indicator__cell:first-child:before {
            top: -22px;
            height: $fitModeCellHeight;
            line-height: $fitModeCellHeight;
        }

        .ts-grid-decorated {

            .floating-header {

                & > div {
                    padding: 0 5px;
                    height: $fitModeCellHeight;
                    line-height: $fitModeCellHeight;
                }
            }

            .x, .y:not(.b) {
                height: $fitModeCellHeight;
            line-height: $fitModeCellHeight;;

                &:not(.row-indicator__cell) {
                    padding: 0;
                }

                &.fx, &.o {

                    &:before {
                        top: 4px;
                    }
                }
            }
            .b {
                padding: 0;
            }
        }

        td.n {
            padding: 0;
            height: $fitModeCellHeight;
            line-height: $fitModeCellHeight;

            &.gh0, &.gh1, &.gh2, &.gh3, &.gh4, &.gh5 {
                background-size: 16px;
            }

            &.i:not(.d) span:first-child + i:after {
                line-height: 16px;
                width: 10px;
                top: 4px;
            }
        }


    }

    table {
        table-layout: auto;
        border-spacing: 0;
        user-select: none;
        background: transparent;

        td:not(.a):not(.pa) span:last-child {
            display: none;
        }

        tr:first-child {

            &[data-tooltip] {

                &:after {
                    left: 50%;
                    -webkit-transform: translate(-50%, 4px);
                    transform: translate(-50%, 4px);
                }

                &:hover {

                    &:after {
                        -webkit-transform: translate(-50%, 20px);
                        transform: translate(-50%, 20px);
                    }
                }
            }
        }

        .rd {
            position: absolute;
        }
    }

    .ts-grid--cellhelper {
        position: absolute;
        top: 0;
        right: 100%;
        opacity: 0.01;
        z-index: -999;
    }

    .ts-grid-decorated {

        border-collapse: separate;

        .x {
            vertical-align: top;

            &.g {

                &.c, &.e {
                    span:first-child,
                    .floating-header__content {
                        padding-left: 27px;

                        &:after {
                            @include icon();
                            content: "\f147";
                            position: absolute;
                            left: $cell-left-space;
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                }

                &.c {
                    span:first-child,
                    .floating-header__content {
                        &:after {
                            content: "\f0fe";
                        }
                    }
                }
            }
        }

        .y {
            vertical-align: middle;

            &.g {

                &.c, &.e {
                    span:first-child {
                        padding-left: 27px;
                    }

                    &:after {
                        @include icon();
                        content: "\f147";
                        position: absolute;
                        left: 10px;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }

                &.c {
                    &:after {
                        content: "\f0fe";
                    }
                }
            }
        }

        @mixin groups {
            @for $i from 1 through 10 {
              $paddingLeft: 5px;
              $spaceWithoutExpandedIcon: 25px;
                .g.y.gi#{$i} { 
                    padding-left: $spaceWithoutExpandedIcon + $cell-left-space + $paddingLeft * $i;
                    &.e,
                    &.c {
                        padding-left: $cell-left-space + $paddingLeft * $i;
                        &:after {
                            left: $cell-left-space + $paddingLeft * $i;
                        }
                    }
                 }
            }
        }
        @include groups;  

        .x, .y {
            padding: 0;
            font-size: 13px;
            font-weight: 600;
            line-height: $cellHeight;
            height: $cellHeight;
            color: $black;
            text-align: left;
            position: relative;
            text-overflow: ellipsis;
            white-space: nowrap;
            background-color: $white;

            span:first-child {
                vertical-align: middle;
                padding: 0 10px;
                position: relative;
            }

            &.c, &.e {
                cursor: pointer;
            }

            &:not(.g):not(.b) {

                &.c, &.e {

                    span:first-child:before {
                        @include icon;
                        content: "\f147";
                        color: $cell__ce__c;
                        margin-right: 8px;
                        font-size: 14px;
                        width: 12px;
                        line-height: 1;
                    }
                }

                &.c {

                    span:first-child:before {
                        content: "\f0fe";
                    }
                }
            }

            &[data-tooltip] {

                &:after {
                    left: 50%;
                    transform: translate(-50%, 4px);
                }

                &:hover {

                    &:after {
                        transform: translate(-50%, 7px);
                    }
                }
            }

            &.fx, &.o {

                span:first-child:before,
                .floating-header__content:before {
                    content: "\e606";
                    font-family: 'icomoon';
                    speak: none;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                    font-size: 14px;
                    vertical-align: middle;
                    padding-right: 4px;
                    line-height: 13px;
                }
            }

            &.q {
                text-align: left;
            }
            &.tc {
                text-align: center;
            }
            &.tr {
                text-align: right;
            }

            &.o {

                span:first-child:before,
                .floating-header__content:before {
                    @include icon();
                    content: "\f08b";
                }
            }

            &.fx.o {
                span:first-child:before,
                .floating-header__content:before {
                    @include icon();
                    content: "\f08b\e606";
                }
            }
        }
        .b {
            border-top: 0;

            span {
                padding: 0;
            }
        }

        .n {
            text-align: right;
            font-size: 13px;
            line-height: $cellHeight;
            height: $cellHeight;
            background-color: $white;
            color: $black;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            cursor: cell;
            position: relative;
            background-clip: padding-box;

            &.me {
                background-color: $errorBg;
            }

            &.mw {
                background-color: $warningBg;
            }

            &.mi {
                background-color: $infoBg;
            }

            &.a {
                @include selected-cell(0.3);
            }

            &.w {
                max-width: 120px;
                min-width: 120px;
            }

            &.d {
                background-color: $cell__d__bg;
                color: $cell__d__c;

                &.me {
                    background-color: desaturate(darken($errorBg,5%),30%);
                }

                &.mw {
                    background-color: desaturate(darken($warningBg,5%),30%);
                }

                &.mi {
                    background-color: desaturate(darken($infoBg,5%),30%);
                }

                &.i, &.s0, &.s1 {
                    cursor: cell;
                }
            }

            &.pa {
                @include selected-cell(0.15);
            }

            &.p {
                background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQI12MQkuaXAgAA1gBXZDgSFwAAAABJRU5ErkJggg==);
                background-repeat: no-repeat;
                background-position: 0 0;
            }

            &.tl span {
                text-align: left;
            }

            &.u {
                text-decoration: underline;
            }

            @mixin color-map-mixin($i) {
                $color: nth($cm_colors, $i+1);
                $contrastColor: nth($cm_colors_contrasting, $i+1);

                &.gbar#{$i} {
                    overflow: hidden; // bottom: -1px; in ':before' below causes overflowing on Chrome

                    & > span {
                        padding-left: 8px;
                    }

                    &:before {
                        content: "";
                        position: absolute;
                        display: block;
                        top: 1px;
                        left: 1px;
                        width: 5px;
                        bottom: -1px; // -1px (and not 0px) because IE
                        background-color: $color;
                    }

                    &.te {
                        color: transparent;
                    }
                }

                &.d.gbg#{$i},
                &.gbg#{$i} {
                    background-color: $color;
                    color: $contrastColor;

                    &.te {
                        color: transparent;
                    }
                }

                &.gft#{$i} {
                    @if ($i > 0) {
                        color: $color;
                    }

                    &.te {
                        color: transparent;
                    }
                }
            }

            @include color-map-mixin(0);
            @include color-map-mixin(1);
            @include color-map-mixin(2);
            @include color-map-mixin(3);
            @include color-map-mixin(4);
            @include color-map-mixin(5);
            @include color-map-mixin(6);
            @include color-map-mixin(7);
            @include color-map-mixin(8);
            @include color-map-mixin(9);

            &.c:after {
                content: "";
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 8px 8px 0;
                border-color: transparent $mg__c5 transparent transparent;
                position: absolute;
                top: 1px;
                right: 0;
            }

            span {
                line-height: 22px;
                position: relative;
                display: block;
                padding: 0 5px;
                overflow: hidden;
                text-overflow: ellipsis;
                min-width: 20px;
                pointer-events: none;
            }

            &.l {

                &:not(.te) {
                    &.ga0, &.ga1, &.ga2, &.ga3, &.ga4, &.ga5,
                    &.gp0, &.gp1, &.gp2, &.gp3, &.gp4, &.gp5 {
                        >span:first-child {
                            padding-left: 32px;

                            &:before {
                                left: 20px;
                            }
                        }
                    }
                }

                >span:first-child {
                    padding-left: 20px;

                    &:before {
                        @include icon();
                        content: "\f023";
                        color: rgba(95, 95, 95, 0.35);
                        position: absolute;
                        left: 3px;
                        line-height: 25px;
                    }
                }

                &.i {

                    span {
                        padding-left: inherit;
                        padding-right: 35px;

                        &:before {
                            left: auto;
                            right: 16px;
                        }
                    }
                }

                &.dt {

                    span {
                        padding-right: 20px;
                    }
                }
            }

            &.i {
                span {
                    padding-right: 20px;
                    text-align: left;
                    min-width: 35px;
                }

                &:not(.d) span:first-child + i:after {
                    @include icon();
                    content: "\f107";
                    color: rgba(95, 95, 95, 0.65);
                    position: absolute;
                    right: 0;
                    line-height: 22px;
                    top: 7px;
                    width: 20px;
                    cursor: pointer;
                }
            }

            &.s {

                span {
                    text-align: left;
                }
            }

            &.s0, &.s1, &.sn {
                background-image: url(../images/cells/bt-0.png);
                background-repeat: no-repeat;
                background-position: center center;
                min-width: 84px;
                cursor: pointer;

                span {
                    //display:none;
                    color: transparent;
                }
            }

            &.s1 {
                background-image: url(../images/cells/bt-1.png);
            }

            &.sn {
                background-image: url(../images/cells/bs-n.png);
            }

            // TODO: #201880 fix colors

            &.ga0, &.ga1, &.ga2, &.ga3, &.ga4, &.ga5 { @include ga-base(); }
            &.ga0 { @include ga0-base(); }
            &.ga1 { @include ga1-base(); }
            &.ga2 { @include ga2-base(); }
            &.ga3 { @include ga3-base(); }
            &.ga4 { @include ga4-base(); }
            &.ga5 { @include ga5-base(); }

            &.gp0, &.gp1, &.gp2, &.gp3, &.gp4, &.gp5 { @include gp-base(); }
            &.gp0 { @include gp0-base(); }
            &.gp1 { @include gp1-base(); }
            &.gp2 { @include gp2-base(); }
            &.gp3 { @include gp3-base(); }
            &.gp4 { @include gp4-base(); }
            &.gp5 { @include gp5-base(); }

            &.gh0, &.gh1, &.gh2, &.gh3, &.gh4, &.gh5 { @include gh-base(); }
            &.gh1 { @include gh1-base(); }
            &.gh2 { @include gh2-base(); }
            &.gh3 { @include gh3-base(); }
            &.gh4 { @include gh4-base(); }
            &.gh5 { @include gh5-base(); }

            &.gb0, &.gb1, &.gb2, &.gb3, &.gb4, &.gb5 { @include gb-base(); }
            &.gb1 { @include gb1-base(); }
            &.gb2 { @include gb2-base(); }
            &.gb3 { @include gb3-base(); }
            &.gb4 { @include gb4-base(); }
            &.gb5 { @include gb5-base(); }

            &.new {
                cursor: pointer;
                font-weight: bold;

                &:hover {
                    background: $lightest;
                    color: $dark;
                }
            }

            &.te {
                >span:first-child {
                    color: transparent;
                }
            }
        }

        &.axis-y-flatnested {

            .y {
                padding-left: 20px;

                &.e, &.c {
                    padding-left: 0;
                }
            }
        }

        &.axis-x-flatnested {

            .x {
                &.w {
                    max-width: 120px;
                    min-width: 120px;

                    span {
                        max-width: 100%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        display: inline-block;
                    }
                }
            }
        }

        &.axis-y-flatordered {

            .y {
                padding-left: 20px;

                &.e, &.c {
                    padding-left: 0;
                }
            }
        }

        &.axis-x-flatordered {

            .x {
                &.w {
                    max-width: 120px;
                    min-width: 120px;

                    span {
                        max-width: 100%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        display: inline-block;
                    }
                }
            }
        }

        &.axis-x-nestedpivot {

            .x {

                &.w {
                    max-width: 120px;
                    min-width: 120px;

                    span {
                        max-width: 100%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        display: inline-block;
                    }
                }
            }
        }

        &.axis-x-classicpivot {

            .x {

                &.w {
                    max-width: 120px;
                    min-width: 120px;

                    span {
                        max-width: 100%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        display: inline-block;
                    }
                }
            }
        }

        &.axis-x-singlelevel {

            .x {

                &.w {
                    max-width: 120px;
                    min-width: 120px;

                    span {
                        max-width: 100%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        display: inline-block;
                    }
                }
            }
        }
    }

    .row-indicator {
        position: absolute;
        left: 23px;
        z-index: 3;

        &:not(.docked):not(.has-freeze-panes).overlap {
            z-index: 5;
        }

        &:not(.docked):not(.has-freeze-panes) {
            z-index: 4;

            .row-indicator__cell:first-child:before {
                content: "";
                position: absolute;
                width: 100%;
                height: $cellHeight - 2;
                background: $cell__d__bg;
                bottom: 32px;
                left: 0;
            }
        }
    }

    .ts-grid-decorated .row-indicator__cell {

        &.y {
            padding: 0;
            overflow: visible;
            display: table-cell;

            span {
                width: 28px;
                padding: 0;
                text-align: center;
                color: $tertiary;

                &.icon-fiber_new {
                    font-size: 20px;
                    color: $on;
                }
                &.icon-pencil {
                    color: $active;
                }
                &.icon-lock {
                    color: $disabledColor;
                }
            }
        }
    }

    &.shadows {

        .x, .y, .n {
            box-shadow: inset 1px 1px 0 $rowBorder;
        }

        .n {

            &.sl {
                box-shadow: inset 4px 0 3px -2px rgba(0,0,0,.2), inset 1px 1px 0 $rowBorder;
            }

            &.st {
                box-shadow: inset 0 4px 3px -2px rgba(0,0,0,.2), inset 1px 1px 0 $rowBorder;
            }

            &.sl.st {
                box-shadow: inset 4px 4px 3px -2px rgba(0,0,0,.2), inset 1px 1px 0 $rowBorder;
            }
        }

        .b {
            border-top: 0;
            box-shadow: inset 1px 0 0 $rowBorder;
        }
        
        .row-indicator {

            &:not(.docked):not(.has-freeze-panes) {
                box-shadow: 5px 0px 10px rgba(0, 0, 0, 0.19), 5px 0px 3px rgba(0, 0, 0, 0.23);
    
                .row-indicator__cell:first-child:before {
                    box-shadow: inset 1px 1px 0 $rowBorder;
                }
            }
        }
    }
}
