﻿.#{$gridWrapper} {

    .ts-grid--help, .ts-grid--shortcuts, .ts-grid--functions-helper {
        z-index:$sectionZIndex;
        position:absolute;
        top: 0;
        right:50px;
        bottom:0;
        width:480px;
        background:rgba($lightest, 0.95);
        overflow-x:hidden;
        overflow-y:auto;
        @extend .font-custom;
        opacity:0;
        visibility:hidden;
        -webkit-transform: translate3d(30px,0,0);
        transform: translate3d(30px,0,0);
        transition: .2s;
        border-right: 1px solid $white;
        padding:30px;
        text-shadow: 0 -1px 1px $white;
        outline: none;

        -webkit-user-select: text;
        -moz-user-select: text;
        -ms-user-select: text;
        user-select: text;

        @media (max-width: 1000px) {
            display:none;
        }

        &.active {
            opacity:1;
            visibility:visible;
            -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
        }

        hr {
            border-top:1px solid darken($lightest,14%);
            border-bottom:1px solid $white;
            margin-left:-30px;
            margin-right:-30px;
            margin-top:40px;
        }

        .operator-list {
            display:inline-block;
            margin-right:40px;
            code {
                position:relative;
            }
        }
    }

    section.ts-grid--help {
        padding:0;
        width:330px;

        i {
            position: absolute;
            top: -80px;
            right: -30px;
            font-size: 320px;
            z-index: -1;
            line-height: 1;
            color: $black;
            text-shadow: none;
            opacity: .04;
            transform: rotate(-10deg);
        }

        h3 {
            padding: 40px 30px 20px;
            opacity:.5;
        }

        p {
            padding-left: 30px;
            padding-right: 30px;
        }

        h4 {
            padding: 20px 30px;
            border-bottom:1px solid darken($lightest,14%);
            border-top:1px solid $white;
            margin:0;
            transition:.2s;
            cursor:pointer;

            small {
                font-size:13px;
            }

            &:hover {
                background:rgba($lighter, 0.25);
            }

            &.active {
                background:rgba($white, 0.50);
            }
        }

        hr {
            margin:0;

            + h4 {
                border-top:none;
            }
        }
    }

    section.ts-grid--shortcuts {
        right: 50px + 330px;
        
        h4 {
            margin-top: 30px;
        } 

        ul {
            margin-top:30px;

            li {
                text-shadow:none;
                position:relative;
                transition:.2s;
                cursor:pointer;
                border-bottom: 1px dashed darken($lightest,14%);

                &:hover, &.active {

                    span {

                        .description {
                            color:$black;
                        }
                    }
                }

                span {
                    line-height:36px;
                    font-size:12px;

                    .key {
                        background: $lightest;
                        border-top: 1px solid $white;
                        color: $light;
                        border-radius:3px;
                        padding: 4px 12px;
                        text-transform:uppercase;
                        box-shadow: 0 2px 0 1px rgba($black, 0.20);

                    }

                    .mouseclick {
                        background: $lightest;
                        border-top: 1px solid $white;
                        color: $light;
                        border-radius:3px;
                        padding: 4px 12px;
                        box-shadow: 0 2px 0 1px rgba($black, 0.20);
                    }

                    .plus {
                        margin-left:4px;
                        margin-right:4px;
                        font-size:14px;
                    }

                    .description {
                        margin-left:10px;
                        color:$light;
                        transition: .2s;

                        &:before {
                            content:'\2014';
                            margin-right:5px;
                            color:$light;
                        }
                    }
                }

                p {
                    font-size:11px;
                    display:none;
                }

                &.active {
                    padding-top:10px;
                    padding-bottom:10px;

                    p {
                        display:block;
                    }

                    &:before {
                        content:'';
                        position:absolute;
                        top:0;
                        left:-30px;
                        right:-30px;
                        bottom:0;
                        background:$white;
                        z-index:-1;
                    }
                }
            }
        }
    }

    section.ts-grid--functions-helper {
        right: 50px + 330px;
        width:560px;

        ul {
            margin-top:30px;

            &:first-of-type {
                    
                li {
                    cursor:default;

                    &:last-child {
                        border-bottom:none;
                    }
                }
            }

            li {
                margin-bottom:5px;
                text-shadow:none;
                position:relative;
                transition:.2s;
                cursor:pointer;
                border-bottom: 1px dashed darken($lightest,14%);

                &:hover, &.active {

                    span {

                        .description {
                            color:$black;
                        }
                    }
                }

                span {
                    line-height:36px;
                    font-size:12px;

                    .name {
                        background: $white;
                        color: $light;
                        border-radius:3px;
                        padding: 4px 12px;
                    }

                    .description {
                        margin-left:10px;
                        color:$light;
                        transition: .2s;

                        &:before {
                            content:'\2014';
                            margin-right:5px;
                            color:$light;
                        }
                    }
                }

                &.shortcut {

                    span {

                        .name {
                            border:1px solid darken($lightest,14%);

                            strong {
                                margin-left: 12px;
                                padding-left: 12px;
                                border-left: 1px solid darken($lightest,14%);
                            }
                        }
                    }

                    p {
                        font-size:12px;
                    }
                }

                p {
                    font-size:11px;
                    display:none;
                }

                &.active {
                    padding-top:10px;
                    padding-bottom:10px;

                    p {
                        display:block;
                    }

                    &:before {
                        content:'';
                        position:absolute;
                        top:0;
                        left:-30px;
                        right:-30px;
                        bottom:0;
                        background:$white;
                        z-index:-1;
                    }
                }
            }
        }
    }

    .ts-grid--help-overlay {
        position:absolute;
        z-index:9999;
        top:0;
        left:0;
        bottom:0;
        right:0;
        overflow:hidden;
        background:rgba($darken, 0.75);
        transition:.2s;
        opacity:0;
        visibility:hidden;

        &.active {
            opacity:1;
            visibility:visible;
            
            .marker {
                display:block;
            }
        }

        .marker {
            display:none;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            -webkit-animation: out 0.9s infinite ease-out;
            animation: out 0.9s infinite ease-out;
            background: $white;
            position: absolute;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            transition: .35s;
        }

        .backdrop {
            position:absolute;
            top:0;
            left:0;
            bottom:0;
            right:0;
        }

        .body {
            @extend .font-custom;
            position:absolute;
            max-width:640px;
            top:50%;
            left:50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            background:$white;
            @include boxshadow-md();
            padding: 40px 40px 120px;

            .iterator {
                background:$white;
                border:1px solid $nav-color;
                font-size:14px;
                width:36px;
                height:36px;
                line-height:36px;
                position:absolute;
                text-align:center;
                border-radius:50%;
                z-index: 999;
                bottom: 12px;
                left: 40%;
                transform: translate(-50%,0);
            }

            .helpclose {
                position:absolute;
                top:16px;
                right:16px;
                cursor:pointer;
                font-size:18px;
                line-height:18px;
                opacity:.5;
                transition: .2s;

                &:hover {
                    opacity:1;
                }
            }

            .buttons {
                
                button {
                    position:absolute;
                    bottom:0;
                    height:60px;
                    line-height:60px;
                    border:none;
                    font-weight:bold;
                    outline:none;
                    transition: .2s;
                }

                .prev {
                    width:40%;
                    left:0;
                    background:$white;
                    border-top:1px solid $lightest;
                }

                .next {
                    width:60%;
                    right:0;
                    background:$nav-color;
                    color:$white;

                    &:hover {
                        background:$primary;
                    }
                }
            }

            .screens {

                > div {
                    line-height:1.8;
                    display:none;

                    &.active {
                        display:block;
                    }

                    h4 {
                        margin-bottom:30px;
                        font-size:24px;
                    }
                }
            }
        }
    }
}
