﻿.#{$gridWrapper} {

    section.ts-grid--variables-manager--details {
        right:360px + 50px;
        width:440px;
        background:darken($lightest,4%);
        -webkit-transform: translate3d(40px,0,0);
        transform: translate3d(40px,0,0);

        .block-fixed:first-child {
            padding-bottom: 10px;
        }

        &.active {
            -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);

            + .ts-grid--variables-manager {
                width:360px;
            }
        }

        p {
            opacity:.8;
        }

        @include group();

        .input-group-addon {
            border:none;
            color:$tertiary;
            background:rgba(lighten($lightest, 0.20),30%);
        }

        h4.beforeless:before {
            display: none;
        }

        label {
            font-weight:normal;
        }

        $padding: 4px;
        .additional-opts {

            > li {
                position:relative;
                height: 34px + 2 * $padding;
                padding: $padding 0;
                border-bottom: 1px dashed lighten($lightest, 8%);

                .itembg {
                    position:absolute;
                    z-index:-1;
                    opacity:0;
                    transition: .2s;
                    top:0;
                    right:0;
                    left:0;
                    bottom:0;
                    background: rgba(darken($lighter, 0.6),20%);

                }

                &:last-child {
                    border-bottom: none;
                }

                input[type="checkbox"]:checked {

                    + label {
                        transform: translate(120px, 0);

                        &.date-time {
                            transform: translate(150px, 0);
                        }
                    }

                    ~.itembg {
                        opacity:1;
                    }
                }

                label {
                    position:absolute;
                    top:3px + $padding;
                    left:0;
                    transition: .2s;
                }

                div:not(.itembg) {
                    width:110px;
                }
            }
        }

        .datepicker-advanced{
            width:100%;

            .form-control {
                border-bottom: none;
            }

            .input-group {
                button {
                    border: none;
                    background: transparent;

                    &:last-child {
                        display:none;
                    }
                }
            }
        }
    }

    section.ts-grid--variables-manager {
        width:380px;
    }
}