﻿.font-custom {
    font-family: $fontname, sans-serif;
}

.mt10 {margin-top:10px;}
.mt20 {margin-top:20px;}
.mt80 {margin-top:80px;}

.pl20 {padding-left:20px!important;}

@mixin boxshadow-xs() {
    box-shadow: 0 1px 1.5px rgba(0, 0, 0, 0.12), 0 1px 1px rgba(0, 0, 0, 0.24);
}

@mixin boxshadow-sm() {
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.16), 0 3px 3px rgba(0, 0, 0, 0.23);
}

@mixin boxshadow-md() {
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.19), 0 6px 3px rgba(0, 0, 0, 0.23);
}

@mixin boxshadow-lg() {
    box-shadow: 0 7px 15px rgba(0, 0, 0, 0.1), 0 4px 3px rgba(0, 0, 0, 0.12);
}

@mixin boxshadow-xl() {
    box-shadow: 0 11px 23px rgba(0, 0, 0, 0.3), 0 6px 6px rgba(0, 0, 0, 0.22);
}

@mixin boxshadow-xl-inversed() {
    box-shadow: 0 -11px 23px rgba(0, 0, 0, 0.3), 0 6px 6px rgba(0, 0, 0, 0.22);
}

@mixin boxshadow-long($c: rgb(64, 127, 122)) {
    box-shadow: inset 0 1px 0.5px rgba(255, 255, 255, 0.5), inset 0 -1px 0.5px rgba(0, 0, 0, 0.2), 0 2px 2px rgba($c, 0.20), 0 4px 4px rgba($c, 0.20), 0 8px 8px rgba($c, 0.20), 0 16px 16px rgba($c, 0.20), 0 32px 32px rgba($c, 0.15);
}

@mixin gradient($direction: left, $start: $lightest, $end: $white) {
    background-image: -webkit-linear-gradient($direction, $start, $end);
    background-image: -moz-linear-gradient($direction, $start, $end);
    background-image: -ms-linear-gradient($direction, $start, $end);
    background-image: -o-linear-gradient($direction, $start, $end);
    //background-image: linear-gradient($direction, $start, $end);
}

@mixin button($icon:'\f105') {
    padding:8px 10px 8px 16px;
    margin-bottom:4px;
    background:transparent;
    border:1px solid rgba($buttonbg, 0.20);
    position:relative;
    color:$tertiary;
    overflow:hidden;
    border-radius:2px;
    transition:.2s;
    z-index:1;

    &:hover,
    &:focus {

        &:before {
            left:8px;
        }

        &:after {
            -webkit-transform: skew(20deg, 0deg) translate3d(20px,0,0);
            transform: skew(20deg, 0deg) translate3d(20px,0,0);
        }
    }

    &:after {
        position:absolute;
        top:0;
        left:-49px;
        bottom:0;
        width:50px;
        background: $primary;
        z-index:0;
        transition: all .2s cubic-bezier(0.65, -0.46, 0.17, 1.46);
        content:'';
        -webkit-transform: skew(0deg, 0deg);
        transform: skew(0deg, 0deg);
    }

    &:before {
        @include icon();
        content:$icon;
        color:$white;
        font-size: 12px;
        position:absolute;
        top:50%;
        -webkit-transform: translate3d(0,-50%,0);
        transform: translate3d(0,-50%,0);
        left:-23px;
        z-index:1;
        opacity:.6;
        transition: left .1s ease .2s;
    }

    &.active, &:active {
        color:$white;
        border-color:transparent;

        &:before {
            left:-23px;
        }

        &:after {
            width:100%;
            left:0;
            -webkit-transform: skew(0deg, 0deg);
            transform: skew(0deg, 0deg);
            background:$buttonbg_active;
        }

        &:focus {
            background: $primary;

            &:after {
                background: lighten($buttonbg_active, 1%);
            }
        }
    }

    &:focus {
        outline:none;
    }

    span {
        position:relative;
        z-index:2;
        transition: all .2s ease;
    }

    &[disabled] {
        background:rgba($buttonbg_disabled, 0.5);
        border:1px solid rgba($buttonbg_disabled, 0.10);
        color:$button_color_disabled;
        &:hover {

            &:before {
                left:8px;
            }

            &:after {
                -webkit-transform: skew(0deg, 0deg) translate3d(0px,0,0);
                transform: skew(0deg, 0deg) translate3d(4px,0,0);
            }
        }

        &:after {
            background: $buttonbg_disabled_dec;
        }

        &:before {
            content:'\f00d';
        }

        &:active {
            border-color:transparent;

            &:before {
                left:-23px;
            }

            &:after {
                width:unset;
                left:unset;
                -webkit-transform: skew(0deg, 0deg) translate3d(6px,0,0);
                transform: skew(0deg, 0deg) translate3d(6px,0,0);
                background:inherit;
            }
        }
    }
}

@mixin boxedbutton() {
    > i {
        display:none;

        position:absolute;
        left:4px;
        top:50%;
        -webkit-transform:translate(0,-50%) rotate(90deg);
        transform:translate(0,-50%) rotate(90deg);
    }

    button {
        position:relative;
        -webkit-transition: color 0.3s;
        transition: color 0.3s;
        -webkit-transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
        transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
        display: inline-block;
        padding: 6px 16px;
        border: none;
        background: none;
        color: $button2_color;
        vertical-align: middle;
        position: relative;
        z-index: 1;
        min-width: 80px;

        &:not([disabled]):hover,
        &:not([disabled]):focus {
            color:$button2_color_hover;

            &:before {
                opacity: 1;
                -webkit-transform: scale3d(1, 1, 1);
                transform: scale3d(1, 1, 1);
            }

            &:after {
                opacity: 0;
	            -webkit-transform: scale3d(0.8, 0.8, 1);
	            transform: scale3d(0.8, 0.8, 1);
            }

            span {
                color:$button2_spancolor_hover;
            }
        }

        &:before, &:after {
            content: '';
	        z-index: -1;
	        border-radius: inherit;
	        pointer-events: none;
	        position: absolute;
	        top: 0;
	        left: 0;
	        width: 100%;
	        height: 100%;
	        -webkit-backface-visibility: hidden;
	        -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
	        transition: transform 0.3s, opacity 0.3s;
	        -webkit-transition-timing-function: cubic-bezier(0.65, -0.92, 0.4, 1.82);
	        transition-timing-function: cubic-bezier(0.65, -0.92, 0.4, 1.82);
        }

        &:before {
            border: 2px solid $button2_border;
	        opacity: 0;
	        -webkit-transform: scale3d(1.2, 1.2, 1);
	        transform: scale3d(1.2, 1.2, 1);
        }

        &:after {
            background: $button2_bg;
        }

        span:not(.text) {
            position:absolute;
            top:50%;
            left:0;
            font-size:12px;
            font-family:'Times New Roman', Times, serif;
            font-style:italic;
            z-index:2;
            background:$button2_span_bg;
            color:$button2_span_color;
            display:block;
            width:18px;
            height:18px;
            line-height:18px;
            text-align:center;
            border-radius:50%;
            -webkit-transform: translate3d(-60%, -50%, 0);
            transform: translate3d(-60%, -50%, 0);
        }

        &.alternative {

            &:not([disabled]):hover {
                color:$button2_color_alt;
            }

            &:before {
                border: 2px solid $button2_color_alt;
            }
        }

        &.sadness {

            &:not([disabled]):hover {
                color:$button2_color_alt_2;
            }

            &:before {
                border: 2px solid $button2_color_alt_2;
            }
        }

        &[disabled] {
            opacity:.5;

            &:hover {

                //color:$button2_color_disabled;

                &:before {
                    opacity: 0;
                }

                &:after {
                    opacity: 1;
	                -webkit-transform: inherit;
	                transform: inherit;
                }
            }
        }
    }
}

@mixin actions() {
    display: flex;
    justify-content: flex-end;
    margin-top: auto;

    .pagin {
        height: 24px;
        line-height: 24px;
        display: flex;

        button:disabled {
            opacity: 0.4;
        }
    }
    .form-control {
        display: inline-block;
        background-color: transparent;
        box-shadow: none;
        border-radius: 0;
        font-size: 13px;
        border-color: $lighter;
        margin-bottom: 5px;
        outline: none !important;
        width: calc(100% - 10px);
        height: auto;
        padding: 2px 5px;
        margin-left: 5px;

        &:focus {
            outline:none;
            border-color:$active;
            box-shadow: inset 0 1px 1px rgba(0,0,0,0.075), 0px 2px 3px rgba(0, 0, 0, 0.175);
        }
    }
}

@mixin toolbarbutton() {

    &.small {
        line-height: 13px;
        height: 25px;
    }
    border:none;
    position:relative;
    color:$buttontoolbar_color;
    padding:6px 16px;
    z-index: 1;


    &:not([disabled]):hover,
    &:not([disabled]):focus {
        color:$buttontoolbar_hover_color;

        &:before {
            opacity: 1;
            -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
        }

        &:after {
            opacity: 0;
	        -webkit-transform: scale3d(0.8, 0.8, 1);
	        transform: scale3d(0.8, 0.8, 1);
        }

        span {
            color:$buttontoolbar_bg;
        }
    }

    &:before, &:after {
        content: '';
	    z-index: -1;
	    border-radius: inherit;
	    pointer-events: none;
	    position: absolute;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;
	    -webkit-backface-visibility: hidden;
	    -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
	    transition: transform 0.3s, opacity 0.3s;
	    -webkit-transition-timing-function: cubic-bezier(0.65, -0.92, 0.4, 1.82);
	    transition-timing-function: cubic-bezier(0.65, -0.92, 0.4, 1.82);
    }

    &:before {
        border: 2px solid $buttontoolbar_hover;
	    opacity: 0;
	    -webkit-transform: scale3d(1.2, 1.2, 1);
	    transform: scale3d(1.2, 1.2, 1);
    }

    &:after {
        background: $buttontoolbar_bg;
    }


    &:disabled {
        opacity: 0.4;
    }

    span {
        position:relative;
        z-index:2;
    }

    &.button-small {
        height:24px;
        padding:4px 10px;
        line-height: 14px;
    }
}

@mixin group() {
    .btn-group {
        padding:3px;
        border:1px solid $btngroup_bg;

        [data-tooltip] {
            position: relative;
            z-index: 4;

            &.active {
                z-index: 3;
            }
        }

        button:disabled {
            opacity: 1;
        }

        .btn-primary {
            color:$btngroup_color;
            background-color:transparent;
            border-color:transparent;
            border-right-color: $btngroup_bg;
            padding: 5px 13px;
            border-radius:0;
            transition:.2s;

            &:last-child {
                border-right-color: transparent;
            }

            &:disabled {
                color:rgba($btngroup_color, 0.45);
            }

            &.active {
                background-color: $btngroup_bg_active;
                border-color: $btngroup_bg_active;
                box-shadow:none;
                color:$white;

                &:focus {
                    //color: $light;
                }

                &:disabled {
                    background:rgba($btngroup_bg_active, 0.45);
                    border-color:rgba($btngroup_bg_active, 0.45);
                }
            }

            &:not([disabled]):focus {
                outline:none;
                border-color: $btngroup_bg_active;
            }

            &:not([disabled]):hover {
                border-color: $btngroup_bg_active;
            }

            &:after {
                color:$btngroup_color;
                background:$btngroup_bg_after;
                @include boxshadow-md()
            }
        }

        > span {
            position: relative;
            float: left;
            padding: 3px 28px 3px 13px;
            color:$tertiary;
        }
    }
}

@mixin noselect() {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@mixin warning-icon() {
    font-family: "icomoon";
    content: '\f06a';
    color: darken($warningBg, 30%);
}

@mixin error-icon() {
    font-family: "icomoon";
    content: '\f071';
    color: darken($errorBg, 30%);
}

@mixin success-icon() {
    font-family: "icomoon";
    content: '\f058';
    color: darken($successBg, 5%);
}

@mixin info-icon() {
    font-family: "icomoon";
    content: '\f05a';
    color: darken($infoBg, 30%);
}






@mixin severity-filter-buttons() {
    padding:4px 8px;
    border:none;
    background:transparent;
    transition:.2s;
    border-right:1px solid rgba($light, 0.40);

    &:first-child {
        border-radius:2px 0 0 2px;
    }

    &:last-child {
        border-radius: 0 2px 2px 0;
        border-right:1px solid transparent;
    }

    &:before {
        margin-right:6px;
    }

    &.error:before {
        @include error-icon();

    }

    &.warning:before {
        @include warning-icon();
    }

    &.info:before {
        @include info-icon();

    }

    &.success:before {
        @include success-icon();
    }

    &.selected{
        background: $white;
        @include boxshadow-sm();
        border-right:1px solid transparent,
    }
}

@mixin type-abbreviation() {
    $badgeSize: 17px;

    background: $lighter;
    border-radius: 3px;
    color: $white;
    cursor: default;
    display: inline-block;
    @extend .font-custom;
    font-size: 10px;
    height: $badgeSize;
    line-height: $badgeSize;
    text-align: center;
    width: $badgeSize;

}


