/* === Titillium Web - regular */
@font-face {
	font-family: 'Titillium Web';
	font-style: normal;
	font-weight: 400;
	src: url("./../fonts/gfonts/TitilliumWeb-Regular.eot");
	src: local("Titillium Web Regular"),
		local("TitilliumWeb-Regular"),
		url("./../fonts/gfonts/TitilliumWeb-Regular.eot") format("embedded-opentype"),
		url("./../fonts/gfonts/TitilliumWeb-Regular.woff2") format("woff2"),
		url("./../fonts/gfonts/TitilliumWeb-Regular.woff") format("woff"),
		url("./../fonts/gfonts/TitilliumWeb-Regular.ttf") format("truetype"),
		url("./../fonts/gfonts/TitilliumWeb-Regular.svg") format("svg");
}

