﻿.#{$gridWrapper} {

    .ts-grid--advanced-metrics {
        display: flex;
        flex-direction: column;

        .form-element-wrapper {
            margin-bottom: 5px;
        }

        ul li {
            .icon-fx:before {
                position: relative;
                top: 2px;
            }
            &.hasParent{
                padding-left:23px;
            }
        }
    }

    .ts-grid--advanced-metrics--details {
        right: $metrics-width + 50px - 30;
        width: 550px;
        background: darken($lightest,4%);
        -webkit-transform: translate3d(40px,0,0);
        transform: translate3d(40px,0,0);

        &.active {
            -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);

            + .ts-grid--advanced-metrics, + .ts-grid--fields-management {
                width: $metrics-width - 30;
            }
        }

        .d-inline {
            display: inline-block;
        }

        @include group();

        h4 {

            div, span {
                margin-left: 20px;
            }

            &.beforeless {

                &:before {
                    display: none;
                }
            }
        }

        .tabs .tab {
            padding-left: 20px;
            padding-right: 20px;
        }

        .datepicker-advanced {
            padding: 0;
        }

        .formula-section {
            .has-error.errors-inflow {
                margin-top: 0;
            }

            .button-wrapper {
                padding-bottom: 5px;
            }

            .fixed-formula-wrapper {
                display: flex;
                justify-content: space-between;
                font-size: 11px;

                & > div:first-child {
                    padding-right: 5px;
                }
            }
        }
    }

    .graphic-template {
        position: relative;

        @mixin gft-gbar-gbg($i) {
            .gft#{$i}, .gbar#{$i}, .gbg#{$i} {
                background: nth($cm_colors, $i+1);
            }
        }

        @include gft-gbar-gbg(0);
        @include gft-gbar-gbg(1);
        @include gft-gbar-gbg(2);
        @include gft-gbar-gbg(3);
        @include gft-gbar-gbg(4);
        @include gft-gbar-gbg(5);
        @include gft-gbar-gbg(6);
        @include gft-gbar-gbg(7);
        @include gft-gbar-gbg(8);
        @include gft-gbar-gbg(9);

        .ga0, .ga1, .ga2, .ga3, .ga4, .ga5 {
            @include ga-base();
        }

        .ga0 {
            @include ga0-base();
        }

        .ga1 {
            @include ga1-base();
        }

        .ga2 {
            @include ga2-base();
        }

        .ga3 {
            @include ga3-base();
        }

        .ga4 {
            @include ga4-base();
        }

        .ga5 {
            @include ga5-base();
        }

        .gp0, .gp1, .gp2, .gp3, .gp4, .gp5 {
            @include gp-base();
        }

        .gp0 {
            @include gp0-base();
        }

        .gp1 {
            @include gp1-base();
        }

        .gp2 {
            @include gp2-base();
        }

        .gp3 {
            @include gp3-base();
        }

        .gp4 {
            @include gp4-base();
        }

        .gp5 {
            @include gp5-base();
        }

        .gh0, .gh1, .gh2, .gh3, .gh4, .gh5 {
            @include gh-base();
        }

        .gh1 {
            @include gh1-base();
        }

        .gh2 {
            @include gh2-base();
        }

        .gh3 {
            @include gh3-base();
        }

        .gh4 {
            @include gh4-base();
        }

        .gh5 {
            @include gh5-base();
        }

        .gb0, .gb1, .gb2, .gb3, .gb4, .gb5 {
            @include gb-base();
        }

        .gb1 {
            @include gb1-base();
        }

        .gb2 {
            @include gb2-base();
        }

        .gb3 {
            @include gb3-base();
        }

        .gb4 {
            @include gb4-base();
        }

        .gb5 {
            @include gb5-base();
        }

        .graphic-template-picker {
            position: absolute;
            z-index: 2;
            @include boxshadow-md();
            top: 0;
            left: 0;
            right: 0;
            background: rgba($lightest, 0.85);
            height: 35px;
            opacity: 0;
            visibility: hidden;
            transition: .25s;
            display: flex;
            flex-direction: row;
            padding: 5px;

            div {
                display: flex;
                flex-direction: column;
                flex: 1 0;
                flex-shrink: 1;
                cursor: pointer;
                height: 100%;
            }

            &.active {
                opacity: 1;
                visibility: visible;
                top: -80px;
            }
        }
    }

    .graphic-template {
        display: flex;
        border: 6px solid rgba($nav-color, 0.2);
        margin: 60px auto 0 auto;
        width: 90%;
        transition: all 0.2s ease-in;

        &.graphic-template--picker-activated {
            margin-top: 90px;
        }
    }

    .graphic-template__range {
        width: 100%;
        position: relative;

        &:not(:last-child) {
            border-right:  2px solid rgba($nav-color, 0.2);
        }

        .range-value-wrapper--from,
        .range-value-wrapper--to {
            position: absolute;
            top: -35px;
        }

        .range-value-wrapper--from {
            left: 0;
            transform: translate(-50%, 0);
        }

        .range-value-wrapper--to {
            right: 0;
            transform: translate(50%, 0);
        }

        .range-value-wrapper {

            input {
                top: -12px;
                left: -23px;
            }

            &:after {
                top: 100%;
                left: 50%;
                -webkit-transform: translate(-50%,0);
                transform: translate(-50%,0);
                border-width: 7px 7px 0 7px;
                border-color: $nav-color transparent transparent transparent;
            }
        }
    }

    .text-template {
        width: 70%;
        margin: 30px auto 0 auto;
        display: flex;
        flex-direction: column;

        .range-value-wrapper--from,
        .range-value-wrapper--to {
            position: absolute;
            right: 100%;
            width: 100px;
            min-height: 22px;
        }

        .range-value-wrapper--from {
            top: -10px;
        }

        .range-value-wrapper--to {
            bottom: -6px;
        }

        .range-value-wrapper {

            input {
                top: -6px;
                left: -1px;
            }

            &:after {
                top: 5px;
                left: 100%;
                border-width: 7px 0 7px 7px;
                border-color: transparent transparent transparent $nav-color;
            }
        }

        .button--range-selector {
            text-align: right;
            padding-right: 2px;
        }
    }

    .text-template__range {
        padding: 0 0 2px 10px;
        position: relative;
    }

    .range-value-wrapper {

        input {
            position: absolute;
            z-index: 1;
            min-width: 60px;
        }

        &:after {
            position: absolute;
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
        }
    }


}
