﻿.#{$gridWrapper} {
    .ts-toggle-wrapper {

        &.disabled {
            position: relative;

            .ts-toggle {
                opacity: 0.4;
            }

            &:before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                z-index: 1;
            }
        }
    }

    .ts-toggle {

        .ts-toggle__on-label,
        .ts-toggle__off-label {
            cursor: pointer;
        }

        &.toggle--on {

            .ts-toggle__icon {
                color: $active;
            }

            .ts-toggle__icon:before {
                content: "\f205";
            }

            .ts-toggle__on-label {
                text-shadow: 0px 0px 8px $active;
                color: $active;
            }
        }

        &.toggle--off {

            .ts-toggle__icon:before {
                content: "\f204";
            }

            .ts-toggle__off-label {
                text-shadow: 0px 0px 8px $active;
                color: $active;
            }
        }

        &.toggle--disabled {
            opacity: 0.5;
            pointer-events: none;
        }

        .ts-toggle__icon {
            @include icon();
            font-size: 22px;
            position: relative;
            top: 2px;
            background: none;
            border: none;
            outline: none;
        }

    }
}
