﻿$errorsize: 20px;

.#{$gridWrapper} {

    .has-error {
        position:relative;

        td .error {
            left: 0;
        }

        &.errors-expanded {

            .error-list {
                opacity:.88;
                visibility:visible;
                -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
            }
        }

        &.errors-inflow {
            margin-top: 20px;

            .error-list {
                box-shadow: none;
            }

            &.errors-expanded {

                .error-list {
                    position:static;
                }
            }
        }

        .error {
            color:$white;
            background:$error;
            width:$errorsize;
            height:$errorsize;
            line-height:$errorsize + 1;
            text-align:center;
            border-radius:2px;
            cursor:pointer;
            position:absolute;
            top: calc(50% - #{$errorsize} / 2);
            left: -$errorsize;
            z-index:2;
            font-size:11px;
            animation:blinkErrorShadow 1s ease infinite;

            &:after {
                content:'';
                position:absolute;
                top:50%;
                left:100%;
                transform: translate(0,-50%);
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 5px 0 5px 5px;
                border-color: transparent transparent transparent $error;
            }

            &:hover + .error-list {
                opacity:.95;
                visibility:visible;
                -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
            }

            &.datetime {
                left: -14px;
            }
        }

        .warning {
            color:$white;
            background:$warning;
            animation:blinkWarningShadow 1s ease infinite;
            &:after {
                border-color: transparent transparent transparent $warning;
            }
        }

        &.error-top .error {
            top: 11px;
            left: 3px;
            z-index: 1000;
        }

        .error-list {
            display:block; //fix for .select ul display:none
            position:absolute;
            top:auto;
            left:0;
            right:0;
            font-size:12px;
            padding: 0 0 0 10px;
            background: $error;
            z-index:999;
            list-style:none;
            @include boxshadow-xl();
            opacity:0;
            visibility:hidden;
            transition:.2s;
            -webkit-transform: translate(0, -5px);
            transform: translate(0, -5px);
            word-wrap: break-word;
            color:$white;
            white-space: normal;

            li {
                padding:5px 5px 5px 10px;
                position:relative;

                &:before {
                    content:'';
                    position:absolute;
                    width:5px;
                    height:5px;
                    top:10px;
                    left:-2px;
                    background:rgba($white, 0.40);
                    border-radius:50%;
                }
            }
        }
                
        .warning-list {
            background: $warning;
        }
    }

    section .buttons-after-hr.has-error {

        button:hover + span {
            transform:translate(-30px, 0);
        }

        .error-list {
            z-index: 2;
        }

        .error {
            position:absolute;
            right:32px + 32px;
            bottom:3px;
            top:auto;
            left:auto;
            z-index:3;
        }
    }

    .input-component .has-error {

        .error {
            left: -$errorsize - 1;
        }

        .error-list {
            white-space: normal;
            top: 100%;
        }
    }

    .ts-grid--procedures-details {

        .col-error .error {
            left: 0;
            z-index: 1000;
        }

        ul.error-list {
            padding: 7px 5px 7px 24px;

            li {
                margin-bottom: 0;

                &:before {
                    display: none;

                }
            }
        }
    }

    .import-modal-content .has-error {

        &.warning i {
            left: -25px;
            top: calc(50% - 13px);
        }

        .error-list {
            margin:10px 0;
        }
    }

    section.ts-grid--layout-dictionaries--details {

        .flexinput.has-error .error-list {
            top: auto;
        }

        .conditions .has-error .single-condition {
            padding-left: 30px;
        }

        .has-error.errors-inflow {
            margin-top: 20px;
        }
    }

    section.ts-grid--variables-manager--details .additional-opts {
        > li {
            .has-error .error+.error-list {
                min-width: 363px;
            }
        }
    }

    span.has-error {
        display: block;
    }
}