.#{$gridWrapper} {
    .in-grid--section--grid-modal {
        display: block;
        position: absolute;
        top: $toolbarHeight;
        right: 10px;
        left: 0;
        bottom: $toolbarHeight;
        margin-right: -25px;
        background: $gridBg;

        &.with-statusbar {
            bottom: $statusbarHeight + $toolbarHeight - $scroll-bar-height;
            margin-bottom: -$scroll-bar-height;
        }

        &.with-settings {
            right: 55px;
            z-index: 0;
        }

        &.with-no-rows {
            .floating-header {
                display: none;
            }
        }

        &.with-splash-screen {
            background: rgba(255, 255, 255, 0.7);

            div.splash-screen-container {
                padding: 0 100px;
                position: absolute;
                right: 0;
                top: $toolbarHeight + 70px;
                width: 100%;

                .splash-screen {
                    max-width: 600px;
                    min-width: 300px;
                    text-align: center;
                    padding: 20px;
                    background: white;
                    box-shadow: 0 0 10px rgba(0, 0, 0, 0.19), 0 3px 3px rgba(0, 0, 0, 0.23);
                    margin: 0 auto;

                    h1 {
                        margin: 0 0 30px;
                    }

                    button[disabled] {
                        opacity: 0.5;
                    }
                }
            }
        }

        &.with-no-rows .in-grid--section--grid-modal--grid-wrapper {
            width: 100%;
        }

        div.in-grid--section--grid-modal--grid-wrapper {
            display: table;

            table {
                margin-right: 20px;
                margin-bottom: 20px;
            }
        }

        @media (max-width: 1000px) {
            right: 0;

            &.with-settings {
                right: 0;
            }
        }

        .in-grid-modal {
            top: 10px;
            left: 10px;
            background: transparent;
        }

        .in-grid-modal__content {
            background: $gridBg;
            height: calc(100% - #{$modal-title-height}px);
            width: 100%;
            padding: 20px;
            overflow: auto;
            position: relative;
        }

        &.flat {
            .in-grid-modal__content {
                padding-left: 50px;
            }
        }

        &:not(.is-pinned) {
            z-index: 1;
            right: auto;
            bottom: auto;

            &.is-disabled {
                display: none;
            }
        }

        &:not(.active) {
            .in-grid-modal__title {
                background: desaturate(lighten($primary, 20%), 100%);
            }
        }

        &.is-pinned {

            .in-grid-modal {
                position: static;
                box-shadow: none;
                height: 100%;
                width: 100%;
            }

            .in-grid-modal__content {
                height: 100%;
                width: 100%;
            }
        }
    }
}
