﻿@mixin toolbar-tooltip() {

    &[data-tooltip]:after {
        top: 100%;
        bottom: auto;
        left: 0;
        right: auto;
        height: $toolbarHeight;
        line-height: $toolbarHeight;
        z-index: 2;
        position: absolute;
        padding: 0 10px;
    }

    &.tooltip-hide {

        &[data-tooltip]:after {
            display: none;
        }
    }
}

.#{$gridWrapper} {

    igrid-toolbar-section {
        display: inline-block;
    }

    .grid-toolbar {
        background: $t_bg;
        height: $toolbarHeight;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        //@include boxshadow-xl();
        z-index: $horizontalToolbarZIndex;
        white-space: nowrap;
        cursor: default;
        padding: 0 25px 0 15px;
        overflow: hidden;
        border-bottom: $defaultCellBorder;

        div:focus {
            outline: none;
        }

        @media (max-width: 1000px) {
            right: 0;
        }

        .static {
            float: left;
            //display: inline-block;
            position: relative;
        }

        .trigger {

            @media (max-width: 1000px) {
                display: none;
            }

            height: $toolbarHeight;
            line-height: $toolbarHeight;
            display: inline-block;
            width: 50px;
            position: absolute;
            top: 0;
            right: 0;
            @include gradient(right, $primary, $primary);
            color: $white;
            text-align: center;
            z-index: 2;

            &:hover {
                @include gradient(right, darken($primary,10%), $primary);
            }
        }

        .section {
            border-right: 1px solid $rowBorder;

            @media (max-width: 1200px) {
                position: static;
                float: none;
                display: inline-block;
            }
        }
        button.dragable-arrow {
            height: 30px;
            top: 0;
            background: $white;

            &.dragable-arrow--left {
                box-shadow: 7px 0px 10px -4px rgba($light, 0.85);;
            }
            &.dragable-arrow--right {
                box-shadow: -7px 0px 10px -4px rgba($light, 0.85);;
            }
        }
    }

    .grid-toolbar--sections-left,
    .grid-toolbar--sections-right {
        display: table;
    }

    .grid-toolbar__dragscroll-wrapper {
        height: calc(100% + 30px);
        overflow-x: scroll;
        overflow-y: hidden;
        background: $white;
        display: flex;
        justify-content: space-between;
    }

    .in-grid-modal {
        position: absolute;
        top: 100%;
        left: -14px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.19), 0 3px 3px rgba(0, 0, 0, 0.23);
        background: $white;
        min-width: 280px;
        z-index: 100;
        transition: opacity .15s ease;
        overflow: hidden;

        .in-grid-modal__list {
            padding: 10px;
        }

        .maxh-200,
        .maxh-400,
        .maxh-600 {
            margin-bottom: 0;
        }

        .maxh-200 {
            max-height: 200px;
            overflow-x: hidden;
            overflow-y: auto;
        }

        .maxh-400 {
            max-height: 400px;
            overflow-x: hidden;
            overflow-y: auto;
        }

        .maxh-600 {
            max-height: 600px;
            overflow-x: hidden;
            overflow-y: auto;
        }

        .in-grid-modal__content {
            height: calc(100% - #{$modal-title-height});
            display: flex;

            .in-grid-modal__content__wrapper {
                display: flex;
                width: 100%;

                & > div {
                    width: 100%;
                }
            }
        }

        &.in-grid-modal--minimized {
            width: 200px !important;
            height: 30px !important;
            min-height: 30px !important;
            min-width: 0px !important;

            .in-grid-modal__content {

                .resizer {
                    display: none;
                }
            }
        }

        &:focus {
            outline: none;
        }

        .stretcher {
            display: none;
        }

        .resizer {
            z-index: 1000;
            position: absolute;
            right: 0;
            bottom: 0;
            width: 0;
            height: 0;
            cursor: nwse-resize;
            border-right: 7px solid $lighter;
            border-bottom: 7px solid $lighter;
            border-top: 7px solid transparent;
            border-left: 7px solid transparent;

            &:after {
                content: "";
                width: 6px;
                height: 6px;
                left: -3px;
                background: whitesmoke;
                position: absolute;
                top: -3px;
                border-radius: 5px;
            }

            &.is-dragging:before {
                content: "";
                @extend .stretcher-base;
            }
        }

        &.is-dragging {
            z-index: 1000;

            .stretcher {
                @extend .stretcher-base;
            }
        }

        .divider {
            padding: 20px 16px 6px;
            padding-bottom: 0;
            border-top: 1px solid $lighter;
            background: $lightest;
            box-shadow: inset 0 7px 7px -5px rgba($light, 0.85);

            &.pb20 {
                padding-bottom: 20px;
            }
        }

        .padsearch {
            padding: 10px 20px 20px;
            white-space: normal;

            input {
                width: 100%;
                //background-color: transparent;
            }
        }

        .padsearch__info {
            font-size: 11px;
            margin: 5px 0;
        }

        .list-expandable {

            li {
                cursor: default !important;

                &:hover {
                    background: transparent;
                }

                em {
                    font-style: normal;
                }

                .comment-content {
                    overflow: hidden;
                    display: block;
                    height: 0;
                    opacity: 0;
                    transition: .2s;
                    cursor: default;
                    padding: 0 20px 0;
                    word-wrap: break-word;
                    white-space: normal;
                }

                textarea {
                    width: 100%;
                    height: 110px;
                    border: 1px solid #d5dcdc;
                    border-radius: 2px;
                    margin-top: 10px;
                    padding: 10px;
                    background: rgba(255, 255, 255, 0.66);
                    box-shadow: inset 0 4px 8px -4px rgba(0, 0, 0, 0.34);
                }

                &.expanded {
                    background: lighten($lighter, 17%);

                    .comment-content {
                        opacity: 1;
                        padding-bottom: 10px;
                        height: auto;
                    }
                }
            }

            + p {
                padding: 0 20px 10px;
                opacity: .7;
            }
        }

        .list-multiple-icons {

            li {
                position: relative;

                > i {
                    color: $tertiary;
                    opacity: .5;
                    display: inline-block;
                    padding-right: 5px;
                }

                span {
                    transition: .2s;
                }

                .type-abbreviation {
                    margin: 0px 4px;
                    transform: translateY(-1px);
                }

                > div {
                    position: absolute;
                    top: 6px;
                    bottom: 6px;
                    right: 10px;
                    z-index: 2;
                }

                .abbreviation-wrapper {
                    line-height: 24px;
                    float: left;
                    margin-right: 5px;
                }
            }
        }

        .list-toolbar {
            padding: 5px 0 0;
            margin-bottom: 10px;
            border-bottom: none;

            &.with-pointer {
                cursor: pointer;
            }

            > li {
                padding: 7.5px 15px;
                transition: .2s;
                color: $tertiary;
                position: relative;
                margin-bottom: 0;

                &.active, &.active:hover {
                    background: $lightest;
                    color: $tertiary;
                    text-shadow: none;
                }

                &:hover {
                    background: lighten($lightest,3%);
                    color: $tertiary;
                    text-shadow: none;
                }

                &.disabled {
                    cursor: default;
                    pointer-events: none;
                    opacity: 0.5;

                    &:hover {
                        background: transparent;
                    }
                }


            }

            + .divider {
                padding-bottom: 14px;
            }
        }

        .mixed-controls {
            display: flex;
            border-top: 1px solid $lightest;
            border-bottom: 1px solid $lightest;
            padding: 0;

            > div {
                flex: 1 auto;
                padding: 6px 11px;
                border-right: 1px solid $lightest;

                &:last-child {
                    border-right: none;
                }

                label {
                    margin-bottom: 0;
                }
            }

            .plain-search {
                position: relative;
                flex-basis: 180px;

                input {
                    width: 100%;
                    height: 26px;
                    min-height: 26px;
                    padding-left: 5px;
                    padding-right: 75px;
                    position: relative;
                    background: transparent;
                    z-index: 2;
                    margin: 0;
                }

                .counter {
                    position: absolute;
                    top: 12px;
                    right: 14px;
                    font-size: 11px;
                    opacity: .6;

                    span {
                        margin-left: 5px;
                    }
                }
            }
        }
    }

    .add-rows-modal-content {

        label {
            padding: 5px 15px 0;
        }

        hr {
            margin-top: 0;
            margin-bottom: 0;
        }

        .list-toolbar > li {
            padding-top: 5px;
            padding-bottom: 5px;
            display: flex;
            justify-content: space-between;
            overflow: hidden;
        }
    }

    .add-rows-modal-content__metrics {
        max-height: 200px;
        overflow-y: auto;
        overflow-x: hidden;

        .list-component__header-button {
            padding: 0 0 30px 0;
            height: 24px;
            
            hr {
                visibility: hidden;
            }

            .list-component__header-button-visibility {
                right: 0px;
                box-shadow: none;
                bottom: 2px;
                width: 24px;
                height: 24px;
                font-size: 12px;

                +span {
                    bottom: 2px;
                }

                &:not([disabled]):hover {
                    transform: none;
                    box-shadow: none;
                }
            }
        }
    }

    .add-rows-modal-content__characteristics {
        max-height: 350px;
        overflow: auto;

        .multiselect .options {
            position: static;
        }
    }

    .add-rows-modal-content__metrics .button--list-action {
        display: flex;
        width: 100%;
        justify-content: space-between;
    }
    .add-rows-modal-content__title {
        padding: 2px 0;
    }

    .add-rows-modal-content__title--rows-count {
        border-top: 1px solid darken($lightest,14%);
        border-bottom: 1px solid darken($lightest,14%);
    }

    .add-rows-modal-content__title--characteristics {
        border-top: 1px solid darken($lightest,14%);
    }

    .add-rows-modal-content__title--metrics {
        border-top: 1px solid darken($lightest,14%);
    }

    .add-rows-modal-content__characteristics:not(.add-rows-modal-content__characteristics--visible) {
        display: none;
    }

    .add-rows-modal-content__metrics:not(.add-rows-modal-content__metrics--visible) {
        display: none;
    }

    .add-rows-modal-content__rows-count:not(.add-rows-modal-content__rows-count--visible) {
        display: none;
    }

    .characteristic-selector {
        width: 100%;
    }

    .add-rows-modal-content__rows-count {
        padding: 5px 10px;
    }

    .add-rows-modal-content__selector {
        display: flex;
        flex-basis: 100%;
    }

    .add-rows-modal-content__actions {
        display: flex;
        flex-basis: 24px;
        justify-content: center;
        align-items: flex-start;
        margin-top: 5px;
    }

    .button--add-rows {
        margin-top: 10px;
    }
    .charts-modal-content {
        @include group();
        padding: 10px 15px 5px 15px;

        .btn-group {
            margin-bottom: 15px;
        }


        label.charts-modal-axis-column {
            padding-left: 2px;
        }

        .charts-modal-axis-column {
            float: right;
            width: 52px;
        }


        .grid-alert {
            margin-left: -15px;
            margin-right: -15px;
            white-space: normal;
        }
    }

    .buttonpreset-modal-content {
        padding: 10px;

        ul.list-standard {
            margin: 0;

            li {
                padding: 0;

                &.more {
                    padding-left: 20px;
                }

                &:before {
                    right: auto;
                }

                &:after {
                    left: 10px;
                    top: 0;
                    line-height: 30px;
                    content: "\F105";
                }
            }

            button {
                display: block;
                padding: 5px 4px;
                text-align: left;
            }
        }
    }

    .propagation-modal-content {
        padding: 0 16px 16px 16px;
    }

    .notes-manager-modal-content {
        padding: 6px 16px;
        height: 100%;

        .filters-activity {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 16px;

            button {
                @include severity-filter-buttons;
                display: flex;
                margin-left: 8px;

                &:before {
                    margin-top: 3px;
                }

                &:focus {
                    color: $primary;
                }
            }
        }

        ul {
            overflow-y: auto;
            margin-bottom: 0;
            max-height: 100%;

            li {
                margin-bottom: 8px;

                &:before {
                    margin-top: 3px;
                    margin-right: 5px;
                }

                &.success:before {
                    @include success-icon;
                }

                &.info:before {
                    @include info-icon;
                }

                &.error:before {
                    @include error-icon;
                }

                &.warning:before {
                    @include warning-icon;
                }

                span,
                p {
                    white-space: normal;
                }

                p {
                    margin: 0 0 0 20px;
                }
            }
        }
    }

    .grid-layout-modal-content {
        @include group;
        background: $lightest;

        .tabs {
            margin-top: 0px;
            margin-bottom: 20px;

            .tab {

                &.is-tab-selected {
                    background: rgba($lightest, 0.96);
                }
            }
        }

        .content-wrapper {
            padding: 10px 10px 10px 10px;

            .nest-arrow {
                margin: 0px 13px 0px 10px;
                opacity: 0.5;
            }

            .list-standard {
                padding-top: 10px;

                &.layouts-list {
                    overflow-y: auto;
                    overflow-x: hidden;
                    max-height: 240px;
                }

                li {
                    padding: 8px 0px;
                    cursor: pointer;
                    border-bottom: 1px dashed rgba($nav-color, 0.40);
                    color: darken($nav-color,4%);
                    transition: all .1s ease;

                    i + span {
                        margin-left: 5px;
                        display: inline-block;
                    }

                    &.active {
                        padding: 8px 10px;
                        background: rgba($white, 0.80);
                        border-bottom: none;
                        color: lighten($nav-color,16%);

                        i {
                            color: $primary;
                        }
                    }

                    &.subpaging {
                        cursor: default;
                        background: rgba($white, 0.80);
                        //padding: 5px 20px 10px 34px;
                        padding: 5px 5px 10px 5px;
                        box-shadow: 0 16px 23px -8px rgba(0, 0, 0, 0.12), 0 8px 6px -4px rgba(0, 0, 0, 0.12);

                        .btn-group {

                            span {
                                padding-left: 6px;
                                padding-right: 12px;
                            }
                        }

                        .row--flex {
                            display: flex;

                            .button-wrapper {
                                display: flex;
                                align-items: flex-end;
                            }
                        }

                        .select {
                            min-width: 100%;

                            .select-list {

                                li {
                                    padding-left: 8px;

                                    &.hovered {
                                        padding-left: 10px;
                                    }
                                }
                            }
                        }
                    }

                    input {
                        min-height: 32px;
                    }

                    h5 {
                        font-size: 13px;
                        opacity: .9;
                        margin-top: 5px;
                        margin-bottom: 5px;
                    }
                }
            }
            .tooltip-wrapper {
                position: relative;
            }
        }
    }

    .grid-navigator-modal-content {
        background: darken($lightest,4%);

        .modal-wrapper {
            display: flex;
            flex-direction: row;
        }
    }

    .grid-navigator-map {
        padding: 10px 10px;
        width: 300px;
        @include group();

        .page-navigator__buttons {
            margin-top: 20px;
            position: relative;

            button {
                position: relative;
                z-index: 2;
            }
        }

        &.disabled {
            .page-navigator,
            button {
                cursor: no-drop;
            }
        }

        &.cant-zoom-in {
            .zoom-in {
                cursor: no-drop;
            }
        }

        &.cant-zoom-out {
            .zoom-out {
                cursor: no-drop;
            }
        }

        &.is-original-size {
            .to-original-size {
                cursor: no-drop;
            }
        }

        .page-navigator {
            border: 1px solid $labelBg;
            box-shadow: inset 0 0 10px rgba(darken($light, 0.5),50%);
            background: rgba($light, 0.15);
            width: 200px;
            cursor: move;
            position:relative;
        }

        .page-navigator__viewport-border {
            position: absolute;
        }

        .page-navigator__viewport-border.t {
            border-top: 1px solid $dark;
            height: 1px;
        }

        .page-navigator__viewport-border.l {
            width: 1px;
            border-left: 1px solid $dark;
        }

        .page-navigator__viewport-border.r {
            border-right: 1px solid $dark;
            width: 1px;
        }

        .page-navigator__viewport-border.b {
            height: 1px;
            border-bottom: 1px solid $dark;
        }

        .page-navigator__current_position {
            position: absolute;
            border: 2px solid $activeBg;
            background-color: #00adde;
        }
    }

    .grid-navigator-paging {
        padding: 10px 10px;
        width: auto;
        min-width: 300px;
        overflow-x: auto;

        .navigator-paging-wrapper {
            font-size: 12px;
            cursor: default;
            padding: 20px 0px;
            position: relative;
            overflow: hidden;

            .page-navigator-datarows {
                margin-left:152px;
                margin-right:20px;
                position:relative;
            }
        }

        .page-navigator-grid {
            width: 864px;
            height: 216px;
        }

        .page-navigator-labels {
            list-style: none;
            padding: 0;
            margin: 0;
            overflow: hidden;

            ul {
                transition: all .2s ease;
                position: relative;
                padding: 0;
                margin: 0;
            }

            &.x, &.y {

                li {
                    position: absolute;

                    &:after {
                        left: 50%;
                        text-align: center;
                        -webkit-transform: translate(-50%,-50%) scale(.6);
                        transform: translate(-50%,-50%) scale(.6);
                        transition: transform .1s .1s;
                        background: $light;
                        line-height: 25px;
                        z-index: 3;
                        padding: 6px 12px;
                        color: #333;
                        @include boxshadow-md();
                        font-size: 13px;
                    }

                    &:hover:after {
                        transition: .1s;
                        -webkit-transform: translate(-50%,-10%) scale(1);
                        transform: translate(-50%,-10%) scale(1);
                    }
                }
            }

            &.y {
                width: 150px;
                float: left;
                margin-right: 2px;
                background: $lightest;

                li {
                    //border-top:2px solid darken($light,4%);
                    //border-bottom:2px solid darken($light,4%);
                    background: transparent;
                    padding-top: 1px;
                    left: 0;

                    > span {
                        width: 60px;
                        vertical-align: middle;
                        line-height: 1;
                        text-align: left;

                        &:first-child {
                            text-align: right;
                        }

                        &.text-center {
                            text-align: center;
                        }
                    }
                }
            }

            &.x {
                margin-left: 152px;
                margin-bottom: 2px;
                background: $lightest;

                li {
                    top: 0;
                    width: 72px;
                    height: 100%;
                    //border-left: 2px solid darken($light,4%);
                    //border-right: 2px solid darken($light,4%);
                    &.empty {
                        min-height: 30px;
                    }
                }
            }

            li {
                display: block;
                width: 150px;
                height: 24px;
                padding: 4px;
                text-align: center;
                transition: width 0.1s linear;

                &:hover, &.active {
                    color: #333;
                    background: fade($labelBg, 20);
                }

                &.empty {
                    padding: 0;
                }

                span {
                    display: inline-block;
                    width: 100%;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }

        .viewport {
            overflow: hidden;
            position: relative;
            transform: translateZ(0);
        }

        .dataset {
            background: url(../images/page-light.png) left top repeat;
            transition: all .2s ease;
            position: absolute;
        }

        .current_page {
            position: absolute;
            background: $activeBg;
            pointer-events: none;
        }

        .hover_page {
            position: absolute;
            pointer-events: none;
            border: 2px solid $activeBg;
            transition: .1s;
        }

        .hover_tooltip {
            position: absolute;
            display: block;
            text-align: center;
            transform: translate(-50%, -100%);
            background: $tooltip_bg;
            color: $tooltip_color;
            white-space: nowrap;
            line-height: 1.2;
            z-index: 3;
            padding: 6px 12px;
            @include boxshadow-md();
            font-size: 13px;
            transition: .1s;
            pointer-events: none;
        }
    }

    .comments-modal-content {
        outline: none;

        .comments-list {
            overflow-x: hidden;
            overflow-y: auto;
            max-height: 500px;

            [data-tooltip]:after {
                top: 10px;
                bottom: auto;
            }
        }

        .insert-comment {
            padding: 0;
            overflow: hidden;
            height: 0;
            margin-bottom: 0;

            textarea {
                height: 140px;
                margin: 0;
                width: 100%;
            }

            &.expanded {
                height: 210px;
                padding: 20px 20px 0px 20px;
            }
        }
    }

    .procedures-history-modal-content {

        .procedure-history-info {
            font-size: 12px;
        }

        .button--repeat-procedure {
            opacity: 1;
            visibility: visible;
        }

        .no-history-label {
            margin-left: 5px;
            margin-top: 5px;
        }
    }

    .variable-configurator-modal-content {
        padding: 5px 0px 0px 0px;

        h4 {
            text-align: center;
        }

        .list-toolbar {

            min-height: 130px;

            li {
                overflow: hidden;
                padding-left: 25px;
                padding-bottom: 25px;
            }
        }

        .select-list {
            position: relative;
        }

        .slider {
            position: relative;
            margin-top: 5px;
            height: 25px;
        }

        .flex {
            display: flex;
            flex-direction: row;

            & div:first-child {
                flex-grow: 1;
            }

            .num-button {
                width: 32px;
                height: 32px;
                line-height: 32px;
                text-align: center;
                cursor: pointer;

                &.disabled {
                    cursor: default;
                    pointer-events: none;
                }
            }
        }

        .datepicker-advanced .cRow__day {
            width: 40px;
        }

        .datepicker-advanced {
            background: inherit !important;
            border: none !important;
            width: 100% !important;
            padding: 0 !important;
            text-align: left;

            .body,
            .header {
                width: auto;
            }

            .calendar {
                position: static;
                text-align: center;
            }
        }
    }

    .distribution-modal-content {
        .list-toolbar > li {
            padding-top: 10px;
            padding-bottom: 10px;
        }
    }

    .row-duplicates-modal-content {
        text-align: center;
        padding-bottom: 8px;

