﻿/// <reference path="../../grid-wrapper.scss" />

.#{$gridWrapper} {

    section {
        $panel-width: 380px;

        &.ts-grid--summaries-management {
            width: $panel-width;
        }

        &.ts-grid--summaries-management-details {
            @include group();
            background: darken($lightest,4%);
            right: $panel-width + 50px;
            width: $panel-width + 50px;
            overflow-x: hidden;
            overflow-y: auto;
            -webkit-transform: translate3d(40px, 0, 0);
            transform: translate3d(40px, 0, 0);

            .title {
                height: 42px;
                line-height: 42px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }

            .flexycontrol {
                padding-right: 40px;

                .actions {
                    position: absolute;
                    right: 11px;
                    top: 6px;
                }
            }

            .add-member {
                display: inline-block;
                cursor: pointer;
                color: $dark;
                transition: .2s;
            }

            &.active {
                -webkit-transform: translate3d(0,0,0);
                transform: translate3d(0,0,0);

                + .ts-grid--summaries-management {
                    width: $panel-width - 30;
                }
            }

            .rollup {

                margin-bottom: 10px;
                display: inline-block;
                
                .btn {
                    padding: 0px 10px;
                    
                    i {
                        margin-right: 0px;
                    }
                }
            }
        }
    }
}
