﻿.#{$gridWrapper} {
    
    .ts-grid--datepicker-specialdates {

        .quick-dates {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            align-items: center;

            .quick-date-button {
                flex-shrink: 1;
                flex-basis: 12%;
                height: auto;
                padding: 2px 5px 2px 2px;
                border-radius: 2px;
                color: $form_element__c;
                background: $white;
                margin: 8px 0px 10px 0;
                border: 1px solid $light;
                min-width: 70px;
                overflow: hidden;
                display: flex;
                justify-content: center;

                &:disabled {
                    opacity: .4;
                }

                span {
                    line-height: 1;
                    font-size: 10px;
                    max-width: 50px;

                    strong {
                        font-size: 12px;
                        max-width: 50px;
                        display: inline-block;
                        word-wrap: break-word;
                        white-space: normal;
                    }

                    &:first-child {
                        font-size: 16px;
                        margin-right: 5px;
                        margin-left: 0;
                        height: 22px;
                        line-height: 22px;
                    }
                }

                &:not(:disabled):hover {
                    box-shadow: inset 0 0 0 rgba($black, 0.0), 0 3px 8px rgba(0, 0, 0, 0.09), 0 1px 3px rgba(0, 0, 0, 0.13);
                }

                &:not(:disabled).active {
                    background-color: $lightest;
                    box-shadow: inset 0 2px 6px -1px rgba(0,0,0,.4), 0 3px 8px rgba(0, 0, 0, 0.0), 0 1px 3px rgba(0, 0, 0, 0.0);

                    &:hover {
                        box-shadow: inset 0 2px 6px -1px rgba(0,0,0,.4), 0 3px 8px rgba(0, 0, 0, 0.0), 0 1px 3px rgba(0, 0, 0, 0.0);
                    }
                }
            }
        }

        .filters {
            padding: 4px;
            border-radius: 4px;
            background: $light;
            box-shadow: inset 0 3px 5px -1px $light;
            float: right;

            button {
                padding: 4px 8px;
                border: none;
                background: transparent;
                transition: .2s;
                border-right: 1px solid rgba($light, 0.40);

                &:first-child {
                    border-radius: 2px 0 0 2px;
                }

                &:last-child {
                    border-radius: 0 2px 2px 0;
                    border-right: 1px solid transparent;
                }

                &.selected {
                    background: $light;
                    @include boxshadow-sm;
                    border-right: 1px solid transparent;
                }
            }
        }

        .shift-buttons {
            flex: 0 0 auto;
            width: 60%;
            border-radius: 3px;
            background: rgba($black, 0.5);
            padding: 5px;
            box-shadow: inset 0 3px 6px -2px rgba($black, 0.22);
            display: flex;
            position: relative;
            z-index: 2;
            margin: 10px auto;

            > div {
                flex: 1 1 auto;
                display: flex;

                &:first-of-type {
                    margin-right: 20px;
                }

                button {
                    flex: 1 1 auto;
                    height: 32px;
                    border-radius: 2px;
                    margin: 0 3px;
                    border: 1px solid rgba($white, 0.80);
                    transition: .2s;
                    color: $form_element__c;
                    background: $white;
                    @include boxshadow-md();

                    &.active {
                        box-shadow: none;
                        background: rgba($black, 0.5);
                    }

                    &:hover {
                        background: $white;
                    }
                }
            }
        }
    }
}
