.#{$gridWrapper} {

    .horizontal-scroll {
        display: flex;
        height: $toolbarHeight;
        position: relative;
        flex: 0.5;
        bottom: 0px;
        min-width: 300px;
        margin-left: 20px;
        align-items: center;

        &.pinable-container {
            visibility: hidden;

            &.is-pinned {
                visibility: visible;
            }
        }


        &.with-statusbar {
            bottom: $statusbarHeight;
        }

        .scroll-bar-wrapper {
            display:flex;
            height: 17px;
            background-color: $scroll_bar_color;
            border: 1px solid $scroll_bar_border;
            width: 100%;

            .arrow {
                max-width:17px;
                outline: none;
                border: 0;
                background-color: $scroll_bar_color;

                i {
                    position: relative;
                    top: -3px;
                }
            }

            .scroll-bar {
                position: relative;
                flex: 1;

                .scroll-handle {
                    background-color: $scroll_handle_color;
                    position: relative;
                    height: 100%;

                    &:hover {
                        background-color: darken($scroll_handle_color, 8%);
                    }

                    .stretcher {
                        display: none;
                    }

                    &.is-dragging {
                        background-color: darken($scroll_handle_color, 20%);

                        .stretcher {
                            @extend .stretcher-base;
                        }
                    }
                }
            }
        }


    }
}
