.#{$gridWrapper} {
    $bar-height: 10px;

    .drag-me {
        position: fixed;
        padding-top: $bar-height;

        .stretcher {
            display: none;
        }

        &.is-dragging {
            .stretcher {
                @extend .stretcher-base;
            }
        }
    }

    .drag-me-bar {
        position: absolute;
        top: 0;
        left: 0;
        cursor: move;
        background: $primary;
        width: 100%;
        height: $bar-height;
        display: flex;
        justify-content: center;
        align-items: center;

        &:before,
        &:after {
            position: absolute;
            content: "";
            width: 15px;
            height: 2px;
            background: darken($primary, 10%);
        }

        &:before {
            margin-top: -2px;
        }

        &:after {
            margin-top: 1px;
        }
    }
}