﻿/// <reference path="../../grid-wrapper.scss" />

.#{$gridWrapper} {

    .ts-grid--alert {
        position:fixed;
        z-index:9999999;
        top:0;
        left:0;
        bottom:0;
        right:0;
        overflow:hidden;
        background:rgba($darken, 0.75);
        transition:.2s;
        opacity:0;
        visibility:hidden;
    
        &.active {
            opacity:1;
            visibility:visible;
        }
    
        .button--close {
            position: absolute;
            right: 0;
            top: 0;
            cursor: pointer;
            color: $black;
            border: none;
            font-size: 30px;
            padding: 10px 15px 5px;
            background: transparent;
            line-height: 22px;
    
            &:hover {
                color: $primary;
            }
    
            &:before {
                content: "×";
            }
        }
    
        .ts-grid--alert-body {
            @extend .font-custom;
            position:absolute;
            max-width:640px;
            top:50%;
            left:50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            background: $white;
            @include boxshadow-md;
            padding: 40px 40px 120px;
    
            .buttons {
    
                button {
                    position:absolute;
                    bottom:0;
                    height:60px;
                    line-height:60px;
                    border:none;
                    font-weight:bold;
                    outline:none;
                    transition: .2s;
                    width:40%;
                    left:0;
                    background: $white;
                    border-top:1px solid $lightest;;
    
                    &:hover {
                        background: $lightest;;
                    }
    
                    &:only-child {
                        width:100%;
                    }
    
                    + button {
                        left:auto;
                        width:60%;
                        right:0;
                        background:$nav-color;
                        color:$white;
    
                        &:hover {
                            background:$primary;
                        }
                    }
                }
            }
    
            h4 {
                margin-bottom:30px;
                font-size:24px;
            }
    
            p {
                line-height:1.8;
            }
        }
    }
}