﻿// $colors: #ED5565, #A0D468, #48CFAD, #5D9CEC, #AC92EC, #EC87C0, #CCD1D9, #656D78, #DA4453, #E9573F, #F6BB42, #4FC1E9, #8CC152, #37BC9B, #3BAFDA, #4A89DC, #967ADC, #D770AD, #AAB2BD, #434A54, #FFCE54, #FC6E51;


@mixin set-multiple-properties($properties, $value, $j: length($properties)) {
    @if ($j > 0) {
        @include set-multiple-properties($properties, $value, $j - 1);
        $property: nth($properties, $j);
        #{$property}: $value;
    }
}

@mixin add-color-classes($properties, $colors, $i: length($colors)) {
    @if ($i > 0) {
        @include add-color-classes($properties, $colors, $i - 1);

        $color: lighten(nth($colors, $i), 30%);
        $index: $i - 1;
        &.c#{$index} {
            @include set-multiple-properties($properties, $color);
        }
    }
}

$lineStrokeColor: lighten($lighter, 17%);
$darkLineStrokeColor: $tertiary;

.#{$gridWrapper} {

    .chart-container {
        overflow: hidden;
        height: 100%;
        background: $white;
        display: flex;
        flex-direction: column;

        .legend {
            min-height: 48px;
            height: auto;
            padding: 5px 20px;
            text-align: right;
            width: 100%;
            overflow: hidden;
            background: $white;
            flex-shrink: 0;
        }

        .legend__item {
            padding: 0;
            line-height: 36px;
            margin-left: 10px;
            display: inline-block;

            span {
                cursor: pointer;
                display: inline-block;
                text-overflow:ellipsis;
                overflow:hidden;
                white-space:nowrap;
                vertical-align: middle;
            }

            .color {
                cursor: pointer;
                display: inline-block;
                margin-right:5px;
                $propertiesToColor: background, border-color;
                @include add-color-classes($propertiesToColor, $cm_colors);
                border-width:2px;
                border-style: solid;
                width:14px;
                height:14px;
                vertical-align: middle;
            }

            &.muted .color {
                background: transparent;
            }
        }
        
        .legend__list-wrapper {
            float: left;
        }

        .svg {
            cursor: default;
            width: 100%;
            flex: 1 1 auto;
            transition: width .2s ease;
            user-select: none;
            overflow: hidden;

            svg {
                display:block;
            }

            .primary-values-group,
            .secondary-values-group {
                text {
                    text-anchor: end;
                    font-size:12px;
                }

                path.ts-chart--zero-level {
                    stroke: $darkLineStrokeColor;
                }

                g {
                    path {
                        stroke:$lineStrokeColor;

                        &.ts-chart--zero-level {
                            stroke: $darkLineStrokeColor;
                        }
                    }
                }
            }

            .labels-group text {
                text-anchor: middle;
                font-size:12px;
            }

            .data-group {

                .handle {
                    stroke-width:2;
                    stroke-linecap: round;
                    cursor: n-resize;

                    &.transparent {
                        fill: transparent;
                    }

                    &.locked {
                        cursor: no-drop;
                    }
                }

                circle {
                    fill: #fff;
                    $propertiesToColor: stroke;
                    @include add-color-classes($propertiesToColor, $cm_colors);
                }

                polyline, line {
                    fill:none;
                    stroke-width:8;
                    stroke-linecap: round;
                    opacity:1;
                    $propertiesToColor: stroke;
                    @include add-color-classes($propertiesToColor, $cm_colors);
                }

                rect {
                    shape-rendering: crispEdges;
                    $propertiesToColor: fill, stroke;
                    @include add-color-classes($propertiesToColor, $cm_colors);
                }

                > g {
                    transition:opacity .2s ease;

                    &.active {
                        polyline, line {
                            opacity:1;
                        }
                    }

                }

                .muted {
                    visibility:hidden;
                    opacity:0;
                }

                .locked .handle, polyline, line {
                    transition:none;
                }
            }

            .primary-values-group,
            .secondary-values-group,
            .labels-group {

                line, path {
                    shape-rendering: crispEdges;
                    stroke: $lineStrokeColor;
                    stroke-width: 1;
                    stroke-linecap: round;
                    stroke-miterlimit: 0;
                    //-webkit-animation: dash 4.3s cubic-bezier(0.65, 0.02, 0.29, 0.95) forwards;
                    //animation: dash 4.3s cubic-bezier(0.65, 0.02, 0.29, 0.95) forwards;
                }

                text {
                    fill: $lighter;

                    &:nth-child(2n) {
                        fill:$tertiary;
                    }
                }
            }
        }
    }
}
